<template>
  <div id="wrapper" v-cloak @drop.prevent="addFile" @dragover.prevent>

  <div class="card" style="width: 96%">
    <v-card class="mx-auto">
      <div class="filemenu" style="width: 100%; text-align: right; background-color: #3577cb;">
          <span>
            <v-btn style="margin-left: 0px; margin-right:auto;" icon @click="goBack">
              <v-icon style="color: white;">mdi-arrow-left</v-icon>
            </v-btn>
          </span>
          <span>
            <v-progress-circular v-if="loading" :key="loading" style="width: 20px; height: 20px; margin-right: 15px;" indeterminate color="white"></v-progress-circular>
            <v-btn style="margin-left: auto; margin-right:10px;" icon @click="getPDF(pdfURI, 1)">
              <v-icon style="color: white;">mdi-reload</v-icon>
            </v-btn>
          </span>
      </div>

        <div id="pdfvuer" v-if="pdfloaded" style="width: 100%">
          <!---<div id="buttons" class="ui grey three item inverted bottom fixed menu transition hidden">
            <a class="item" @click="pdfpage > 1 ? pdfpage-- : 1">
              <i class="left chevron icon"></i>
              Back
            </a>
            <a class="ui active item">
              {{pdfpage}} / {{ numPages ? numPages : '' }}
            </a>
            <a class="item" @click="pdfpage < numPages ? pdfpage++ : 1">
              Forward
              <i class="right chevron icon"></i>
            </a>
          </div>
          <div id="buttons" class="ui grey three item inverted bottom fixed menu transition hidden">
            <a class="item" @click="scale -= scale > 0.2 ? 0.1 : 0">
              <i class="left chevron icon" />
                Zoom -
            </a>
            <a class="ui active item">
              {{ formattedZoom }} %
            </a>
            <a class="item" @click="scale += scale < 2 ? 0.1 : 0">
              Zoom +
              <i class="right chevron icon" />
            </a>
          </div>-->
          <pdf :src="pdfdata" v-for="i in 10" :key="i" :id="startPage+i" :page="startPage+i"
            :scale.sync="scale" style="width:100%;margin:20px auto;"
              :annotation="true"
              :resize="true"
              @link-clicked="handle_pdf_link">
            <template slot="loading">
              Loading page...
            </template>
          </pdf>
        </div>
    </v-card>
  </div>
  <div class="bottommenu">
    <v-btn v-if="pdfpage<totalNumPages" style="margin-left: 5px; margin-right:5px;" icon @click="nextPage">
      <v-icon style="color: white;">mdi-arrow-down</v-icon>
    </v-btn>
    <span v-else style="width: 50px; height: 25px"> </span>
    <font color="#FFFFF">Page {{ pdfpage }} of {{ totalNumPages }}</font>
    <v-btn style="margin-left: 5px; margin-right:5px;" icon @click="previousPage">
      <v-icon style="color: white;">mdi-arrow-up</v-icon>
    </v-btn>
  </div>


  </div>
</template>


<script>

const N3 = require('n3');
const n3store = new N3.Store();
const $rdf = require('rdflib');
import store from '../store';
import router from '../router';
import pdf from 'pdfvuer'
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import ur from './util-rdf.js';
import moment from 'moment';
import { getIcon } from 'material-file-icons';

import {
  createSolidDataset,
  createThing,
  setThing,
  getThing,
  addUrl,
  addStringNoLocale,
  saveSolidDatasetAt,
  getSolidDataset,
  getThingAll,
  getStringNoLocale,
  getStringNoLocaleAll,
  createContainerAt,
  getPodOwner
} from "@inrupt/solid-client";

var factory = new (require('rdf-data-factory').DataFactory)();
var RdfTerm = require('rdf-string');
var toRdfLiteral = require("rdf-literal").toRdf;
let searchRdfStore = $rdf.graph();

const pw = {}

export default {
  id: pw,
  name: 'PDFviewer',
  data() {
    return {
      valid: true,
      pdfURI: '',
      pdfdata: undefined,
      pdfloaded: false,
      pdfpage: 1,
      numPages: 10,
      totalNumPages: 10,
      pagePositions: [],
      scrolling: false,
      scale: 'page-width',
      debug: false,
      loading: false,
      startPage: 0,
    }
  },
  methods: {
      scrollMeTo(refName) {
        var element = this.$refs[refName];
        var top = element.offsetTop;
        window.scrollTo(0, top);
      },
      goBack() {
        history.back();
      },
      checklogin(){
        console.log(window.solid.session.info);
        if (window.solid.session.info.isLoggedIn){
          console.log("Test logged in");
        } else {
          console.log("Is logged out, reroute");
          this.$router.push({ path: '/' })
        }
      },
      async getPDF(pdfURI, page){
        this.loading = true;
        this.pdfloaded = false;
        this.pdfdata = undefined;
        this.pagePositions = [];
        try {
          this.pdfpage = this.startPage+parseInt(page);
        } catch (error) {
          this.pdfpage = this.startPage+1;
        } 
        try {
          const res = await window.solid.session.fetch(
            pdfURI, {
              method: "GET",
              credentials: "include",
              });
              console.log(res);
            let filedata = await res.arrayBuffer();
            let fileurl = URL.createObjectURL(new Blob([filedata], { type: "application/pdf"}));
            this.pdfdata = pdf.createLoadingTask(fileurl);
            this.pdfdata.then(pdf => {
            console.log(fileurl);
            setTimeout( () => {
              let previousPos = 0;
              for (var i=1; i<100; i++) {
                const foundPos = this.findPos(document.getElementById(this.startPage+i));
                if(previousPos==foundPos) break;
                this.pagePositions.push(foundPos);
                this.numPages = this.startPage+i;
                previousPos = foundPos;
              }
              this.numPages--;
            }, 1000 );
            this.pdfloaded = true;
            this.loading = false;
            this.totalNumPages = pdf.numPages;
            });
        } catch (error) {
          console.log(error);
        }
      },
      handle_pdf_link: function (params) {
        // Scroll to the appropriate place on our page - the Y component of
        // params.destArray * (div height / ???), from the bottom of the page div
        var page = document.getElementById(String(params.pageNumber));
        page.scrollIntoView();
      },
      findPos(obj) {
        return obj.offsetTop;
      },
      arrayRemove(arr, value) {
        return arr.filter(function(ele){
            return ele != value;
        });
      },
      between(x, min, max) {
        return x >= min && x <= max;
      },
      nextPage() {
        if(this.pdfpage<this.numPages){
          this.pdfpage++;
        } else {
          if(String(this.pdfpage).charAt(String(this.pdfpage).length - 1)==0){
            this.startPage = this.startPage+10;
            this.getPDF(this.pdfURI, 1);
          }
        }
        document.getElementById(this.pdfpage).scrollIntoView();
        window.scrollBy(0,-120);
      },
      previousPage() {
        if(this.pdfpage > this.startPage+1){
          this.pdfpage--;
        } else {
          if(this.pdfpage>1){
            this.startPage = this.startPage-10;
            this.getPDF(this.pdfURI, 1);
          }
        }
        document.getElementById(this.pdfpage).scrollIntoView();
        window.scrollBy(0,-120);
      }
  },
  computed: {
    formattedZoom () {
      return Number.parseInt(this.scale * 100);
    },
  },
  watch: {
    /*pdfpage: function (p) {
      console.log(this.pdfpage);
      document.getElementById(p).scrollIntoView();
      window.scrollBy(0,-120);
      //if( window.pageYOffset <= this.findPos(document.getElementById(p)) || ( document.getElementById(p+1) && window.pageYOffset >= this.findPos(document.getElementById(p+1)) )) {
        // window.scrollTo(0,this.findPos(document.getElementById(p)));
      //}
    }*/
  },
  mounted(){
      this.checklogin();
      this.pdfURI = this.$route.query.select;
      this.getPDF(this.pdfURI, 1);
  },
  created(){
    let isScrolling;
    window.addEventListener('scroll', () => {
      this.scrolling = true
      window.clearTimeout(isScrolling);
      isScrolling = setTimeout(() => {
        this.scrolling = false
        console.log(window.scrollY)
        for (var i=0; i<this.numPages+1; i++) {
          if(this.between(window.scrollY, this.pagePositions[i]-120, this.pagePositions[i+1]-120)){
            this.pdfpage = this.startPage+i+1;
          }
          if(window.scrollY<50) this.pdfpage = this.startPage+1;
          if(window.scrollY>this.pagePositions[this.pagePositions.length-1]) this.pdfpage = this.numPages;
        }
      }, 400);
    }, false);
  },
  destroyed () {

  },
  components: {
    pdf
  }
};
</script>

