const a = {};

// Class for array functions

a.getLastElement = function(array) {
    if (Array.isArray(array)) {
      return array.slice(-1)[0];
    }
};

a.getSecondLastElement = function(array) {
  if (Array.isArray(array)) {
    return array.slice(-2, -1)[0];
  }
};

a.removeFirstElement = function(array) {
  if (Array.isArray(array)) {
    array.shift();
    return array;
  }
};

a.removeLastElement = function(array) {
    if (Array.isArray(array)) {
      return array.slice(0, array.length-1);
    }
};

a.checkIfDuplicateExists = function(array) {
    if (Array.isArray(array)) {
      return new Set(array).size !== array.length;
    }
};

a.removeDuplicates = (array = []) => {
    const map = new Map();
    array.forEach((x) => map.set(JSON.stringify(x), x));
    array = [...map.values()];
    return array;
};

a.reverseSubArrays = function(array){
    for(var sa =0 ; sa < array.length; sa++) {
        array[sa].reverse();
    }
    return array;
};

a.removeValueFromArray = function(array, value){
  for( var i = 0; i < array.length; i++){ 
      if ( array[i] == value) { 
        array.splice(i, 1); 
      }
  }
  return array;
}

export default a;
