<template>

  <div id="wrapper" class="wrapper" v-cloak @drop.prevent="addFile" @dragover.prevent>

  <div class="left" align="left">
  <v-card
    class="wrapper"
    width="100%"
  >
    <div class="filemenu" style="width: 100%; text-align: right; background-color: #3577cb;">
    <span>
      <v-btn style="margin-left: 0px; margin-right:auto;" icon @click="goBack">
        <v-icon style="color: white;">mdi-arrow-left</v-icon>
      </v-btn>
    </span>
    <span>
      <v-progress-circular v-if="loading" :key="loading" style="width: 20px; height: 20px; margin-right: 15px;" indeterminate color="white"></v-progress-circular>
      <v-btn style="margin-left: auto; margin-right:10px;" icon @click="reloadData(true)">
        <v-icon style="color: white;">mdi-reload</v-icon>
