        <img class="icon" v-if="item.podtype=='Building'" src='../assets/building2.png'/>
        <img class="icon" v-else-if="item.podtype=='Site'" src='../assets/position2.png'/>
        <img class="icon" v-else-if="item.podtype=='Personal'" src='../assets/person2.png'/>
        <img class="icon" v-else-if="item.podtype=='Organization'" src='../assets/organization3.png'/>
        
        <v-list-item-content>
          <v-list-item-title v-text="item.title"></v-list-item-title>
          <v-list-item-subtitle v-text="item.subtitle"></v-list-item-subtitle>
          <!--<v-list-item-subtitle v-text="item.parentpod+' '+item.podlevel"></v-list-item-subtitle>-->
        </v-list-item-content>

        <v-list-item-action>
          <v-btn icon>
            <v-icon color="grey lighten-1">mdi-information</v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>

    </v-list>
