        <v-list-item-subtitle v-text="selectedPod.subtitle"></v-list-item-subtitle>
        <br/>
        {{ selectedPod.podtype+" POD" }}
      </v-list-item-content>

        <img class="iconbig" v-if="selectedPod.podtype=='Building'" src='../assets/building2.png'/>
        <img class="iconbig" v-else-if="selectedPod.podtype=='Site'" src='../assets/position2.png'/>
        <img class="iconbig" v-else-if="selectedPod.podtype=='Personal'" src='../assets/person2.png'/>
        <img class="iconbig" v-else-if="selectedPod.podtype=='Organization'" src='../assets/organization3.png'/>
    </v-list-item>

    <div v-if="organizationpodSelected">
      <v-divider dark style="background-color: lightgray"></v-divider>
      <v-subheader>{{ $t("text_employees") }}</v-subheader>
      <v-list-item
        v-for="employee in employeeList"
        :key="employee.subtitle"
        @click="podSelected(employee.subtitle)"
        :class="{'container': employee.subtitle.substring(0, employee.subtitle.length - 1)==currentPod}"
      >
        <v-list-item-avatar :color="employee.color || '#FF0000'" size="48">
          <span class="white--text text-h5">{{ employee.initials }}</span>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title v-text="employee.title"></v-list-item-title>
          <v-list-item-subtitle v-text="employee.subtitle"></v-list-item-subtitle>
        </v-list-item-content>

        <v-list-item-action>
          <v-btn icon>
            <v-icon color="grey lighten-1">mdi-information</v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
      <br>
      <v-divider dark style="background-color: lightgray"></v-divider>
      <v-subheader>{{ $t("text_employee_new") }}</v-subheader>
      <div class="inputwrapper">
        <v-form
            ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              v-model="newEmployeeURI"
              label="Employee URI"
              required
            ></v-text-field>

            <v-btn
              :disabled="!valid"
              color="success"
              class="mr-4"
              @click="addEmployee"
            >
              {{ $t("text_add") }}
            </v-btn>
          </v-form>
        </div>
        <br>
      </div>
      </div>
  <div>
  </div>
  </v-card>
  </div>


  <v-row justify="center">
    <v-dialog
      v-model="whatToDoVisible"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
    <WhatToDo :dialog.sync="whatToDoVisible" />
    </v-dialog>
    </v-row>
