</template>


<script>

const N3 = require('n3');
const n3store = new N3.Store();
const $rdf = require('rdflib');
import store from '../store';
import router from '../router';
import WhatToDo from './WhatToDo.vue';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import ur from './util-rdf.js';
import so from './util-s.js';
import c from './util-c.js';
import ifcu from './BIMviewer-ifc-utils.js';
import l from './TrinityLogin.vue';
import { getIcon } from 'material-file-icons';
import {
  createSolidDataset,
  createThing,
  setThing,
  getThing,
  addUrl,
  addStringNoLocale,
  saveSolidDatasetAt,
  getSolidDataset,
  getThingAll,
  getStringNoLocale,
  getStringNoLocaleAll,
  createContainerAt,
  getPodOwner
} from "@inrupt/solid-client";
import Vue from 'vue';



const ps = {}


function addToStores(subject, predicate, object){
  store.state.rs.rdfStore.add(subject, predicate, object, $rdf.default);
}

export default {
  id: ps,
  name: 'PodSelect',
  components: {
      WhatToDo
  },
  data() {
    return { 
      valid: true,
      currentPOD: '',
      currentPod: this.$route.query.location,
      selectedPod: { filename: 'pod', title: "", subtitle: "", parentpod: "", podlevel: 0 },
      files:[],
      fileDetails: {},
      createContainerName: "",
      loading: false,
      employeeList: [],
      podlist: [],
      newEmployeeURI: "",
      organizationpodSelected: false,
      whatToDoVisible: false,
    }
  },
  methods: {
      checklogin(){
        console.log(window.solid.session.info);
        if (window.solid.session.info.isLoggedIn){
          console.log("Test logged in");
        } else {
          console.log("Is logged out, reroute");
          this.$router.push({ path: '/' })
        }
      },
      goBack() {
        history.back();
      },
      podSelected(uri){
        this.employeeList = [];
        uri = uri.substr(uri.length - 2) === "/i" ? uri.substring(0, uri.length - 1) : uri; // Removes i from the location
        this.currentPod = uri;
        if(this.$route.query.location !== uri){
          this.$router.push({path: this.$route.path, query: {location: uri, select: this.$route.query.select} });
        }
        // Set pod list organization.
        //this.podList.
        console.log("Selected Pod: ", uri);
        this.podlist.filter(element => element.subtitle == uri+"i").forEach((pod, podindex) => {
          this.selectedPod = pod;
          if(pod.podtype.includes("Organization")){
            this.organizationpodSelected = true;
          } else {
            this.organizationpodSelected = false;
          }
        });
        this.getEmployees(uri+'i');
      },
      reloadData(force){
        console.log("Trying to reload: "+store.state.webId);
        this.getPodsFromURI(store.state.webId, force);
      },
      getTripleMatches(tripleArray, tripleGraph){
        let tripleResult = [];
        tripleArray.forEach((item, i) => {
          if(i==0){
            //console.log("S: "+tripleArray[i].subject+" P: "+tripleArray[i].predicate+" O: "+tripleArray[i].object);
            tripleGraph.match(tripleArray[i].subject, tripleArray[i].predicate, tripleArray[i].object).forEach(st => {
              tripleResult.push(st);
            });
          } else {
            tripleGraph.match(tripleArray[i].subject, tripleArray[i].predicate, tripleArray[i].object).forEach(st => {
              if(tripleResult.includes(st)){
                tripleResult = this.arrayRemove(tripleResult, st);
                tripleResult.push(st);
              } else {
                tripleResult.push(st);
              }
            });
          }
        });
        return tripleResult;
      },
      arrayRemove(arr, value) {
        return arr.filter(function(ele){
            return ele != value;
        });
      },
      async getPodsFromURI(uri, force){
        this.podlist = [];
        this.loading = true;
        let containedPodURIs = [];
        let pods = await ur.fetchAndSaveTurtle(uri, force);
        console.log(pods)

          
        containedPodURIs.push(uri);
        let didFetchNewURI = true;
        while(didFetchNewURI){ // Loop until all pod cards have been checked.
          didFetchNewURI = false;
          let tripleArray = store.state.rs.rdfStore.match(null, ns.FOAF("maker"), null);
          let tripleArray2 = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.ORG("Organization")); // Get all organization trinpods
          let tripleArray3 = store.state.rs.rdfStore.match(null, ns.ACL("owner"), null); // Get all site trinpods
          let tripleArray4 = store.state.rs.rdfStore.match(null, ns.ORG("hasMember"), null); // Get all site trinpods
          let tripleArray5 = store.state.rs.rdfStore.match(null, ns.DCTERMS("contributor"), null);
          tripleArray2.forEach(tp => { tripleArray.push(tp) });
          tripleArray3.forEach(tp => { tripleArray.push(tp) });
          tripleArray4.forEach(tp => { tripleArray.push(tp) });
          tripleArray5.forEach(tp => { tripleArray.push(tp) });
          for(let i=0; i<tripleArray.length; i++){
            const subject = tripleArray[i].subject.value;
            const object = tripleArray[i].object.value;
            if(subject.slice(-2).includes("/i")){
              if(!containedPodURIs.includes(subject.substring(0, subject.length - 2)) && !containedPodURIs.includes(subject)) { // Check if it contains the exact same or without /i in the end.
                containedPodURIs.push(subject);
                let pod = await ur.fetchAndSaveTurtle(subject, force);
                if(pod) didFetchNewURI = true;
              }
            }
            if(object.slice(-2).includes("/i")){
              if(!containedPodURIs.includes(object.substring(0, object.length - 2)) && !containedPodURIs.includes(object)) { // Check if it contains the exact same or without /i in the end.
                containedPodURIs.push(object);
                let pod = await ur.fetchAndSaveTurtle(object, force);
                if(pod) didFetchNewURI = true;
              }
            }
          }
        }
        console.log(containedPodURIs); // Problem is that not all pods are returned in this array. Must fetch

        for(let cPod=0; cPod<containedPodURIs.length; cPod++){
          /*let pods = await ur.fetchAndSaveTurtle(containedPodURIs[cPod], force); 
          console.log("Pods");
          console.log(pods);*/
          this.podlist.filter(element => element.subtitle == containedPodURIs[cPod]).forEach((listitem) => {
            try{
              listitem.title = store.state.rs.rdfStore.match($rdf.sym(listitem.subtitle), ns.FOAF("name"), null)[0].object || "";
            } catch (error){
              console.log(error);
            }
            let obj = ur.parseLinkHeader(listitem.subtitle);
            if(obj.type){
              if(!obj.type.includes("http://xmlns.com/foaf/0.1/PersonalProfileDocument")){
                this.podlist.splice(listitem, 1);
              }
            }
          });
          containedPodURIs.forEach((puri, ind) => {
            if(puri=='') containedPodURIs.splice(ind, 1);
          });
          if(cPod == containedPodURIs.length-1) {
            console.log("Listing pods..")
            this.setPodNames(containedPodURIs, uri);
          }
        }
      },
      setPodNames(containedPodURIs, rootPod){
          let podListHierarchy = [];
          // Add to pods list
          containedPodURIs.forEach((poditem, i) => {
            poditem = poditem.substr(poditem.length - 2) === "/i" ? poditem : poditem+"/i";
            this.podlist.push({ filename: 'pod', title: "POD", subtitle: poditem, parentpod: "", podlevel: 0 });
          });

          // Set pod hierachy
          let plevel = 0;
          podListHierarchy.push({ filename: 'pod', title: "POD", subtitle: rootPod, parentpod: "", podlevel: plevel, podtype: "Personal" });
          // Must be looped until no more sub pods are found.
          for(let pl = 0; pl <= 20; pl++){ // Looping max 20 levels of pods for infinite security
            console.log("Looping round: "+pl);
            let found = false;
            podListHierarchy.filter(element => element.podlevel == pl).forEach((pod, podindex) => {
              const topPodLevel = [];
              store.state.rs.rdfStore.match(null, ns.FOAF("maker"), $rdf.sym(pod.subtitle)).forEach(item => {
                if(!topPodLevel.includes(item)) topPodLevel.push(item);
              });
              store.state.rs.rdfStore.match(null, ns.ORG("hasMember"), $rdf.sym(pod.subtitle)).forEach(item => {
                if(!topPodLevel.includes(item)) topPodLevel.push(item);
              });
              topPodLevel.forEach(somesubitem => {
                if(containedPodURIs.includes(somesubitem.subject.value) && somesubitem.subject.value != pod.subtitle){ // Check if in list of pods and not same as current pod.
                  console.log("Found child pod: "+somesubitem.subject.value);
                  let podtype = "";
                  let exists = podListHierarchy.some( element => element['subtitle'] === somesubitem.subject.value );
                  if(store.state.rs.rdfStore.match($rdf.sym(somesubitem.subject.value), ns.ACL("owner"), null).length>=1) exists = true; // Do not add if it has an owner. Filters only first level of organization tree
                  if(store.state.rs.rdfStore.match($rdf.sym(somesubitem.subject.value), ns.RDF("type"), ns.ORG("Organization")).length>=1) podtype = "Organization";
                  if(!exists) podListHierarchy.splice(podindex+1, 0, { filename: 'pod', title: "POD", subtitle: somesubitem.subject.value, podtype: podtype, parentpod: pod.subtitle, podlevel: pl+1 });
                  if(!exists) found = true;
                }
              });
              store.state.rs.rdfStore.match($rdf.sym(pod.subtitle), ns.ORG("hasSubOrganization"), null).forEach(suborg => {
                if(containedPodURIs.includes(suborg.object.value) && suborg.object.value != pod.subtitle){ // Check if in list of pods and not same as current pod.
                    let exists = podListHierarchy.some( element => element['subtitle'] === suborg.object.value );
                    if(!exists) podListHierarchy.push({ filename: 'pod', title: "POD", subtitle: suborg.object.value, podtype: "Organization", parentpod: pod.subtitle, podlevel: pl+1 });
                    if(!exists) found = true;
                }
              });
              store.state.rs.rdfStore.match(null, ns.FRBR("owner"), $rdf.sym(pod.subtitle)).forEach(site => {
                if(containedPodURIs.includes(site.subject.value) && site.subject.value != pod.subtitle){ // Check if in list of pods and not same as current pod.
                  let exists = podListHierarchy.some( element => element['subtitle'] === site.subject.value );
                  if(!exists) {
                    let podtype = "Site";
                    let podlevel = pl+1;
                    if(store.state.rs.rdfStore.match($rdf.sym(site.subject.value), ns.RDF("type"), ns.BOT("Site")).length==0 || store.state.rs.rdfStore.match($rdf.sym(site.subject.value), ns.RDF("type"), ns.BOT("Building")).length>=1){
                      podtype = "Building";
                      podlevel = pl+2;
                    } 
                    podListHierarchy.push({ filename: 'pod', title: "POD", subtitle: site.subject.value, podtype: podtype, parentpod: pod.subtitle, podlevel: podlevel });
                  } else {
                    podListHierarchy.filter( element => element['subtitle'] == site.subject.value ).forEach(site1 => {
                      site1.parentpod = pod.subtitle;
                      site1.podlevel = pl+1;
                      if(store.state.rs.rdfStore.match($rdf.sym(site.subject.value), ns.RDF("type"), ns.BOT("Site")).length==0) site1.podtype = "Site";
                      if(store.state.rs.rdfStore.match($rdf.sym(site.subject.value), ns.RDF("type"), ns.BOT("Building")).length>=1){
                        site1.podtype = "Building";
                        if(store.state.rs.rdfStore.match(null, ns.SIO("SIO_000202"), $rdf.sym(site.subject.value))[0]) site1.parentpod = store.state.rs.rdfStore.match(null, ns.SIO("SIO_000202"), $rdf.sym(site.subject.value))[0].subject.value;
                        site1.podlevel = pl+2;
                      }
                    });
                  }
                  if(!exists) found = true;
                }
              });
            });
            if(!found) this.loading = false;
            if(!found) break; // Exit loop if no more pods are found
          }

          for(let i=0; i<2; i++){ // This loop is needed to make order of pods correctly, may need to be increased
            let prevPod = 0;
            podListHierarchy.forEach((poditem, index) => {
              if(poditem.podlevel<prevPod) podListHierarchy = so.arraymove(podListHierarchy, index, index-1);
              prevPod = poditem.podlevel;
            });
          }
          //podListHierarchy.sort((p1, p2) => (p1.podlevel < p2.podlevel) ? 1 : (p1.podlevel > p2.podlevel) ? -1 : 0);
          for(let i=0; i<2; i++){
            for(let pi=0; pi<podListHierarchy.length; pi++){
              let parent = podListHierarchy[pi].parentpod;
              for(let p=0; p<podListHierarchy.length; p++){
                // Get parent position and move after
                if(podListHierarchy[p].subtitle==parent) so.arraymove(podListHierarchy, pi, p+1);
              }
            }
          }

          


          console.log(podListHierarchy);

          this.podlist = podListHierarchy;

          // Get pod names
          this.podlist.forEach((listitem, i) => {
            console.log(listitem.podlevel);
            if(listitem.podlevel==1) this.checkPodAuthorization(listitem.subtitle);
            try{
             listitem.title = store.state.rs.rdfStore.match($rdf.sym(listitem.subtitle), ns.FOAF("name"), null)[0].object || "";
            } catch (error){
              console.log(error);
            }
          });
          /*this.podlist.forEach((podlistitem, i) => {
            
          });*/
      },
      checkPodAuthorization(podURI){
        const done = ur.fetchAndSaveTurtle(podURI.replace("/i", "/home/"));
      },
      getEmployees(uri){
        if(uri=='') return;
        let pods = ur.fetchAndSaveTurtle(uri, true).then((successBool) => {
          console.log("Get employees from: "+uri);
          this.employeeList = [];
          let employeeURIs = [];
          //let containerURIs = [];

          // Get all URIs
          let tripleArray = store.state.rs.rdfStore.match($rdf.sym(uri), ns.ORG("hasMember"), null);
          tripleArray.forEach(item => {
              if(!employeeURIs.includes(item.object.value)) { // Check if it contains the exact same or without /i in the end.
                employeeURIs.push(item.object.value);
              }
          });

          employeeURIs.forEach((poditem, i) => {
            this.employeeList.push({ filename: 'pod', personuri: poditem, title: "Personal", initials: "", subtitle: poditem, color: so.makeRandomColor() });
          });

          // Get pod names
          this.employeeList.forEach((listitem, i) => {
            let pods = ur.fetchAndSaveTurtle(listitem.personuri, true).then((successBool) => {
              try{
              let personName = store.state.rs.rdfStore.match($rdf.sym(listitem.subtitle), ns.FOAF("name"), null)[0].object.value || "";
              listitem.title = personName;
              listitem.initials = so.getInitials(personName);
              } catch (error){
                console.log(error);
              }
            });
            return successBool;
          });
        });
      },
      addEmployee(){
        console.log(this.newEmployeeURI);
        // Do checks on URI string

        // Request the URI to check validity
        let employeeCheck = ur.fetchAndSaveTurtle(this.newEmployeeURI, true).then(empl => {
          console.log(empl);
        });
      },
      showWhatToDo(){
        
      }
  },
  computed: {
      svgFile() {
        return (filename) => getIcon(filename).svg;
      },
  },
  watch: {
    "$store.state.whatToDoVisible": function (val) {
      this.whatToDoVisible = store.state.whatToDoVisible;
    },
  },
  mounted(){
      this.checklogin();
      if(!this.$route.query.location){
        this.$router.push({path: this.$route.path, query: {location: this.currentPOD} });
      }
      this.getPodsFromURI(store.state.webId, false);
      if(this.currentPOD) this.getEmployees(this.currentPOD+"i");
      const form = document.getElementById('form');
      
      if(this.$route.query.location.length>=5){
        ifcu.getIFCmetaDataFromJson(this.$route.query.location+"home/IFC/building.json").then(IFCmetadata => {
          store.state.currentBuildingTag = IFCmetadata.tag;
        }); 
      }

      /*form.addEventListener('submit', function(e) {
          // Prevent default behavior:
          console.log(e);
      })*/
  },
  created(){
    if(!this.$route.query.location) store.state.whatToDoVisible = true;
  }
};
</script>

