const factory = new (require('rdf-data-factory').DataFactory)();
const RdfTerm = require('rdf-string');
const toRdfLiteral = require("rdf-literal").toRdf;

const wl = {}

const components = {
  //...
}

wl.searchAndParseResult = async function(conceptName){
  let uri = store.state.webId.replace("/i", "/search/")+encodeURIComponent(conceptName);
  try {
    let res = await window.solid.session.fetch(
      uri, {
        method: "GET",
        credentials: "include",
        headers: {
          "Accept": "text/turtle"
        },
    }).then(res => res.text());
    $rdf.parse(res, store.state.rs.rdfStore, uri, 'text/turtle');
    return res;
  } catch (error) {
    console.log(error);
    console.log("Search failed");
  }
}

export default {
  id: wl,
  name: 'WorkList',
  components: {
    TaskCard,
    draggable
  },
  data() {
    return {
            columns: [
        {
          title: "Not started",
          tasks: []
        },
        {
          title: "In Progress",
          tasks: []
        },
        {
          title: "Resolved",
          tasks: []
        },
        {
          title: "Closed",
          tasks: []
        }
      ],
    }
  },
  methods: {
      checklogin(){
        console.log(window.solid.session.info);
        if (window.solid.session.info.isLoggedIn){
          console.log("Test logged in");
        } else {
          console.log("Is logged out, reroute");
          this.$router.push({ path: '/' })
        }
      },
      async getTasksFromServer(){
          let taskTAGS = await wl.searchAndParseResult("#i4task*");
          let taskURIs = [];
          store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
              if(item.object.value.includes("i4task")){
                if(!taskURIs.includes(item.subject.value)) taskURIs.push(item.subject.value);
              }
          });
          for(let t=0; t<taskURIs.length; t++){
            let done = await ur.fetchAndSaveTurtle(taskURIs[t]);
            if(done) console.log("Done");
          }
          this.getTasksFromDatabase();
          store.state.tasks.forEach(task => {
          this.columns.forEach((column, cid) => {
              if(task.state == column.title){
                  this.columns[cid].tasks.push(task);
              }
          });
      });
      },
      getTasksFromDatabase(){
        store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
            if(item.object.value.includes("i4task")){
                console.log("Found item: "+item.object.value);
                let title = "";
                let descript = "";
                let imageuri = "";
                let responsible = "";
                let responsibleName = "";
                let state = "Not started";
                //state = decodeURIComponent(item.object.value.replace("i4task.", ""));
                store.state.rs.rdfStore.match(item.subject, ns.IFOUR("taskStatus"), null).forEach(foundItem => {
                    //console.log("Found state: "+foundItem.object.value);
                    state = foundItem.object.value;
                });
                store.state.rs.rdfStore.match(item.subject, ns.RDFS("label"), null).forEach(foundItem => {
                    title = foundItem.object.value;
                    console.log("Label", title);
                });
                store.state.rs.rdfStore.match(item.subject, ns.RDFS("comment"), null).forEach(foundItem => {
                    descript = foundItem.object.value;
                });
                store.state.rs.rdfStore.match(item.subject, ns.FOAF("image"), null).forEach(foundItem => {
                    imageuri = foundItem.object.value;
                });
                store.state.rs.rdfStore.match(item.subject, ns.FOAF("aimChatID"), null).forEach(foundItem => {
                    responsible = foundItem.object.value;
                    if(store.state.rs.rdfStore.match($rdf.sym(foundItem.object.value), ns.FOAF("name"), null)[0]) responsibleName = store.state.rs.rdfStore.match($rdf.sym(foundItem.object.value), ns.FOAF("name"), null)[0].object.value;
                });
                let matrix = "";
                let position = new Vector3( 0, 0, 0);
                store.state.rs.rdfStore.match(item.subject, ns.NEO("t_"), null).forEach(foundMatrix => {
                    matrix = foundMatrix.object.value;
                });
                if(matrix.length>=10){
                    matrix = matrix.replace(']^^neo:a_matrix-3m', '');
                    matrix = matrix.replace('[0,0,0,0,0,0,', '');
                    const matrixArray = matrix.split(",");
                    position.x = parseInt(matrixArray[0]);
                    position.y = parseInt(matrixArray[1]);
                    position.z = parseInt(matrixArray[2]);
                }
                console.log(position);
                let found = false;
                store.state.tasks.forEach(task => {
                  if(task.uri===item.subject.value) found = true;
                });
                if(!found){
                  console.log("Found state: "+state);
                  store.state.tasks.push({ id: Date.now(), uri: item.subject.value, position: position, title: title, description: descript, imageuri: imageuri, responsible: responsibleName, state: state, date: "16.09.2022", type: "issue" });
                }
            }
        });
      }
  },
  computed: {

  },
  watch: {
    columns: {
        handler: function (newColumns, oldVal) {
            console.log("Task list changed..");
            newColumns.forEach((column, cid) => {
                newColumns[cid].tasks.forEach(task => {
                    // Check if changed column
                    store.state.tasks.forEach((storeTask, stid) => {
                        if(storeTask.id===task.id){
                            if(storeTask.state!=newColumns[cid].title){
                                console.log(storeTask.title+" : Has changed.");
                                console.log("URI: "+storeTask.uri);
                                store.state.rs.rdfStore.removeMatches($rdf.sym(storeTask.uri), ns.IFOUR("taskStatus"), storeTask.state); // Deletes the triple that matches.
                                store.state.tasks[stid].state = newColumns[cid].title;
                                store.state.rs.rdfStore.add($rdf.sym(store.state.tasks[stid].uri), ns.IFOUR("taskStatus"), store.state.tasks[stid].state, $rdf.default);
                                // Start updating Trinpod
                                store.state.rs.tempRdfStore = $rdf.graph();
                                store.state.rs.tempRdfStore.add($rdf.sym(store.state.tasks[stid].uri), ns.IFOUR("taskStatus"), store.state.tasks[stid].state, $rdf.default);
                                let turtle = u.storeToTurtle(store.state.rs.tempRdfStore, "");
                                turtle = ur.modifyTurtle(turtle);
                                console.log(turtle);
                                let taskPod = so.getCharactersBefore(store.state.tasks[stid].uri, "node/")+"i";
                                console.log("Taskpod:"+taskPod);
                                /*ur.uploadTurtle(taskPod, turtle, false).then((successfulFetch) => {
                                  console.log(successfulFetch);
                                });*/
                            } 
                        }
                    });

                    // Check if changed order

                });
            });
        },
        deep: false
    },
  },
  mounted(){
      this.checklogin();      
      this.currentPOD = this.$route.query.location;
      this.currentContainer = this.$route.query.location+'home/';

      /*form.addEventListener('submit', function(e) {
          // Prevent default behavior:
          console.log(e);
      })*/
      this.columns[0].tasks = [];
      console.log(store.state.tasks.length);
      store.state.tasks = [];
      this.getTasksFromServer();
  },
  created(){

  }
};
</script>

