import { Vector3 } from "three";

const v = {};

// Class for array functions

v.v3subtract = function(v1, v2){
  // Subtracts two vectors
  const v3 = new Vector3(0,0,0);
  v3.x = v1.x-v2.x;
  v3.y = v1.y-v2.y;
  v3.z = v1.z-v2.z;
  return v3;
}

v.v3add = function(v1, v2){
  // Subtracts two vectors
  const v3 = new Vector3(0,0,0);
  v3.x = v1.x+v2.x;
  v3.y = v1.y+v2.y;
  v3.z = v1.z+v2.z;
  return v3;
}

v.v3multiply = function(v1, v2){
  // Subtracts two vectors
  const v3 = new Vector3(0,0,0);
  v3.x = v1.x*v2.x;
  v3.y = v1.y*v2.y;
  v3.z = v1.z*v2.z;
  return v3;
}

v.v3half = function(v1){
  // Subtracts two vectors
  const v3 = new Vector3(0,0,0);
  v3.x = v1.x/2;
  v3.y = v1.y/2;
  v3.z = v1.z/2;
  return v3;
}

v.v3cross = function(v1, v2){
  let v3 = {x:0,y:0,z:0};
  v3.x = (v1.y*v2.z)-(v1.z*v2.y);
  v3.y = (v1.z*v2.x)-(v1.x*v2.z);
  v3.z = (v1.x*v2.y)-(v1.y*v2.x);
  return v3;
}

v.rotateRadians = function(cx, cy, x, y, radians) {
  const cos = Math.cos(radians);
  const sin = Math.sin(radians);
  const nx = (cos * (x - cx)) + (sin * (y - cy)) + cx;
  const ny = (cos * (y - cy)) - (sin * (x - cx)) + cy;
  return [nx, ny];
}

v.getAngleRadians = function(fromX,fromY,toX,toY) {
  let deltaX = fromX - toX;
  let deltaY = fromY - toY;
  let radians = Math.atan2(deltaY, deltaX)
  return radians;
}

export default v;
