import Vue from 'vue'
import Vuex from 'vuex'
import BV from './modules/bimview'
import CP from './modules/capacity'
import RS from './modules/rdfStore'
import u from '../components/util'

Vue.use(Vuex)
const N3 = require('n3');

Vue.filter('kb', val => {
  return Math.floor(val/1024);  
});

//const serverURL = 'trinpod.us';
//const serverURL = 'trinpod.eu';
const serverURL = 'stage.graphmetrix.net';

const testNum = 4;

const store = new Vuex.Store({
  state: {
    sensors: [],
    n3store: new N3.Store(),
    devMode: false,
    blankExisted: false,
    customers: [],
    blankNodeIndex: 1,
    blankNodeString: "",
    isLoggedIn: false,
    webId: "",
    loggedInName: "",
    loggedInInitials: "",
    loginExpire: "",
    currentBuildingTag: "",
    tasks: [],
    bmurl: "",
    ministryURI: "https://helsenorge"+testNum+'.'+serverURL,
    server: "https://"+serverURL,
    serverPath: serverURL,
    testNum: testNum,
    whatToDoVisible: false,
    redirect: {path:"/", query:{"location": ""}},
    location: ""
  },


  modules: {
    bv: BV,
    rs: RS,
    cp: CP,
  },

  // Mutations are functions that affect the State
  mutations: {
    blankNodeIncrement(state, qty) {
      if (!qty) {
        qty = 1
      }
      var index = state.blankNodeIndex;
      var idx = index + qty;
      state.blankNodeIndex = idx;
      state.blankNodeString = "_:t" + idx;
      //console.log("blankNodeIncrement", idx);
    },
  },
  // Action are functions that call Mutations
  actions: {
    move({ state, commit, getters, dispatch }, obj) {
	    console.log("dispatch.move - node changed", obj.node)
	    var uri = u.getURI(obj.location)
	    if (state.node !== uri) {
        //await ur.aLoadNode(node);
        //commit('location', uri)
        dispatch('select', { node: uri, noPush: true })
        /*var Types = $.map(ur.getTypes(uri), function (Type) {
            return ur.getLabel(Type);
        })*/

        //var locations = state.locations
        var eObj = Object.assign({}, obj.query)
        //commit('Types', Types)
        //commit('node', uri)
        if (eObj.location !== uri) {
            eObj.location = uri
            eObj.select = uri
            obj.router.push({
          path: obj.path,
          query: eObj //had u.getReload here
            })
        }
        //window.localStorage.setItem('redirect_uri',encodeURIComponent(window.location.href))
      }
    },
    select({ state, commit, getters, dispatch }, obj) {
	    //first change the highlight on the nodes - only applies to graph view
	    var node = obj.node
	    var push = obj.push
	    //var noPush = obj.noPush
	    //var trinity = obj.trinity
	    //var uri = u.getURI(node)
      var uri = node;
	    if (uri !== obj.select) {
        if (push) {
          var eObj = u.getEObj()
          if (eObj.select !== uri) {
            eObj.select = uri
            obj.router.push({
                path: obj.path,
                query: eObj
            })
          }
        }
      }
      console.log("dispatch.select", node)
      //commit('selectURI', null)
      //commit('selectACL', null)
      /*if (uri) {
        dispatch('loadDataSet', { uri: uri })
        commit('selectURI', uri)
        var pathname = window.location.pathname
        var serverPaths = ['/registration','/gmxLogin','/pricing']
        if (!serverPaths.includes(pathname)){
            window.localStorage.setItem('redirect_uri',encodeURIComponent(window.location.href))
        }
      }*/
    },
  },
  computed: {

  },
  methods: {

  }

});

export default store
