// Make the function wait until the connection is made...
function waitForSocketConnection(socket, callback){
    let tried = false;
    setTimeout(
        function () {
            if (socket.readyState === WebSocket.OPEN) {
                console.log("Connection is made")
                if (callback != null){
                    setTimeout( function () {
                      callback();
                    }, 500);
                }
            } else {
                console.log("wait for connection...")
                if(!tried){
                  socket = websocketconnect();
                }
                waitForSocketConnection(socket, callback);
            }

        }, 1000); // wait 5 milisecond for the connection...
}

function websocketconnect() {
    // Websocket things START --------------
    let ws = new WebSocket('ws://localhost:8003');
    ws.onopen = function(e) {
      console.log("[open] Connection established");
      //socket.send("My name is Espen");
    };

    ws.onmessage = function(event) {
      console.log(event.data);
      if(event.data.includes("http")){
        router.push({path: '/fileedit', query: {location: '', select: event.data} });
      }
      //window.open(event.data, '_blank');
    };

    ws.onclose = function(event) {
      if (event.wasClean) {
        console.log(`[close] Connection closed cleanly, code=${event.code} reason=${event.reason}`);
      } else {
        // e.g. server process killed or network down
        // event.code is usually 1006 in this case
        console.log('[close] Connection died');
      }
      //console.log("Trying to reconnect to websocket.");
    };

    ws.onerror = function(error) {
      console.log(`[error] ${error.message}`);
    };
    // Websocket things END --------------

    return ws;
}

