      async openDocumentInZoho(documentURI, filename){
        socket = websocketconnect();
        waitForSocketConnection(socket, function(){
            socket.send("filename:"+filename);
        });
        this.downloading = true;
        try {
          const res = await window.solid.session.fetch(
