          const reader = res.body.getReader();
          var parts = [];
          socket = websocketconnect();
          const pump = () => reader.read()
            .then(({ value, done }) => {
              if (done){
                //console.log('Finished.');
                /*const blob = new Blob(parts, { type: "application" });
                socket = websocketconnect();
