        var endTime = performance.now();
        console.log(`Fetch URI took: ${endTime - startTime} milliseconds`);
        console.log(successBool);
        
        this.filelistitems = [];
        let containedFileURIs = [];
        let fileURIs = [];

        // Get all URIs contained by the current POD (does also include things that are not files)
        let tripleArray = store.state.rs.rdfStore.match($rdf.sym(this.currentContainer), ns.LDP("contains"), null);
        tripleArray.forEach(item => {
            if(!containedFileURIs.includes(item.object.value)) {
              containedFileURIs.push(item.object.value);
            }
        });

        // Get all file URIs where contained by POD
        let fileArray = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.REG("file"));
        fileArray.forEach(item => {
            if(!fileURIs.includes(item.subject.value) && containedFileURIs.includes(item.subject.value)) {
              fileURIs.push(item.subject.value);
            }
        });
        tripleArray = store.state.rs.rdfStore.match(null, ns.RDF("type"), null);
        tripleArray.forEach(item => {
            if(!fileURIs.includes(item.subject.value) && containedFileURIs.includes(item.subject.value) && (item.object.value.includes("http://www.w3.org/ns/iana/") || item.object.value == ns.REG("file"))) {
              fileURIs.push(item.subject.value);
            }
        });

        // Get function URI for crosslink
        tripleArray = store.state.rs.rdfStore.match(null, ns.NEO("i_function"), ns.RDFS("label"));
        this.crosslinkFunctionURI = ""; // Must be cleared to support multiple containers. So it does not use function from other container.
        tripleArray.forEach(item5 => {
            let currentContainerName = uri.slice(0, -1).substring(uri.slice(0, -1).lastIndexOf('/') + 1);
            let resultName = store.state.rs.rdfStore.match($rdf.sym(item5.subject.value), ns.NEO("o_result"), null);
            if(resultName[0].object.value.toLowerCase().includes(currentContainerName)) {
              console.log("Did find URI for Crosslink function: "+item5.subject.value);
              this.crosslinkFunctionURI = item5.subject.value.substring(item5.subject.value.lastIndexOf('/') + 1);
              console.log(this.crosslinkFunctionURI);
            } else {
              console.log("Did NOT find URI for Crosslink function: "+item5.subject.value);
            }
        });


        // Get file names
        fileURIs.forEach((fileitem, i) => {
          let tripleArray = store.state.rs.rdfStore.match($rdf.sym(fileitem), ns.RDFS("label"), null);
            tripleArray.forEach(item3 => {
              console.log("File: ", item3.object.value);
              let found = false;
              this.filelistitems.forEach((item4, i) => {
                if(item4.title === item3.object.value){ found = true; }
              });

              // Get the last changed Time
              let lastChangedTime = "";
              let changeArray = store.state.rs.rdfStore.match($rdf.sym(fileitem), ns.NEO("m_last-change"), null);
              if(changeArray[0]){
                let lastChangeURI = changeArray[0].object.value;
                if(lastChangeURI){
                  let startArray = store.state.rs.rdfStore.match($rdf.sym(lastChangeURI), ns.NEO("t_start"), null);
                  let startURI = startArray[0].object.value;
                  if(startURI){
                    let timeArray = store.state.rs.rdfStore.match($rdf.sym(startURI), ns.NEO("o_result"), null);
                    lastChangedTime = timeArray[0].object.value;
                  }
                }
                lastChangedTime = moment(lastChangedTime).format('MMMM Do YYYY, HH:mm:ss');
              }


              let loading = false;
              if(fileitem.substr(fileitem.length - 4) === '.pdf'){
                loading = true;
              }
              let isContainer = store.state.rs.rdfStore.match($rdf.sym(item3.subject.value), ns.RDF("type"), ns.LDP("BasicContainer"))[0];
              if(!found && !isContainer){
                this.filelistitems.push({ uri: item3.subject.value, filename: item3.object.value, title: item3.object.value, subtitle: lastChangedTime, crossLinkStatus: "none", loading: loading });
              }
            });
          });

          // Get file crosslink status
          fileURIs.forEach((fileitem, i) => {
            var cStatus = 'unstarted';
            if(fileitem.substr(fileitem.length - 4) === '.pdf'){
              let file = ur.fetchAndSaveTurtle($rdf.sym(fileitem), true).then((successBool) => {
                  let currentFileElement = this.filelistitems.find(element => element.uri === fileitem);
                  // Get file crosslink status
                  let statusProcessing = store.state.rs.rdfStore.match($rdf.sym(fileitem), ns.NEO("c_trinpod-processing"), null);
                  console.log(statusProcessing);
                  if(statusProcessing[0]){
                    cStatus = 'processing';
                  }

                  statusProcessing = store.state.rs.rdfStore.match($rdf.sym(fileitem), ns.NEO("c_trinpod-read"), null);
                  console.log(statusProcessing);
                  if(statusProcessing[0]){
                    cStatus = 'linked';
                  }

                  statusProcessing = store.state.rs.rdfStore.match($rdf.sym(fileitem), ns.NEO("c_crosslink"), null);
                  if(statusProcessing[0]){
                    cStatus = 'linked';
                  }

                  console.log("cStatus: "+cStatus);
                  currentFileElement.crossLinkStatus = cStatus;
                  currentFileElement.loading = false;
              });
            }
          });
          

          this.loading = false;
          return successBool;
        });
      },
      getContainersFromURI(uri){
        this.checkIfParentFolder(uri);
        let containers = ur.fetchAndSaveTurtle(uri, true).then((successBool) => {
          this.items = [];
          let containedContainerURIs = [];
          let containerURIs = [];

          containedContainerURIs.push(this.currentContainer); // Adding the current container

          // Get all URIs contained by the current POD (does also include things that are not containers)
          let tripleArray = store.state.rs.rdfStore.match($rdf.sym(this.currentContainer), ns.LDP("contains"), null);
          tripleArray.forEach(item => {
              if(!containedContainerURIs.includes(item.object.value)) {
                containedContainerURIs.push(item.object.value);
              }
          });

          // Get all container URIs where contained by POD
          tripleArray = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.LDP("BasicContainer"));
          tripleArray.forEach(item => {
              if(!containerURIs.includes(item.subject.value) && containedContainerURIs.includes(item.subject.value)) {
                containerURIs.push(item.subject.value);
              }
          });

          // Get container names
          containerURIs.forEach((containeritem, i) => {
              console.log(containeritem);
              let tripleArray = store.state.rs.rdfStore.match($rdf.sym(containeritem), ns.RDFS("label"), null);
              tripleArray.forEach(item3 => {
                console.log("Container: ", item3.object.value);
                let found = false;
                this.items.forEach((item4, i) => {
                  if(item4.title === decodeURI(item3.object.value)){ found = true; }
                });

                // Get the last changed Time
                let lastChangedTime = this.getLastChangedTime(containeritem);
                if(!found){
                  this.items.push({ uri: item3.subject.value, name: decodeURI(item3.object.value), title: decodeURI(item3.object.value), subtitle: lastChangedTime });
                }
              });
              if(tripleArray.length === 0){
                // Container does not have a label
                let lastChangedTime = this.getLastChangedTime(containeritem);
                this.items.push({ uri: containeritem, name: decodeURI(so.getCharactersAfter(so.removeLastCharacter(containeritem), "/")), title: decodeURI(so.getCharactersAfter(so.removeLastCharacter(containeritem), "/")), subtitle: lastChangedTime });
              }
            });
            return successBool;
        });
      },
      getLastChangedTime(containeritem){
          // Get the last changed Time
          let lastChangedTime = "";
          let changeArray = store.state.rs.rdfStore.match($rdf.sym(containeritem), ns.NEO("m_last-change"), null);
          let lastChangeURI = changeArray[0] ? changeArray[0].object.value : null;
          if(lastChangeURI){
            let startArray = store.state.rs.rdfStore.match($rdf.sym(lastChangeURI), ns.NEO("t_start"), null);
            if(startArray[0]) return "";
            let startURI = startArray[0].object.value;
            if(startURI){
              let timeArray = store.state.rs.rdfStore.match($rdf.sym(startURI), ns.NEO("o_result"), null);
              lastChangedTime = timeArray[0].object.value;
            }
          }
          return moment(lastChangedTime).format('MMMM Do YYYY, HH:mm:ss');
      },
      checkIfParentFolder(uri){
          let tripleArray = store.state.rs.rdfStore.match(null, ns.LDP("contains"), $rdf.sym(uri));
          tripleArray.forEach(item => {
              console.log("Parent found:", item.subject.value);
              if(item.subject.value != this.currentPOD){
                this.parentContainer = item.subject.value;
              } else {
                this.parentContainer = "";
              }
          });
      },
      async createContainer(containerName){
        let createContainerURL = this.currentContainer + encodeURI(containerName) + "/"; // Very important to add the / in end of url - Espen
        if (containerName.length >= 3){
          this.loading = true;
          try {
            console.log("Trying to create container..");
            await window.solid.session.fetch(
              createContainerURL, {
                method: "PUT",
                credentials: "include",
                body:'<> <http://www.w3.org/2000/01/rdf-schema#label> "' + encodeURI(containerName) +'" .',
                headers: {
                  "Content-Type": "text/turtle",
                  "link": '<http://www.w3.org/ns/ldp#Container>; rel="type", <http://www.w3.org/ns/ldp#BasicContainer>; rel="type"',
                },
            }).then((res) => {
              console.log("Container response:");
              console.log(res);
              this.dialog = false;
              this.loading = false;
              this.reloadData();
              this.createContainerName = "";
            });
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("Containername is too short.");
        }
      },
      async startCrossLink(fileURI){
        const timeElapsed = new Date();
        const date = new Date(timeElapsed);
        this.filelistitems.forEach((file, i) => {
          if(file.uri === fileURI){ file.loading = true; }
        });
        let turtleString = `INSERT DATA {@prefix u1: <`+store.state.webId.slice(0, -1)+`node/>.
@prefix solid: <http://www.w3.org/ns/solid/terms#>.
@prefix neo: <https://neo.graphmetrix.net/node/>.
@prefix ldp: <http://www.w3.org/ns/ldp#>.
@prefix rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>.
@prefix rdfs: <http://www.w3.org/2000/01/rdf-schema#>.
@prefix sio: <http://semanticscience.org/resource/>.
@prefix acl: <http://www.w3.org/ns/auth/acl#>.
@prefix vcard: <http://www.w3.org/2006/vcard/ns#>.

_:13 neo:i_function <http://purl.org/NET/c4dm/timeline.owl#at>;
    neo:o_result "`+date.toISOString()+`"^^<http://www.w3.org/2001/XMLSchema#dateTime>.
_:12 neo:t_start _:13;
    <http://purl.obolibrary.org/obo/OBI_0000308> u1:`+this.crosslinkFunctionURI+`.
<`+fileURI+`> sio:SIO_000230 _:12.
}`;
        console.log(turtleString);
        if(this.crosslinkFunctionURI.length >= 1){ // Check if Function URI has been obtained.
            let res = await window.solid.session.fetch(
            this.currentPOD+'node/Substance', {
              method: "PATCH",
              credentials: "include",
              body: turtleString,
              headers: {
                "Content-Type": "application/sparql-update",
              },
            });
            console.log(res);
            addToStores($rdf.sym(fileURI), ns.NEO("c_trinpod-processing"), date.toISOString());
            this.reloadData();
        } else {
          alert("Unable to get required function for crosslink!!");
        }
      },
  },
  computed: {
      svgFile() {
        return (filename) => getIcon(filename).svg;
      },
      checkCurrentUploading(){
        return (listedfile) => {
          if(listedfile==this.currentUploading){
            return true;
          } else {
            return false;
          }
        }
      }
  },
  watch: {
    files(){
      console.log(this.files);

    },
    currentUploading(){
      console.log(this.currentUploading);
    }
  },
  mounted(){
      this.checklogin();      
      this.currentPOD = this.$route.query.location;
      this.currentContainer = this.$route.query.location+'home/';
      if(!this.$route.query.location){
        this.goToPodSelect();
      }
      this.reloadData();

      const form = document.getElementById('form');
      
      /*form.addEventListener('submit', function(e) {
          // Prevent default behavior:
          console.log(e);
      })*/
  },
  created(){
      console.log("connecting..")
      socket = websocketconnect();
  }
};
</script>

