ifcu.getIFCpropertiesJson = async function(currentURL) {
  // Getting IFC properties
  const propurl = currentURL+"home/IFC/ifc.json";
  try {
    window.solid.session.fetch(propurl, { method: "GET", credentials: "include" }).then((res) => {
      res.arrayBuffer().then((filedata) => {
        let file = new Blob([filedata]);
        const fr = new FileReader();
        fr.onload = function() {
            let jsonResult = JSON.parse(this.result);
            properties = jsonResult;
            console.log("IFC json result:");
            console.log(this.result);
            return ifcu.constructSpatialTree();
        };
        fr.readAsText(file);
      });
    });
  } catch (error) {
    console.log(error);
  }
}

