ifcu.constructSpatialTree = async function(){
  const ifcProject = getFirstItemOfType('IFCPROJECT');

  // Creating Project Node
  const ifcProjectNode = {
      expressID: ifcProject.expressID,
      type: 'IFCPROJECT',
      children: [],
  };

  const relContained = getAllItemsOfType('IFCRELAGGREGATES');
  const relSpatial = getAllItemsOfType('IFCRELCONTAINEDINSPATIALSTRUCTURE');

  await constructSpatialTreeNode(
      ifcProjectNode,
      relContained,
      relSpatial,
  );
  store.state.bv.IFCPropertyJson = ifcProjectNode;
  return ifcProjectNode;
}

// Utils functions
function getFirstItemOfType(type) {
  return Object.values(properties).find(item => item.type === type);
}

function getAllItemsOfType(type) {
  return Object.values(properties).filter(item => item.type === type);
}

// Recursively constructs the spatial tree
async function constructSpatialTreeNode(
  item,
  contains,
  spatials,
) {
  const spatialRels = spatials.filter(
      rel => rel.RelatingStructure === item.expressID,
  );
  const containsRels = contains.filter(
      rel => rel.RelatingObject === item.expressID,
  );

  const spatialRelsIDs = [];
  spatialRels.forEach(rel => spatialRelsIDs.push(...rel.RelatedElements));

  const containsRelsIDs = [];
  containsRels.forEach(rel => containsRelsIDs.push(...rel.RelatedObjects));

  const childrenIDs = [...spatialRelsIDs, ...containsRelsIDs];

  const children = [];
  // Looping through the child elements and creating populating values for it
  for (let i = 0; i < childrenIDs.length; i++) {
      const childID = childrenIDs[i];
      const props = properties[childID];
      let child = {};
      const childkeys = Object.keys(props);
      const childvalues = Object.values(props);
      for (var x = 0; x < childkeys.length; x++) {
        child[childkeys[x]] = childvalues[x];
      }
      /*const child = {
          expressID: props.expressID,
          type: props.type,
          Name: props.Name,
          Tag: props.Tag,
          GlobalId: props.GlobalId,
          OverallHeight: props.OverallHeight,
          OverallWidth: props.OverallWidth,
          children: [],
      };*/

      // For Every child element repeating the step and pushing it to children array
      await constructSpatialTreeNode(child, contains, spatials);
      children.push(child);
  }

  item.children = children;
}


ifcu.getAllSubsetMeshes = function(elementIDs, modelID, ifcLoader) {
  const meshes = [];
  const customID = 'temp-gltf-subset';

  for(const elementID of elementIDs) {
    const coordinates = [];
    const expressIDs = [];
    const newIndices = [];

    const alreadySaved = new Map();

    // Get the subset for the wall

    const subset = ifcLoader.ifcManager.createSubset({
        ids: [elementID],
        modelID,
        removePrevious: true,
        customID
    });

    // Subsets have their own index, but share the BufferAttributes 
    // with the original geometry, so we need to rebuild a new 
    // geometry with this index

    if (!subset.geometry.index) throw new Error('Geometry must be indexed!');

    const positionAttr = subset.geometry.attributes.position;
    const expressIDAttr = subset.geometry.attributes.expressID;

    const newGroups = subset.geometry.groups.filter((group) => group.count !== 0);
    const newMaterials = [];
    const prevMaterials = subset.material;
    let newMaterialIndex = 0;
    newGroups.forEach((group) => {
      newMaterials.push(prevMaterials[group.materialIndex]);
      group.materialIndex = newMaterialIndex++;
    });

    let newIndex = 0;
    for (let i = 0; i < subset.geometry.index.count; i++) {
      const index = subset.geometry.index.array[i];

      if (!alreadySaved.has(index)) {
        coordinates.push(positionAttr.array[3 * index]);
        coordinates.push(positionAttr.array[3 * index + 1]);
        coordinates.push(positionAttr.array[3 * index + 2]);

        expressIDs.push(expressIDAttr.getX(index));
        alreadySaved.set(index, newIndex++);
      }

      const saved = alreadySaved.get(index);
      newIndices.push(saved);
    }

    const geometryToExport = new BufferGeometry();
    const newVerticesAttr = new BufferAttribute(Float32Array.from(coordinates), 3);
    const newExpressIDAttr = new BufferAttribute(Uint32Array.from(expressIDs), 1);

    geometryToExport.setAttribute('position', newVerticesAttr);
    geometryToExport.setAttribute('expressID', newExpressIDAttr);
    geometryToExport.setIndex(newIndices);
    geometryToExport.groups = newGroups;
    geometryToExport.computeVertexNormals();

    const mesh = new Mesh(geometryToExport, newMaterials);
    
    meshes.push(mesh);
  }

  ifcLoader.ifcManager.removeSubset(modelID, undefined, customID);
  return meshes;
}

ifcu.createFloorplan = async function(storeynumber = 1, IFCuri, buildingTag){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  const container = document.createElement('div');
  let viewer = new IfcViewerAPI({ container, backgroundColor: new Color(0xffffff) });
  await viewer.IFC.loader.ifcManager.useWebWorkers(true, '../IFCWorker.js');
  await viewer.IFC.setWasmPath('../');
  this.ifcloading = true;
  try {
    const res = await window.solid.session.fetch(IFCuri, { method: "GET", credentials: "include" });
    console.log(res);
    let filedata = await res.arrayBuffer();
    let fileurl = URL.createObjectURL(new Blob([filedata], { type: "application/x-step"}));
    const ifcModel = await viewer.IFC.loadIfcUrl(fileurl);
    console.log("Model is loaded..");
    // Setup camera controls
    const controls = viewer.context.ifcCamera.cameraControls;
    controls.setPosition(7.6, 4.3, 24.8, false);
    controls.setTarget(-7.1, -0.3, 2.5, false);

    // Generate all plans
    console.log("Starting to generate all plans..")
    await viewer.plans.computeAllPlanViews(ifcModel.modelID);

    const lineMaterial = new LineBasicMaterial({ color: 0x000000 });
    const baseMaterial = new MeshBasicMaterial({
      polygonOffset: true,
      polygonOffsetFactor: 1, // positive value pushes polygon further away
    });
    console.log("Creating edges");
    await viewer.edges.create('example', ifcModel.modelID, lineMaterial, baseMaterial);

    const allPlans = viewer.plans.getAll(ifcModel.modelID);
    console.log("Printing the plan:");

    viewer.dxf.initializeJSDXF(Drawing);
    const ifcProject = await viewer.IFC.getSpatialStructure(ifcModel.modelID);
    const storeys = ifcProject.children[0].children[0].children;
    for (let storey of storeys) {
      for (let child of storey.children) {
        if (child.children.length) {
          storey.children.push(...child.children);
        }
      }
    }
    let plan = allPlans[storeynumber];
    const currentPlan = viewer.plans.planLists[ifcModel.modelID][plan];

    let floorTag = buildingTag;
    const IFCmetadata = await ifcu.getIFCmetaDataFromJson(IFCuri.replace(".ifc", ".json"));
    for(const [key, value] of Object.entries(IFCmetadata.storeys)) {
      if(value==currentPlan.name) floorTag = buildingTag+key;
    }
    console.log(floorTag);

    const storey = storeys.find(storey => storey.expressID === currentPlan.expressID);
    await ifcu.drawProjectedFloorplanItems(storey, currentPlan, ifcModel.modelID, viewer, 0, floorTag);
    viewer.dispose();
    viewer = null;
    this.ifcloading = false;
  } catch (error) {
    console.log(error);
  }
}


ifcu.createPolygon = function( polyArray ) {
  const polyShape = new Shape();
  var i = 0;
  while (polyArray.length > 1) {
    const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
    const x = (polyXYZ[0]);
    const y = (polyXYZ[2]);
    if(i == 0){
      polyShape.moveTo( x, y, 1 );
      i = i+1;
    } else {
      polyShape.lineTo( x, y, 1 );
    }
  }
  polyShape.autoClose = true;
  var geometry = new ShapeGeometry( polyShape );
  var material = new MeshBasicMaterial( {
      color: 0xCCCCCC
  } );
  const mesh = new Mesh(geometry, material);
  return mesh;
}

ifcu.drawProjectedFloorplanItems = async function(storey, plan, modelID, viewer, planheight = 0, floorTag) {
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  const dummySubsetMat = new MeshBasicMaterial({visible: false});
  // Create a new drawing (if it doesn't exist)
  if (!viewer.dxf.drawings[plan.name]) viewer.dxf.newDrawing(plan.name);

  // Get the IDs of all the items to draw
  const ids = storey.children.map(item => item.expressID);

  // If no items to draw in this layer in this floor plan, let's continue
  if (!ids.length) return;

  // If there are items, extract its geometry
  const subset = viewer.IFC.loader.ifcManager.createSubset({
    modelID,
    ids,
    removePrevious: true,
    customID: 'floor_plan_generation',
    material: dummySubsetMat,
  });

  // Get the projection of the items in this floor plan
  const filteredPoints = [];
  const edges = await viewer.edgesProjector.projectEdges(subset);
  const positions = edges.geometry.attributes.position.array;

  // Lines shorter than this won't be rendered
  const tolerance = 0.01;
  for (let i = 0; i < positions.length - 5; i += 6) {

    const a = positions[i] - positions[i + 3];
    // Z coords are multiplied by -1 to match DXF Y coordinate
    const b = -positions[i + 2] + positions[i + 5];

    const distance = Math.sqrt(a * a + b * b);

    if (distance > tolerance) {
      filteredPoints.push([positions[i], -positions[i + 2], positions[i + 3], -positions[i + 5]]);
    }

  }
  const floorplanMeshArray = [];

  //const pointsArray = [];
  const materialBlack = new LineBasicMaterial({ color: 0x000000, linewidth: 3 });
  for (let i = 0; i < filteredPoints.length; i++) {
    const polygon = filteredPoints[i];
    const points = [];
    for (let j = 0; j < polygon.length - 3; j += 2) {
        points.push( new Vector3( polygon[j], planheight+10, -polygon[j + 1] ) );
        points.push( new Vector3( polygon[j + 2], planheight+10, -polygon[j + 3] ) );
    }
    /*points.forEach(point => {
      pointsArray.push(point.x);
      pointsArray.push(point.y);
      pointsArray.push(point.z);
    });*/
    const geometry = new BufferGeometry().setFromPoints( points );
    const line = new Line( geometry, materialBlack );
    floorplanMeshArray.push(line);
  }

  // Create background object
  /*console.log("Pointarray")
  console.log(pointsArray);
  const backgroundMesh = ifcu.createPolygon(pointsArray);
  console.log(backgroundMesh);
  floorplanMeshArray.push(backgroundMesh);
  buildingbox.add(backgroundMesh);*/

  // Draw the projection of the items
  viewer.dxf.drawEdges(plan.name, filteredPoints, 'Projection', Drawing.ACI.BLUE, 'CONTINUOUS');
  //console.log("Edges:")
  //edges.geometry = edges.geometry.translate(0, planheight+10, 0);
  //floorplanMeshArray.push(edges);

  // Clean up
  edges.geometry.dispose();

  // Draw all sectioned items. thick and thin are the default layers created by IFC.js
  viewer.dxf.drawNamedLayer(plan.name, plan, 'thick', 'Section', Drawing.ACI.RED, 'CONTINUOUS');
  viewer.dxf.drawNamedLayer(plan.name, plan, 'thin', 'Section_Secondary', Drawing.ACI.CYAN, 'CONTINUOUS');

  const materialBlue = new LineBasicMaterial({ color: 0x4444ff, linewidth: 3 });
  const materialRed = new LineBasicMaterial({ color: 0xFF5555, linewidth: 3 });
  console.log("Creating first Sections..");
  const points = [];
  viewer.dxf.drawings[plan.name].layers['Section'].shapes.forEach(shape => { // Shape is only line points. Not of type line Espen.
    points.push( new Vector3( shape.x1, planheight+10, -shape.y1 ) );
    points.push( new Vector3( shape.x2, planheight+10, -shape.y2 ) );
  });
  const geometry = new BufferGeometry().setFromPoints( points );
  const line = new LineSegments( geometry, materialBlue );
  floorplanMeshArray.push(line);
  console.log("Creating secondary Sections..");
  const points2 = [];
  viewer.dxf.drawings[plan.name].layers['Section_Secondary'].shapes.forEach(shape => { // Shape is only line points. Not of type line Espen.
    points2.push( new Vector3( shape.x1, planheight+10, -shape.y1 ) );
    points2.push( new Vector3( shape.x2, planheight+10, -shape.y2 ) );
  });
  const geometry2 = new BufferGeometry().setFromPoints( points2 );
  const line2 = new LineSegments( geometry2, materialRed );
  floorplanMeshArray.push(line2);
  console.log("Adding to buildingbox..");
  //floorplanMesh.scale.set(store.state.bv.scaleFactor, store.state.bv.scaleFactor, store.state.bv.scaleFactor);
  //buildingbox.add(floorplanMesh);

  // Download the generated floorplan in GLTF format
  const filename = floorTag+".FLOORPLAN.gltf";
  const exporter = new GLTFExporter();
  const options = {
    trs: false,
    onlyVisible: true,
    truncateDrawRange: true,
    binary: true,
    maxTextureSize: 0,
    forceIndices: false,
  };
  exporter.parse( floorplanMeshArray , function ( gltf ) {
    console.log( gltf );
    const file = new File([new Blob([gltf])], filename);
    const link = document.createElement('a'); 
    document.body.appendChild(link);
    link.href = URL.createObjectURL(file);
    link.download = filename;
    link.click();
  }, options );

  // Download the generated floorplan in DXF format
  const result = viewer.dxf.exportDXF(plan.name);
  const link = document.createElement('a');
  link.download = floorTag+".FLOORPLAN.dxf";
  link.href = URL.createObjectURL(result);
  document.body.appendChild(link);
  link.click();
  link.remove();
}

ifcu.rotateRadians = function(cx, cy, x, y, radians) {
  const cos = Math.cos(radians);
  const sin = Math.sin(radians);
  const nx = (cos * (x - cx)) + (sin * (y - cy)) + cx;
  const ny = (cos * (y - cy)) - (sin * (x - cx)) + cy;
  return [nx, ny];
}

ifcu.getAngleRadians = function(fromX,fromY,toX,toY) {
  let deltaX = fromX - toX;
  let deltaY = fromY - toY;
  let radians = Math.atan2(deltaY, deltaX)
  return radians;
}

ifcu.v3cross = function(v1, v2){
  let v3 = {x:0,y:0,z:0};
  v3.x = (v1.y*v2.z)-(v1.z*v2.y);
  v3.y = (v1.z*v2.x)-(v1.x*v2.z);
  v3.z = (v1.x*v2.y)-(v1.y*v2.x);
  return v3;
}

export default ifcu;
