<template>
  <div class="wrapper" :style="{ 'height': viewHeight + 'px' }">
    <v-btn
          style="position: absolute; z-index: 3; top: 28px; right: 8px;"
          color="#3577cb"
          class="white--text"
          @click="toggleViews"
        >
        <v-icon>mdi-arrow-split-vertical</v-icon>
    </v-btn>
    <Multipane scroll="no" id="resizer" class="vertical-panes" :style="{ 'height': viewHeight + 'px' }" layout="vertical" @paneResizeStart="paneresizestarted($event)" @paneResizeStop="paneresizestopped($event)">
      <div class="vertical" :style="{ 'height': viewHeight + 'px' }" ref="leftpane" style="overflow: scroll"><BMviewer msg="Show BMViewer form" /></div>
      <multipane-resizer class="resizer" :style="{ 'height': viewHeight + 'px' }">R</multipane-resizer>
      <div class="verticalflex" :style="{ 'height': viewHeight + 'px' }" ref="rightpane" style="overflow: scroll"><BMDetails msg="Show details form" /></div>
    </Multipane>
  </div>
</template>

<script>
// @ is an alias to /src
import BMDetails from '../components/BMDetails.vue'
import BMviewer from '../components/BMviewer.vue'
import "vuetify/dist/vuetify.min.css";
import { mapMutations } from "vuex";
import store from '../store';
import { Multipane, MultipaneResizer } from 'vue-multipane';

export default {
  name: 'TSviewerV',
  components: {
    BMDetails,
    BMviewer,
    Multipane,
    MultipaneResizer
  },
  data: function() {
    return {
      bimVisible: true,
      viewHeight: 600,
    };
  },
  mounted: function() {

  },
  methods: {
    updateCanvasSize(){
      const rightpane = this.$refs.rightpane
      console.log(rightpane);
      const height = window.innerHeight-60;
      this.viewHeight = height;
    },
    toggleViews(){
      const leftpane = this.$refs.leftpane
      const rightpane = this.$refs.rightpane
      console.log(leftpane)
      if(leftpane.style.width == "70vw"){
        leftpane.style.width = "100vw";
      } else {
        leftpane.style.width = "70vw";
      }
      this.updateCanvasSize();
    },
    windowResize(){
      this.updateCanvasSize();
    },
    paneresizestarted: function(event) {
      console.log("resize start...")
    },
    paneresizestopped: function(event) {
      console.log("resize stop...")
      this.updateCanvasSize();
      window.dispatchEvent(new Event('resize'));
    },
  },
  mounted(){
    this.windowResize();
  },
  created() {
    window.addEventListener("resize", this.windowResize);
  },
  destroyed() {
    window.removeEventListener("resize", this.windowResize);
  }
}
</script>

<style scoped>
#wrapper {
  overflow: hidden;
}

.vertical-panes {
  overflow: hidden;
}
.vertical {
  position: relative;
  overflow: hidden;
  width: 100vw;
}

.resizer {
  width: 0px; 
  background-color: gray;
}

.verticalflex {
  position: relative;
  overflow: hidden;
  right: 0px;
  margin: 0px;
  padding: 0px;
  flex-grow: 1;
  width: 10px;
  height: 95vh;
  background-color:lightblue;
}

.horizontal {
  position: relative;
  height: 50vh;
  width: 100vw;
}

.horizontalflex {
  position: relative;
  flex-grow: 1;
  width: 100vw;
}
</style>

