const so = {};

// Class for string functions

so.getInitials = function (string) {
    var names = string.split(' '),
        initials = names[0].substring(0, 1).toUpperCase();

    if (names.length > 1) {
        initials += names[names.length - 1].substring(0, 1).toUpperCase();
    }
    return initials;
};

so.makeRandomColor = function(){
    var c = '';
    while (c.length < 6) {
      c += (Math.random()).toString(16).substr(-6).substr(-1)
    }
    return '#' + c;
};

so.arraymove = function(arr, fromIndex, toIndex) {
    var element = arr[fromIndex];
    arr.splice(fromIndex, 1);
    arr.splice(toIndex, 0, element);
    return arr;
}

so.getCharactersAfter = function(str, char){
    return str.split(char).pop();
}

so.getCharactersBefore = function(str, char){
    return str.split(char)[0];
}

so.isLetter = function(str) {
    let c = str.charAt(0)
    return c.toLowerCase() != c.toUpperCase();
}

so.removeLastCharacter = function(str){
    return str.substr(0, str.length - 1);
}

so.removeCharactersAfter =  function(str, char){
    return str.substring(0, str.indexOf(char))
}

export default so;