<template>
  <div scroll="no" class="wrapper" :style="{ 'height': viewHeight + 'px' }">
    <v-btn
          style="position: absolute; z-index: 3; top: 28px; right: 8px;"
          color="#3577cb"
          class="white--text"
          @click="toggleViews"
        >
        <v-icon>mdi-arrow-split-vertical</v-icon>
    </v-btn>
    <Multipane scroll="no" id="resizer" class="vertical-panes" :style="{ 'height': viewHeight + 'px' }" layout="vertical" @paneResizeStart="paneresizestarted($event)" @paneResizeStop="paneresizestopped($event)">
      <div class="vertical" ref="leftpane" :style="{ 'height': viewHeight + 'px' }" style="overflow: scroll"><Capacity msg="Show capacity form" /></div>
      <multipane-resizer class="resizer" :style="{ 'height': viewHeight + 'px' }">-</multipane-resizer>
      <BIMviewer class="verticalflex" :style="{ 'height': viewHeight + 'px' }" ref="myviewer" msg="Show bimviewer form" />
    </Multipane>
  </div>
</template>

<script>
// @ is an alias to /src
import Capacity from '../components/Capacity.vue'
import BIMviewer from '../components/BIMviewer.vue'
import "vuetify/dist/vuetify.min.css";
import { mapMutations } from "vuex";
import store from '../store';
import { Multipane, MultipaneResizer } from 'vue-multipane';

export default {
  name: 'CapacityV',
  components: {
    Capacity,
    BIMviewer,
    Multipane,
    MultipaneResizer
  },
  data: function() {
    return {
      bimVisible: true,
      viewHeight: 600,
    };
  },
  methods: {
    ...mapMutations('bv', ["RESIZE"]),
    updateCanvasSize(){
      const myviewer = this.$refs.myviewer
      console.log(myviewer);
      const height = window.innerHeight-60;
      this.viewHeight = height;
      const width = myviewer.$el.clientWidth+10
      console.log("Width is: "+width);
      store.state.bv.renderer.setSize(width, height);
      if(store.state.bv.twodim){
        store.state.bv.labelrenderer.setSize(width, height);
        store.state.bv.twodimcamera.left = width/-2;
        store.state.bv.twodimcamera.right = width/2;
        store.state.bv.twodimcamera.top = height/2;
        store.state.bv.twodimcamera.bottom = height/-2;
        store.state.bv.twodimcamera.updateProjectionMatrix();
      } else {
        if(store.state.bv.thirdperson){
          store.state.bv.thirdpersoncamera.aspect = width/height;
          store.state.bv.thirdpersoncamera.updateProjectionMatrix()
        } else {
          store.state.bv.labelrenderer.setSize(width, height);
          store.state.bv.camera.aspect = width/height;
          store.state.bv.camera.updateProjectionMatrix()
        }
      }
    },
    toggleViews(){
      const leftpane = this.$refs.leftpane
      console.log(leftpane)
      if(leftpane.clientWidth>0){
        leftpane.style.width = "0vw";
      } else {
        leftpane.style.width = "100vw";
      }
      this.updateCanvasSize();
    },
    windowResize(){
      this.updateCanvasSize();
    },
    paneresizestarted: function(event) {
      console.log("resize start...")
    },
    paneresizestopped: function(event) {
      console.log("resize stop...")
      this.updateCanvasSize();
      window.dispatchEvent(new Event('resize'));
    },
  },
  mounted(){
    this.windowResize();
  },
  created() {
    window.addEventListener("resize", this.windowResize);
  },
  destroyed() {
    window.removeEventListener("resize", this.windowResize);
  }
}
</script>

<style scoped>
#wrapper {
  overflow: hidden;
}

.vertical-panes {
  overflow: hidden;
}

.vertical {
  position: relative;
  height: 95vh;
  width: 30vw;
}

.resizer {
  width: 12px; 
  background-color: gray;
}

.verticalflex {
  position: relative;
  overflow: hidden;
  right: 0px;
  margin: 0px;
  padding: 0px;
  flex-grow: 1;
  width: 10px;
  height: 95vh;
}

.horizontal {
  position: relative;
  height: 50vh;
  width: 100vw;
}

.horizontalflex {
  position: relative;
  flex-grow: 1;
  width: 100vw;
}
</style>
