<script>
import i18n from './plugins/i18n.js'
import store from './store';
import ur from './components/util-rdf.js'

export default {
  name: 'App',

  data: () => ({
      languages: [
          { flag: 'us', language: 'en' },
          { flag: 'no', language: 'no' }
      ],
      isLoggedIn: false,
      webId: "",
      loggedInName: "",
      loggedInInitials: "",
      loginExpire: "",
      devMode: false,
      window: {
        width: 0,
        height: 0
      }
  }),
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    changeLocale(locale) {
      i18n.locale = locale;
    },
    followLink(linkName){
      if(!this.$route.path.includes(linkName)){
        this.$router.push({path: linkName, query: {location: this.$route.query.location, select: this.$route.query.select} });
      }
    },
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
    }
  },
  watch: {
    "$store.state.isLoggedIn": function (val) {
      this.isLoggedIn = store.state.isLoggedIn;
    },
    "$store.state.webId": function (val) {
      this.webId = store.state.webId;
    },
    "$store.state.loggedInName": function (val) {
      this.loggedInName = store.state.loggedInName;
    },
    "$store.state.loggedInInitials": function (val) {
      this.loggedInInitials = store.state.loggedInInitials;
    },
    "$store.state.loginExpire": function (val) {
      this.loginExpire = store.state.loginExpire;
    },
    "$store.state.devMode": function (val) {
      this.devMode = store.state.devMode;
    },
    "$store.state.redirect": function (val) {
      this.$router.push(val);
    },
    $route(to, from) {
      console.log("to.mode", to, "from.mode", from);
      store.state.location = to.query.location;
      return;
      var routerPush
      var redirectLogin
      if (this.isLoggedIn) {
        //var Node = this.$store.getters.getNode(to);
        //var fNode = this.$store.getters.getNode(from);
        var Node = to;
        var fNode = from;
        Node = decodeURIComponent(Node);
        var qObj = to.query;
        fNode = decodeURIComponent(fNode);
        var fObj = from.query;
        if (qObj.preview && qObj.location){
            this.$store.commit("loading", false)
        } else {
          if (!fObj.select || !fObj.location){ //make sure required query location/select are present
            routerPush = true
            if (fObj.preview){
                fObj.select = fObj.preview
                fObj.location = fObj.preview
            } else if (fObj.location){
                fObj.select = fObj.location
            } else if (fObj.select){
                fObj.location = fObj.select
            } else if (this.$store.state.webId){
                fObj.location = this.$store.state.webId
                fObj.select = this.$store.state.webId
            } else {
                redirectLogin = true
            }
          }
          if (redirectLogin){
            this.$router.push({ path: "/"})
          } else if (routerPush){
            this.$router.push({ path: this.$router.currentRoute.path, query: fObj})
          } else {
            if (Node + JSON.stringify(qObj) !== fNode + JSON.stringify(fObj) || !fNode) {
              console.log("action: URL changed", Node, fNode, qObj, fObj);
              if (qObj.location && fObj.location && qObj.location !== fObj.location) {
                //new selection
                this.$store.dispatch("move", {location: qObj.location, select: this.$router.currentRoute.query.select, path: this.$router.currentRoute.path, router: this.$router});
              } else if (qObj.select && fObj.select && qObj.select !== fObj.select) {
                //new selection - check that select is in current pod
                if (ur.getWebId(qObj.select) !== ur.getWebId(qObj.location)){
                  this.$store.dispatch("move", {node: qObj.select, select: this.$router.currentRoute.query.select, path: this.$router.currentRoute.path, router: this.$router})
                } else { 
                  this.$store.dispatch("select", {node: qObj.select, noPush: true, select: this.$router.currentRoute.query.select, path: this.$router.currentRoute.path, router: this.$router});
                }
              }
              /* else if ((Node && !this.$store.state.node) || Node !== fNode) {
                this.$store.dispatch("move", Node);
              }*/
                
            }
          }
        }
	    }
    },
  }
};
</script>
