  console.log(uri);
  try {
    let res = await window.solid.session.fetch(
      uri, {
        method: "GET",
        credentials: "include",
        headers: {
          "Accept": "text/turtle"
        },
    }).then(res => res.text());
    $rdf.parse(res, store.state.rs.rdfStore, uri, 'text/turtle');
    return res;
  } catch (error) {
    console.log("Search failed");
    console.log(error);
  }
}

function streamToString (stream) {
  const chunks = [];
  return new Promise((resolve, reject) => {
    stream.on('data', (chunk) => chunks.push(Buffer.from(chunk)));
    stream.on('error', (err) => reject(err));
    stream.on('end', () => resolve(Buffer.concat(chunks).toString('utf8')));
  })
}


