      getAllBuildings(){
        const buildingURIs = [];
        store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.BOT("Building")).forEach(st => {
          console.log("Found building URI: "+st.subject.value)
          buildingURIs.push(st.subject.value);
        });
        buildingURIs.forEach(bURI => {
          store.state.rs.rdfStore.match($rdf.sym(bURI), ns.RDFS("label"), null).forEach(st => {
            console.log("Found building Name: "+st.object.value)
            if(!this.buildingURIs.includes(bURI)) this.buildingURIs.push(bURI);
            if(!this.buildingURIs.includes(st.object.value)) this.buildingNames.push(st.object.value);
          });
        });
        return buildingURIs;
      },
      createRoomList(){// : async() => {
        this.roomTableList = [];
        // Get Room ids
        let bedRoomCount = 0;
        let survailanceRoomCount = 0;
        let isolateRoomCount = 0;
        let cotRoomCount = 0;
        this.selectedrooms.forEach((item, i) => {
          let roomTAG = "";
          store.state.rs.rdfStore.match($rdf.sym(item), ns.TAG("tag"), null).forEach(st => {
             roomTAG = st.object.value;
          });
          let storeyTAG = "";
          storeyTAG = roomTAG.replace("."+so.getCharactersAfter(roomTAG, "."), ""); // NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG.E01
          let buildingTAG = storeyTAG.replace("."+so.getCharactersAfter(storeyTAG, "."), ""); // NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG
          const storeyTAGPart = so.getCharactersAfter(storeyTAG, "."); // E01

          const storeyTAGs = [];
          storeyTAGs.push(buildingTAG+".FLOY_S."+storeyTAGPart); // Make a loop for all wings
          storeyTAGs.push(buildingTAG+".FLOY_N."+storeyTAGPart);
          storeyTAGs.push(buildingTAG+".FLOY_O."+storeyTAGPart);
          storeyTAGs.push(buildingTAG+".FLOY_V."+storeyTAGPart);
          
          let storeyName = "";
          storeyTAGs.forEach(storeyT => {
            store.state.rs.rdfStore.match(null, ns.TAG("tag"), $rdf.literal(storeyT, null, ns.XSD('string'))).forEach(st => {
                store.state.rs.rdfStore.match($rdf.sym(st.subject.value), ns.RDFS("label"), null).forEach(label => {
                  storeyName = label.object.value;
                });
            });
          });

          let floor = "0";
          store.state.rs.rdfStore.match(null, ns.DUL("hasPart"), $rdf.sym(item)).forEach(st => {
              if(store.state.rs.rdfStore.match(st.subject, ns.RDF("type"), ns.BOT("Storey")).length >= 0){
                store.state.rs.rdfStore.match(st.subject, ns.RDFS("label"), null).forEach(st => {
                  floor = st.object;
                });
              }
            //floor = decodeURI(st.object);
          });

          // Count bed capacity
          let roomBedCount = 0;
          store.state.rs.rdfStore.match($rdf.sym(item), ns.NEO("m_port"), null).forEach(st => {
            store.state.rs.rdfStore.match($rdf.sym(st.object.value), ns.RDF("type"), ns.NEO("y_port")).forEach(st => {
              //console.log("Bed URI " + st.subject);
              roomBedCount = roomBedCount+1;
            });
          });

          // Get room name
          let roomName = "";
          store.state.rs.rdfStore.match($rdf.sym(item), ns.RDFS("label"), null).forEach(st => {
            roomName = st.object.value;
          });

          let partFunction = "";
          // Get room function
          store.state.rs.rdfStore.match($rdf.sym(item), ns.NEO("b_capable"), null).forEach(st => {
              store.state.rs.rdfStore.match($rdf.sym(st.object.value), ns.TAG("tag"), null).forEach(st => {
                  let tagString = ""+decodeURI(st.object);
                  //console.log(tagString);
                  if(tagString.includes("SENGEROM")){
                    bedRoomCount = bedRoomCount+1;
                  }
                  if(tagString.includes("OVERV")){
                    survailanceRoomCount = survailanceRoomCount+1;
                  }
                  if(tagString.includes("ISOLAT")){
                    isolateRoomCount = isolateRoomCount+1;
                  }
                  if(tagString.includes("BARSEL")){
                    cotRoomCount = cotRoomCount+1;
                  }
              });
              store.state.rs.rdfStore.match($rdf.sym(st.object.value), ns.RDFS("label"), null).forEach(st => {
                partFunction = st.object.value.replace("Delfunksjon: ", "");
              });
          });
          this.roomTableList.push(JSON.parse('{"roomname":"'+roomName+'","storey":"'+storeyName+'","function":"'+partFunction+'","capacity":"'+roomBedCount+'","roomid":"'+item+'"}'));
        });
        this.values = [   { name: '134 - Sengerom', one: bedRoomCount },
                    { name: '113 - Overvkingsplass', one: survailanceRoomCount },
                    { name: '62 - Isolat', one: isolateRoomCount },
