      },
      getRHFDataFromTrinity(){
        const URIofInterest = store.state.ministryURI;
        if(store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).length != 0){
          console.log("RHF FOUND!, getting data from local storage.");
          store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).forEach(st => {
             console.log("RHF URI" + st.object);
             this.rhfselectedURIs.push(st.object);
          });
          // Getting URI names
          this.rhfselectedURIs.forEach((item, i) => {
            const URITriple = $rdf.st(item, ns.FOAF("name"), null);
            let tripleArray = [];
            tripleArray.push(URITriple);
            this.getURIObject(tripleArray, URITriple, '').then((rhfURIs) => {
              rhfURIs.forEach((item, i) => {
                if(!this.rhf.includes(''+item.object)){
                  this.rhf.push(''+item.object);
                  this.rhfselected.push(''+item.object);
                }
              });
            });
          });
        } else {
          console.log("RHF NOT FOUND!, getting data from TrinPod");
          ur.aLoadURI(URIofInterest+"/i", null, null).then((result) => {
            if(result){
              store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).forEach(st => {
                   console.log("RHF URI" + st.object);
                   store.state.rs.rdfStore.add(st);
                   this.rhfselectedURIs.push(st.object);
              });
              // Getting URI names
              this.rhfselectedURIs.forEach((item, i) => {
                const URITriple = $rdf.st(item, ns.FOAF("name"), null);
                let tripleArray = [];
                tripleArray.push(URITriple);
                this.getURIObject(tripleArray, URITriple, '').then((rhfURIs) => {
                  rhfURIs.forEach((item, i) => {
