      async getHospitalFunctions(){
        let functionsData = await cp.searchAndGetTriples("#NMH.F.SYKEHUS.*");
        this.csfitems = [];
        const mainfunctionTags = [];
        const mainfunctionURIs = [];
        const partfunctionTags = [];
        const partfunctionURIs = [];
        store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
          if(item.object.value.includes("NMH.F.SYKEHUS.")){
            // Check if main function
            let foundMainfunction = false;
            store.state.rs.rdfStore.match($rdf.sym(item.subject.value), ns.RDFS("label"), null).forEach(subitem => {
              if(subitem.object.value.includes("Hovedfunksjon")) foundMainfunction = true;
            });
            if(foundMainfunction){
              console.log("Found: "+ item.object.value);
              if(!mainfunctionURIs.includes(item.subject.value)){
                mainfunctionTags.push(item.object.value);
                mainfunctionURIs.push(item.subject.value);
              }
            } else {
              if(!partfunctionURIs.includes(item.subject.value)){
                partfunctionTags.push(item.object.value);
                partfunctionURIs.push(item.subject.value);
              }
            }
          }
        });
        let idCounter = 0;
        mainfunctionTags.forEach((mainfunctionTag, i) => {
          const partFunctions = [];
          partfunctionTags.forEach((partfunctionTag, j) => {
            if(partfunctionTag.includes(mainfunctionTag+".")){
              let partFunctionName = store.state.rs.rdfStore.match($rdf.sym(partfunctionURIs[j]), ns.RDFS("label"), null)[0].object.value.replace("Delfunksjon: ", "");
              partFunctionName = u.decodeCharacters(partFunctionName);
              partFunctions.push({id: idCounter, name: partFunctionName, color: this.colorlist[i], tag: partfunctionTag, uri: partfunctionURIs[j] })
              idCounter++;
            }
          });
          partFunctions.sort(customCompare);
          let mainFunctionName = store.state.rs.rdfStore.match($rdf.sym(mainfunctionURIs[i]), ns.RDFS("label"), null)[0].object.value.replace("Hovedfunksjon: ", "");
          mainFunctionName = u.decodeCharacters(mainFunctionName);
          this.csfitems.push({id: idCounter, name: mainFunctionName, color: this.colorlist[i], tag: mainfunctionTag, uri: mainfunctionURIs[i], children: partFunctions })
          this.csfitems.sort(customCompare);
          idCounter++;
        });
      },
      setAllFunctionsSelected(){
        if(this.csfselection.length>0){
