        this.selectedrooms.push('https://hovedbygg111.trinpod.us/node/t_uk');
    },
    "$store.state.bv.selectedRoomNames": function (val) { // Functionality for calling wayfinding
      if(store.state.bv.selectedRoomNames.length == 2){
        console.log("Call wayfinding now.");
        const room1 = $rdf.literal(store.state.bv.selectedRoomNames[0], 'en');
        const room2 = $rdf.literal(store.state.bv.selectedRoomNames[1], 'en');
        console.log("Room1: "+store.state.bv.selectedRoomNames[0]);
        console.log("Room2: "+store.state.bv.selectedRoomNames[1]);
        let foundWay = wf2.methods.wayBetweenRooms(room1, room2, 1, "");
        store.state.bv.wayBetweenRooms = foundWay;
      }
    },
    "$store.state.rs.urisFetched": function (val) {
        this.urisFetched = store.state.rs.urisFetched;
    },
    "$store.state.cp.roomDetailURI": function (val) {
        if(val.length>=5){
          this.roomDetailsVisible = true;
        } else {
          this.roomDetailsVisible = false;
        }
    },
    "$store.state.cp.buildingDetailURI": function (val) {
        if(val.length>=1){
          this.buildingDetailsVisible = true;
        } else {
          this.buildingDetailsVisible = false;
        }
    },
    roomTableSelectedRow: function (val) {
      store.state.bv.selectedRoomIDs = [];
      store.state.bv.selectedRoomNames = [];
      val.forEach((roomObject, i) => {
        //console.log(roomObject.roomid);
        store.state.bv.selectedRoomIDs.push(roomObject.roomid);
        const name = roomObject.roomname.replace("Room ", "").replace("Rom ", "");
        store.state.bv.selectedRoomNames.push(name);
      });
    },
    csfselection: function(val, oldVal){
      this.partfunctionsSelected = [];
      //console.log(val);
      // Remove all previous rooms
      store.state.bv.roomGeometry = [];
      const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      for( var i = buildingbox.children.length - 1; i >= 0; i--) { 
        if(!buildingbox.children[i].name.includes("FLOORPLAN")) buildingbox.remove(buildingbox.children[i]);
      }
      // Add new rooms
      val.forEach(roomvalue => {
         this.partfunctionsSelected.push({uri: roomvalue.uri, color: roomvalue.color});
      });
    },
    partfunctionsSelected: function(val, oldVal){
      // Find rooms based on function
      this.filteredrooms = [];
      this.selectedrooms = [];
      val.forEach(functionvalue => {
        //store.state.rs.rdfStore.match(null, ns.NEO("b_capable"), $rdf.sym(functionvalue.uri)).forEach(roomItem => { // Must be replaced back when database is not broken
        this.selectedRoomURIs.forEach(roomURI => { // Sometimes makes issues with matching on next line
          try{
            store.state.rs.rdfStore.match($rdf.sym(roomURI), null, $rdf.sym(functionvalue.uri)).forEach(roomItem => {
              if(!this.selectedrooms.includes(roomItem.subject.value)){ // Prevent duplicates in list
                this.filteredrooms.push({uri: roomItem.subject.value, color: functionvalue.color});
                this.selectedrooms.push(roomItem.subject.value);
              }
            });
          } catch(error){}
        });
      });
    },
    filteredrooms: function(val, oldVal){
      // Add newly selected rooms
      //console.log("Found rooms");
      val.forEach(room => {
        //console.log("RoomURI: "+room.uri);
        store.state.rs.rdfStore.match($rdf.sym(room.uri), ns.IFOUR("m_polyline"), null).forEach((polyline, i) => {
            bmu.addRoomMatrix(polyline, colorToSigned24Bit(room.color), 1, room.uri);
          });
      });
      this.createRoomList();
    },
  },
  mounted() {
    //this.createInput();
    this.hrimage = "hr1111.png";
    this.checklogin();
    store.state.bv.mapMode = true;
    this.getHospitalFunctions();
    this.getRHFDataFromTrinity();
    store.state.bv.twodim = true;
    this.twodim = true;
    

  /*  getRegions("shb").then((result) => {
