           this.rhfselected.push("Helse Nord RHF");
           this.hfselected.push("Helgelandssykehuset HF");
           this.lokselected.push("1 - Mo i Rana");
           this.buildingSelected.push("111 - Hovedbygg");
         }
      });
      //this.populateSpaces();
    }, 2000);

    /*
    // Get  ministryUri
    let ministryURI = "";
    console.log("Finding MinistryURI ?");
    store.state.rs.rdfStore.match(null, ns.GMXO("2"), "MinistryOfHealth").forEach(st => {
       console.log("MinistryURI " + st.subject);
       ministryURI = st.subject;
    });

    // Get Region URIs
    let regionURIs = [];
    console.log("Finding RegionURI ?");
    store.state.rs.rdfStore.match(ministryURI, ns.DUL("hasPart"), null).forEach(st => {
       console.log("Region URI " + st.object);
       regionURIs.push(st.object);
    });

    // Get Region names
    regionURIs.forEach((item, i) => {
      store.state.rs.rdfStore.match(item, ns.RDFS("label"), null).forEach(st => {
         console.log("Region Name " + st.object);
         this.rhf.push(''+st.object);
      });
    });*/

  },
  created(){
    
