const ir = {}

import { Vector3, BufferGeometry, BufferAttribute } from "three";
// Required by shader --------------
import { flatten } from 'lodash';
import implicitMesh from 'implicit-mesh/shader';
import angleNormals from 'angle-normals';
// Required by shader --------------

export default {
  ir: ir,
  name: 'ImplicitRoom',
  methods: {
    createImplicitFromPolyline(polyArray, geoW, geoL, geoH, scaleFactor) {
      let scaleF = Math.max(Math.max(geoW, geoL), geoH); // Gets the largest side of bounding box
      let heightSc = (geoH/scaleF);
      const triangles = this.triangulatePolyLine(polyArray, scaleFactor, geoL, scaleF, heightSc);
      const startTime = performance.now();
      const shader = this.createShaderCode(triangles, heightSc);
      const mesh = implicitMesh(256, shader);
      var endTime = performance.now();
      console.log(`Implicit shader used: ${endTime - startTime} milliseconds`);
      const geo = new BufferGeometry()

      const v = new Float32Array(flatten(mesh.positions))
      const positions = new BufferAttribute(v, 3)

      const n = new Float32Array(flatten(angleNormals(mesh.cells, mesh.positions)))
      const normals = new BufferAttribute(n, 3)

      geo.setIndex(flatten(mesh.cells))
      geo.setAttribute('position', positions)
      geo.setAttribute('normal', normals)
      geo.scale(scaleF,scaleF,scaleF);
      geo.translate( 0, geoH/2+(scaleF/geoH)*2, (geoL/scaleF)-geoL );
      console.log("GeoL: "+geoL+" ScaleF: "+scaleF);

      //geo.computeBoundingBox()
      //geo.computeBoundingSphere()
      //geo.computeVertexNormals()

      return geo;
    },
    triangulatePolyLine(polyArray, scaleFactor, geoL, scaleF, heightSc){
      // Create points from polyline
      var aPoints = [];
      if(polyArray.length >= 16){
        while (polyArray.length > 1) {
          const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
          const x = (polyXYZ[0]*scaleFactor);
          const y = (polyXYZ[1]*scaleFactor);
          var arr = [x, geoL-y]; // Will be mirrored without geoL-y
          aPoints.push(arr);
        }
      }

      // Start triangulate
      var poly2tri = require('poly2tri');
      var shape = [];
      for(var i=0; i<aPoints.length-1; ++i) {
        shape.push(new poly2tri.Point(aPoints[i][0], aPoints[i][1]));
      }
      var swctx = new poly2tri.SweepContext(shape);
      var triangles = swctx.triangulate();
      let trianglesSc = [];
      for(var i=0; i<triangles.triangles_.length; ++i) {
        trianglesSc.push("vec2("+triangles.triangles_[i].points_[0].x/scaleF+", "+triangles.triangles_[i].points_[0].y/scaleF+")");
        trianglesSc.push("vec2("+triangles.triangles_[i].points_[1].x/scaleF+", "+triangles.triangles_[i].points_[1].y/scaleF+")");
        trianglesSc.push("vec2("+triangles.triangles_[i].points_[2].x/scaleF+", "+triangles.triangles_[i].points_[2].y/scaleF+")");
      }
      return trianglesSc;
    },
    createShaderCode(triangles, heightSc){
      console.log("Triangles length: "+triangles.length);
      let shader = /* glsl */ `

      float opUnion( float d1, float d2 ) { return min(d1,d2); }

      float opSubtraction( float d1, float d2 ) { return max(-d1,d2); }

      float opIntersection( float d1, float d2 ) { return max(d1,d2); }

      float dot2( in vec2 v ) { return dot(v,v); }
      float ndot( in vec2 a, in vec2 b ) { return a.x*b.x - a.y*b.y; }

      float opSmoothUnion( float d1, float d2, float k ) {
        float h = clamp( 0.5 + 0.5*(d2-d1)/k, 0.0, 1.0 );
        return mix( d2, d1, h ) - k*h*(1.0-h); }

      float opSmoothSubtraction( float d1, float d2, float k ) {
          float h = clamp( 0.5 - 0.5*(d2+d1)/k, 0.0, 1.0 );
          return mix( d2, -d1, h ) + k*h*(1.0-h); }

      float opSmoothIntersection( float d1, float d2, float k )
      {
          float h = clamp( 0.5 - 0.5*(d2-d1)/k, 0.0, 1.0 );
          return mix( d2, d1, h ) + k*h*(1.0-h); }

      float sdTriangle( in vec2 p, in vec2 p0, in vec2 p1, in vec2 p2 )
      {
          vec2 e0 = p1-p0, e1 = p2-p1, e2 = p0-p2;
          vec2 v0 = p -p0, v1 = p -p1, v2 = p -p2;
          vec2 pq0 = v0 - e0*clamp( dot(v0,e0)/dot(e0,e0), 0.0, 1.0 );
          vec2 pq1 = v1 - e1*clamp( dot(v1,e1)/dot(e1,e1), 0.0, 1.0 );
          vec2 pq2 = v2 - e2*clamp( dot(v2,e2)/dot(e2,e2), 0.0, 1.0 );
          float s = sign( e0.x*e2.y - e0.y*e2.x );
          vec2 d = min(min(vec2(dot(pq0,pq0), s*(v0.x*e0.y-v0.y*e0.x)),
                           vec2(dot(pq1,pq1), s*(v1.x*e1.y-v1.y*e1.x))),
                           vec2(dot(pq2,pq2), s*(v2.x*e2.y-v2.y*e2.x)));
          return -sqrt(d.x)*sign(d.y);
      }

      float opExtrussion( in vec3 p, in float sdf, in float h )
      {
          vec2 w = vec2( sdf, abs(p.z) - h );
          return min(max(w.x,w.y),0.0) + length(max(w,0.0));
      }

      float sdCross( in vec2 p, in vec2 b, float r )
      {
          p = abs(p); p = (p.y>p.x) ? p.yx : p.xy;
          vec2  q = p - b;
          float k = max(q.y,q.x);
          vec2  w = (k>0.0) ? q : vec2(b.y-p.x,-k);
          return sign(k)*length(max(w,0.0)) + r;
      }

      float sdBox( vec3 p, vec3 b )
      {
        vec3 q = abs(p) - b;
        return length(max(q,0.0)) + min(max(q.x,max(q.y,q.z)),0.0);
      }

      float sdSphere( vec3 p, float s )
      {
        return length(p)-s;
      }

      float extRoom(vec3 p){
        vec2 shapes[`+triangles.length+`];
        `
        console.log("Shader code generated...");
        let tNum = 0;
        while (triangles.length > 1) {
          const points3 = triangles.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
          shader = shader+`float t`+tNum+` = sdTriangle( p.xy, `+points3[0]+`, `+points3[1]+`, `+points3[2]+`);`;
          tNum = tNum+1;
        }
        shader = shader+`float u1 = opUnion( t0, t1);`;
        for(var i=2; i<tNum; ++i) {
          shader = shader+`float u`+i+` = opUnion( u`+(i-1)+`, t`+i+`);`;
        }
        shader = shader+`
        float t3d = opExtrussion(p, u`+(tNum-1)+`, `+heightSc/2+`);
        return t3d;
      }

      float surface (vec3 p) {
        p = vec3(p.z, p.x, p.y );
        return extRoom(p);
      }
      `
      return shader;
    },
  }
};
