    async contentListener(message) {
      if(typeof message.data!="string") return;
      const messageString = message.data+"";
      console.log("Did receive message: "+messageString)
      const messageObj = JSON.parse(messageString);
      console.log("Select: "+messageObj.select);
      if(messageObj.location) {
        this.$router.push({path: this.$route.path, query: {location: messageObj.location} });
      }
      if(messageObj.select) {
        this.$router.push({path: this.$route.path, query: {select: messageObj.select} });
      }
    }
