<template>
  <div class="inputwrapper" align="left">
    <v-form
      ref="form1"
      v-model="valid"
      lazy-validation
    >

    <div>
      <h2>{{ $t('select_building') }}</h2>
      <hr class="hr2"/>
      <v-select
              v-model="buildingSelected"
              :items="buildingNames"
              :label="$t('text_all')"
              multiple
              chips
              solo
            ></v-select>
    </div>

    <div v-if='buildingSelected != ""'>
      <h2>{{ $t('select_wings') }}</h2>
      <hr class="hr2"/>
      <v-select
              v-model="wingselected"
              :items="wings"
              :label="$t('text_all')"
              multiple
              chips
              solo
            ></v-select>
    </div>

    <div v-if='wingselected != ""'>
      <h2>{{ $t('select_floors') }}</h2>
      <hr class="hr2"/>
      <v-select
              v-model="floorselected"
              :items="floors"
              :label="$t('text_all')"
              single
              chips
              solo
            ></v-select>
    </div>

      <div v-if='!loading'>
        <h2>{{ $t('select_functions') }}</h2>
        <hr class="hr1"/>
        <v-sheet class="pa-4 primary lighten-2">
          <v-text-field
            v-model="csfsearch"
            label="Search"
            dark
            flat
            solo-inverted
            hide-details
            clearable
            clear-icon="mdi-close-circle-outline"
          ></v-text-field>
          <v-checkbox
            v-model="caseSensitive"
            dark
            hide-details
            label="Case sensitive search"
          ></v-checkbox>
        </v-sheet>
        <v-treeview
          v-model="csfselection"
          :items="csfitems"
          :search="csfsearch"
          :filter="filter"
          :transition="true"
          :open-on-click="true"
          :rounded="true"
          selectable
          return-object
          dense
        >
        <template v-slot:prepend="{ item, open }">
          <v-icon :color="item.color"> {{ 'mdi-folder' }}</v-icon>
        </template>
        </v-treeview>
      </div>


      <v-btn
          color="#3577cb"
          class="white--text"
          @click="setAllFunctionsSelected()"
        >
      Select all
        </v-btn><br><br>

      <v-switch
      v-model="loading"
      label="Split by floor"
      color="blue"
      hide-details
    ></v-switch><br><br>

    </v-form>
  </div>
</template>


<script>

import { mapMutations, mapActions  } from "vuex";
const $rdf = require('rdflib');
import store from '../store';
import router from '../router';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import ur from './util-rdf.js';
import a from './util-a.js';
import r from './util-r.js';
import bw from './BIMviewer.vue';
import bmu from './BIMviewer-utils.js';
import ifcu from './BIMviewer-ifc-utils.js';


import {
  createSolidDataset,
  createThing,
  setThing,
  getThing,
  addUrl,
  addStringNoLocale,
  saveSolidDatasetAt,
  getSolidDataset,
  getThingAll,
  getStringNoLocale,
  getStringNoLocaleAll,
  createContainerAt,
  getPodOwner
} from "@inrupt/solid-client";
import {
  Color,
  ExtrudeGeometry,
  Shape,
  MeshPhongMaterial,
  Mesh,
  LineBasicMaterial,
  MeshBasicMaterial,
  Geometry,
  Vector3,
  PointsMaterial,
  Points,
  Line,
  BufferAttribute,
  LineCurve3,
  BoxGeometry,
  BufferGeometry,
  Float32BufferAttribute,
  LineSegments,
  Box3,
  Face3,
  Triangle,
  LineDashedMaterial,
  DoubleSide,
  MeshLambertMaterial,
  MeshStandardMaterial
} from "three";
require('./tailwind.min.css');

const rm = {}

rm.searchAndGetTriples = async function(searchWord){
  let uri = store.state.webId.replace("/i", "/search/")+encodeURIComponent(searchWord);
  console.log("Trying to search..")
  console.log(uri);
  try {
    let res = await window.solid.session.fetch(
      uri, {
        method: "GET",
        credentials: "include",
        headers: {
          "Accept": "text/turtle"
        },
    }).then(res => res.text());
    $rdf.parse(res, store.state.rs.rdfStore, uri, 'text/turtle');
    return res;
  } catch (error) {
    console.log("Search failed");
    console.log(error);
  }
}

const customCompare = (a, b) => 
  /^\d/.test(a.name) - /^\d/.test(b.name) || 
  a.name.localeCompare(b.name, undefined, { numeric: true });

function colorToSigned24Bit(s) {
  return (parseInt(s.substr(1), 16) << 8) / 256;
}

export default {
  id: rm,
  name: 'IFCexporter',
  data() {
    return {
      rhf: [],
      rhfselected: [],
      rhfselectedURIs: [],
      helseforetak: [],
      hfselected: [],
      hfselectedURIs: [],
      lokselected: [],
      locationNames: [],
      locationURIs: [],
      buildingNames: [],
      buildingURIs: [],
      buildingSelected: [],
      wings: [],
      wingURIs: [],
      wingselected: [],
      floors: [],
      floorURIs: [],
      floorselected: [],
      siteURIs: [],
      siteNames: [],
      siteSelected: [],
      roomsSelected: [],
      selectedRoomURIs: [],
      valid: true,
      loading: false,
      csfselection: [],
      csfsearch: null,
      caseSensitive: false,
      partfunctionsSelected: [],
      colorlist: ['#3cb44b', '#ffe119', '#4363d8', '#f58231', '#911eb4', '#46f0f0', '#f032e6', '#bcf60c', '#fabebe', '#008080', '#e6beff', '#9a6324', '#fffac8', '#800000', '#aaffc3', '#808000', '#ffd8b1', '#000075', '#808080'],
      csfitems: [
        {
          id: 1,
          name: 'Root',
          color: '#FF0000',
          children: [
            { id: 2, name: 'Child #1', color: '#0000FF' },
            { id: 3, name: 'Child #2', color: '#00FF00' },
            { id: 4, name: 'Child #3', color: '#0000FF' },
          ],
        },
      ],
    }
  },
  methods: {
    ...mapMutations('bv', ["RESIZE"]),
    ...mapActions('bv', ["RENDER", "INITIALIZE_PLAYER"]),
    checklogin(){
      console.log(window.solid.session.info);
      if (window.solid.session.info.isLoggedIn){
        console.log("Test logged in");
      } else {
        console.log("Is logged out, reroute");
        this.$router.push({ path: '/' })
      }
    },
    async getHospitalFunctions(){
      let functionsData = await rm.searchAndGetTriples("#NMH.F.SYKEHUS.*");
      this.csfitems = [];
      const mainfunctionTags = [];
      const mainfunctionURIs = [];
      const partfunctionTags = [];
      const partfunctionURIs = [];
      store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
        if(item.object.value.includes("NMH.F.SYKEHUS.")){
          // Check if main function
          let foundMainfunction = false;
          store.state.rs.rdfStore.match($rdf.sym(item.subject.value), ns.RDFS("label"), null).forEach(subitem => {
            if(subitem.object.value.includes("Hovedfunksjon")) foundMainfunction = true;
          });
          if(foundMainfunction){
            console.log("Found: "+ item.object.value);
            mainfunctionTags.push(item.object.value);
            mainfunctionURIs.push(item.subject.value);
          } else {
            partfunctionTags.push(item.object.value);
            partfunctionURIs.push(item.subject.value);
          }
        }
      });
      let idCounter = 0;
      mainfunctionTags.forEach((mainfunctionTag, i) => {
        const partFunctions = [];
        partfunctionTags.forEach((partfunctionTag, j) => {
          if(partfunctionTag.includes(mainfunctionTag+".")){
            const partFunctionName = store.state.rs.rdfStore.match($rdf.sym(partfunctionURIs[j]), ns.RDFS("label"), null)[0].object.value.replace("Delfunksjon: ", "");
            partFunctions.push({id: idCounter, name: partFunctionName, color: this.colorlist[i], tag: partfunctionTag, uri: partfunctionURIs[j] })
            idCounter++;
          }
        });
        partFunctions.sort(customCompare);
        const mainFunctionName = store.state.rs.rdfStore.match($rdf.sym(mainfunctionURIs[i]), ns.RDFS("label"), null)[0].object.value.replace("Hovedfunksjon: ", "");
        this.csfitems.push({id: idCounter, name: mainFunctionName, color: this.colorlist[i], tag: mainfunctionTag, uri: mainfunctionURIs[i], children: partFunctions })
        this.csfitems.sort(customCompare);
        idCounter++;
      });
    },
    setAllFunctionsSelected(){
      if(this.csfselection.length>0){
        this.csfselection = [];
      } else {
        this.csfitems.forEach(item => {
          this.csfselection.push(item);
        });
      }
    },
    getRHFDataFromTrinity(){
        const URIofInterest = store.state.ministryURI;
        if(store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).length != 0){
          console.log("RHF FOUND!, getting data from local storage.");
          store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).forEach(st => {
             console.log("RHF URI" + st.object);
             this.rhfselectedURIs.push(st.object);
          });
          // Getting URI names
          this.rhfselectedURIs.forEach((item, i) => {
            const URITriple = $rdf.st(item, ns.FOAF("name"), null);
            let tripleArray = [];
            tripleArray.push(URITriple);
            this.getURIObject(tripleArray, URITriple, '').then((rhfURIs) => {
              rhfURIs.forEach((item, i) => {
                if(!this.rhf.includes(''+item.object)){
                  this.rhf.push(''+item.object);
                  this.rhfselected.push(''+item.object);
                }
              });
            });
          });
        } else {
          console.log("RHF NOT FOUND!, getting data from TrinPod");
          ur.aLoadURI(URIofInterest+"/i", null, null).then((result) => {
            if(result){
              store.state.rs.rdfStore.match($rdf.sym(URIofInterest+"/i"), ns.ORG("hasSubOrganization"), null).forEach(st => {
                   console.log("RHF URI" + st.object);
                   store.state.rs.rdfStore.add(st);
                   this.rhfselectedURIs.push(st.object);
              });
              // Getting URI names
              this.rhfselectedURIs.forEach((item, i) => {
                const URITriple = $rdf.st(item, ns.FOAF("name"), null);
                let tripleArray = [];
                tripleArray.push(URITriple);
                this.getURIObject(tripleArray, URITriple, '').then((rhfURIs) => {
                  rhfURIs.forEach((item, i) => {
                    if(!this.rhf.includes(''+item.object)){
                      this.rhf.push(''+item.object);
                      this.rhfselected.push(''+item.object);
                    }
                  });
                });
              });
            } else {
              console.log("Ministry Of Health URI not found.");
            }
          });
        }
    },
    async getURIObject(tripleArray, URITriple, URIreqire){ // Must rebuild to handle both subject and predicate as null values
        return new Promise(resolve => {
          let foundURIs = [];
          if(store.state.rs.rdfStore.match(URITriple.subject, URITriple.predicate, null).length != 0){ // This check must be fixed
            console.log("Getting locally.")
            tripleArray = this.getTripleMatches(tripleArray, store.state.rs.rdfStore);
            tripleArray.forEach(item => {
              if(item.object.value.includes(URIreqire)){
                console.log("Found URI" + item);
                foundURIs.push(item);
              }
            });
            resolve(foundURIs);
          } else {
            console.log("Getting TrinPod.")
            ur.aLoadURI(URITriple.subject.value, null, null).then((result) => {
              if(result){
                console.log(result);
                  tripleArray = this.getTripleMatches(tripleArray, store.state.rs.rdfStore);
                  tripleArray.forEach(item => {
                    if(item.object.value.includes(URIreqire)){
                      //console.log("Found URI" + item.object);
                      store.state.rs.rdfStore.add(item);
                      foundURIs.push(item);
                    }
                  });
                  resolve(foundURIs);
              }
            });
          }
        });
      },
      async getURISubject(tripleArray, URITriple, URIreqire){ // Must rebuild to handle both subject and predicate as null values
        return new Promise(resolve => {
          let foundURIs = [];
          if(store.state.rs.rdfStore.match(null, URITriple.predicate, URITriple.object).length != 0){ // This check must be fixed
            console.log("Getting locally.")
            tripleArray = this.getTripleMatches(tripleArray, store.state.rs.rdfStore);
            tripleArray.forEach(item => {
              if(item.subject.value.includes(URIreqire)){
                console.log("Found URI" + item);
                foundURIs.push(item);
              }
            });
            resolve(foundURIs);
          } else {
            console.log("Getting TrinPod.")
            ur.aLoadURI(URITriple.object.value, null, null).then((result) => {
              if(result){
                console.log(result);
                  console.log(tripleArray);
                  tripleArray = this.getTripleMatches(tripleArray, store.state.rs.rdfStore);
                  tripleArray.forEach(item => {
                    if(item.subject.value.includes(URIreqire)){
                      //console.log("Found URI" + item.subject);
                      store.state.rs.rdfStore.add(item);
                      foundURIs.push(item);
                    }
                  });
                  resolve(foundURIs);
              }
            });
          }
        });
      },
      getTripleMatches(tripleArray, tripleGraph){
        let tripleResult = [];
        tripleArray.forEach((item, i) => {
          if(i==0){
            //console.log("S: "+tripleArray[i].subject+" P: "+tripleArray[i].predicate+" O: "+tripleArray[i].object);
            tripleGraph.match(tripleArray[i].subject, tripleArray[i].predicate, tripleArray[i].object).forEach(st => {
              tripleResult.push(st);
            });
          } else {
            tripleGraph.match(tripleArray[i].subject, tripleArray[i].predicate, tripleArray[i].object).forEach(st => {
              if(tripleResult.includes(st)){
                tripleResult = this.arrayRemove(tripleResult, st);
                tripleResult.push(st);
              } else {
                tripleResult.push(st);
              }
            });
          }
        });
        return tripleResult;
      }
  },
  computed: {
    filter () {
        return this.caseSensitive
          ? (item, csfsearch, textKey) => item[textKey].indexOf(csfsearch) > -1
          : undefined
      },
  },
  watch: {
    csfselection: function(val, oldVal){
      this.partfunctionsSelected = [];
      console.log(val);
      // Remove all previous rooms
      store.state.bv.roomGeometry = [];
      const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      for( var i = buildingbox.children.length - 1; i >= 0; i--) { 
        if(!buildingbox.children[i].name.includes("FLOORPLAN")) buildingbox.remove(buildingbox.children[i]);
      }
      // Add new rooms
      val.forEach(roomvalue => {
         this.partfunctionsSelected.push({uri: roomvalue.uri, color: roomvalue.color});
      });
    },
    partfunctionsSelected: function(val, oldVal){
      // Find rooms based on function
      this.roomsSelected = [];
      val.forEach(functionvalue => {
        //store.state.rs.rdfStore.match(null, ns.NEO("b_capable"), $rdf.sym(functionvalue.uri)).forEach(roomItem => { // Must be replaced back when database is not broken
        this.selectedRoomURIs.forEach(roomURI => {
          store.state.rs.rdfStore.match($rdf.sym(roomURI), null, $rdf.sym(functionvalue.uri)).forEach(roomItem => {
            this.roomsSelected.push({uri: roomItem.subject.value, color: functionvalue.color});
          });
        });
      });
    },
    roomsSelected: function(val, oldVal){
      // Add newly selected rooms
      console.log("Found rooms");
      val.forEach(room => {
        console.log("RoomURI: "+room.uri);
        store.state.rs.rdfStore.match($rdf.sym(room.uri), ns.IFOUR("m_polyline"), null).forEach((polyline, i) => {
            bmu.addRoomMatrix(polyline, colorToSigned24Bit(room.color), 1, room.uri);
          });
      });
    },
    rhfselected: function (val) {
      console.log("rhf selected...");
      var A = 0;
      var B = 0;
      var C = 0;
      var D = 0;
      for(let i = 0; i < this.rhfselected.length; i++){
        console.log("Selected RHF: "+ this.rhfselected[i])
        if(this.rhfselected[i] == "Helse Nord RHF"){ A = 1 }
        if(this.rhfselected[i] == "Helse Midt-Norge RHF"){ B = 1 }
        if(this.rhfselected[i] == "Helse Vest RHF"){ C = 1 }
        if(this.rhfselected[i] == "Helse Sr-st RHF"){ D = 1 }
      }

      this.hrimage = "hr"+A+B+C+D+".png"

      this.rhfselectedURIs.forEach((item, i) => {
        const URITriple = $rdf.st(item, ns.ORG("hasSubOrganization"), null);
        let tripleArray = [];
        tripleArray.push(URITriple);
        this.getURIObject(tripleArray, URITriple, '').then((hfURIs) => {
          hfURIs.forEach((item, i) => {
            console.log("Found HF URIs: "+item.object.value);
            this.hfselectedURIs.push(item.object);
            const URITriple = $rdf.st(item.object, ns.FOAF("name"), null);
            let tripleArray = [];
            tripleArray.push(URITriple);
            this.getURIObject(tripleArray, URITriple, '').then((hfNames) => {
              hfNames.forEach((item, i) => {
                if(!this.hfselected.includes(''+item.object.value)){
                  this.helseforetak.push(item.object.value);
                  this.hfselected.push(item.object.value);
                }
              });
            });
          });
        });
      });
    },
    hfselected: function (val) {
      console.log("hf selected...");
      this.hfselectedURIs.forEach((item, i) => {
        const URITriple = $rdf.st(item, ns.ORG("hasSubOrganization"), null);
        let tripleArray = [];
        tripleArray.push(URITriple);
        this.getURIObject(tripleArray, URITriple, '').then((locURIs) => {
          locURIs.forEach((item, i) => {
            console.log("Found Location URIs: "+item.object.value);
            this.locationURIs.push(item.object);
            const URITriple = $rdf.st(item.object, ns.FOAF("name"), null);
            let tripleArray = [];
            tripleArray.push(URITriple);
            this.getURIObject(tripleArray, URITriple, '').then((locNames) => {
              locNames.forEach((item, i) => {
                if(!this.locationNames.includes(''+item.object.value)){
                  this.locationNames.push(item.object.value);
                  this.lokselected.push(item.object.value);               
                }
              });
            });
          });
        });
      });
    },
    lokselected: function (val) {
      console.log("location selected...");
      this.locationURIs.forEach((item, i) => {
        const URITriple = $rdf.st(null, ns.ACL("owner"), item);
        let tripleArray = [];
        tripleArray.push(URITriple);
        this.getURISubject(tripleArray, URITriple, '').then((locURIs) => {
          locURIs.forEach((item, i) => {
            console.log("Found Site URIs: "+item.subject.value);
            this.siteURIs.push(item.subject);
          });
          this.siteURIs.forEach((item, i) => {
            const URITriple = $rdf.st(null, ns.ACL("owner"), item);
            let tripleArray = [];
            tripleArray.push(URITriple);
            this.getURISubject(tripleArray, URITriple, '').then((buildURIs) => {
              buildURIs.forEach((item, i) => {
                console.log("Found Building URIs: "+item.subject.value);
                this.buildingURIs.push(item.subject);
                const URITriple = $rdf.st(item.subject, ns.FOAF("name"), null);
                let tripleArray = [];
                tripleArray.push(URITriple);
                this.getURIObject(tripleArray, URITriple, '').then((buildNames) => {
                  buildNames.forEach((item, i) => {
                    if(!this.buildingNames.includes(''+item.object.value)){
                      this.buildingNames.push(''+item.object.value);
                      this.buildingSelected.push(''+item.object.value);
                    }
                  });
                });
              });
            });
          });
        });
      });
    },
    buildingSelected: async function (val) {
      const buildingSelectedURIs = [];
      this.buildingNames.forEach((item, i) => {
        buildingSelectedURIs.push(this.buildingURIs[i]);
      });

      let storeyfetch = await buildingSelectedURIs.forEach((item, i) => {
        ur.fetchAndSaveTurtle(item.value, true).then((successBool) => {
          return successBool;
        });
      });
      if(storeyfetch){}

      // Get all the storey URIs
      /*let tripleArray = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.BOT("Storey"));
      tripleArray.forEach(item => {
        //if(item.object.value.includes("Storey-")){
          console.log("Found storey URI: "+item.subject.value);
          this.floorURIs.push(item.subject.value);
        //}
      });*/

      let wingSearch = await r.searchAndGetURIs("#"+store.state.currentBuildingTag+".FLOY*");
      if(wingSearch){}
      store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.NEO("s_instance")).forEach(item => {
          console.log("Found wing: " + item.subject.value);
          //if(!this.floorURIs.includes(item.subject.value)){
             this.wingURIs.push(item.subject.value);
          //}
      });

      // Get the storey names
      this.wingURIs.forEach((item, i) => {
        let tripleArray = store.state.rs.rdfStore.match($rdf.sym(item), ns.RDFS("label"), null);
        tripleArray.forEach(item => {
          console.log("Found wing name: "+item.object.value);
          if(!this.wings.includes(decodeURI(item.object.value))){
             this.wings.push(decodeURI(item.object.value));
          }
        });
      });


    },
    wingselected: async function (val){
      let storeySearch = await r.searchAndGetURIs("#"+store.state.currentBuildingTag+"*");
      if(storeySearch){}
      store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.BOT("Storey")).forEach(item => {
          console.log("Found storey: " + item.subject.value);
          //if(!this.floorURIs.includes(item.subject.value)){
             this.floorURIs.push(item.subject.value);
          //}
      });

      // Get the storey names
      this.floorURIs.forEach((item, i) => {
        let tripleArray = store.state.rs.rdfStore.match($rdf.sym(item), ns.RDFS("label"), null);
        tripleArray.forEach(item => {
          console.log("Found storey name: "+item.object.value);
          if(!this.floors.includes(decodeURI(item.object.value))){
             this.floors.push(decodeURI(item.object.value));
          }
        });
      });
    },
    floorselected: async function(val) {
      console.log(this.floorselected);
      store.state.bv.GLTFloadingQueue = [];
      bmu.unloadGeometryUsingTag(store.state.currentBuildingTag, ["BUILDING"]);
      bmu.unloadGeometryContainingString("FLOORPLAN");

      let selectedStoreyURIs = [];
      let roomNames = [];

      // Get the selected storey URIs
      this.floors.forEach((item, i) => {
        if(this.floorselected.includes(item)){
          let tripleArray = store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal(item, 'en'));
          tripleArray.forEach(item => {
            console.log("Found selected Storey URI: "+item.subject.value);
            selectedStoreyURIs.push(item.subject.value);
          });
        } else {
          // Remove geometry for storeys not selected
          let tripleArray = store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal(item, 'en'));
          tripleArray.forEach(storeytriple => {
            store.state.rs.rdfStore.match($rdf.sym(storeytriple.subject.value), ns.TAG("tag"), null).forEach(tagitem => {
            console.log("Storey tag to remove: "+tagitem.object.value);
            bmu.unloadGeometryUsingTag(tagitem.object.value, ["FLOORPLAN"]);
          });
        });
        }
      });

      // Get tags for selected storey URIs
      let storeyTAG = "";
      selectedStoreyURIs.forEach(storeyitem => {
        store.state.rs.rdfStore.match($rdf.sym(storeyitem), ns.TAG("tag"), null).forEach(item => {
          console.log("Storey tag: "+item.object.value);
          storeyTAG = item.object.value;
          storeyTAG = storeyTAG.replace(".FLOY_O", "");
          storeyTAG = storeyTAG.replace(".FLOY_S", "");
          // Show geometry for the tag
          if(store.state.bv.gltfGeoVisible){
            bmu.loadGeometryUsingTag(storeyTAG, this.$route.query.location, ["FLOORPLAN"]);
          }
        });
      });

      console.log("StoreyTAG:"+storeyTAG);
      let roomSearch = await r.searchAndGetURIs("#"+storeyTAG+"*"); 
      if(roomSearch){}
      console.log()
      store.state.rs.rdfStore.match(null, ns.RDFS("label"), null).forEach(item => {
          if(item.object.value.includes("Room")){
            console.log("Found room: " + item.subject.value);
            if(!this.selectedRoomURIs.includes(item.subject.value)){
              this.selectedRoomURIs.push(item.subject.value);
            }
          }
      });
      
      // Fetch data for rooms
      for(let i=0; i<this.selectedRoomURIs.length; i++){
        let fetchrooms = await ur.fetchAndSaveTurtle(this.selectedRoomURIs[i]).then((successBool) => {
          console.log("Finished doing room fetch!");
          return successBool;
        });
        if(fetchrooms){}
      }
      //this.getRoomGeometry(this.selectedRoomURIs);
    },
  },
  mounted() {
    this.checklogin();
    console.log(store.state.webId);
    this.getHospitalFunctions();
    this.getRHFDataFromTrinity();
    //bmu.zoomTwoDimCamera();
  },
  created(){
    store.state.bv.twodim = true;
  }
};
</script>

