<template>
  <div scroll="no" class="wrapper" :style="{ 'height': viewHeight + 'px' }">
    <v-btn
          style="position: absolute; z-index: 3; top: 28px; right: 8px;"
          color="#3577cb"
          class="white--text"
          @click="toggleViews"
        >
        <v-icon>mdi-arrow-split-vertical</v-icon>
    </v-btn>
    <Multipane scroll="no" id="resizer" class="vertical-panes" :style="{ 'height': viewHeight + 'px' }" layout="vertical" @paneResizeStart="paneresizestarted($event)" @paneResizeStop="paneresizestopped($event)">
      <div class="vertical" ref="leftpane" :style="{ 'height': viewHeight + 'px' }" style="overflow: scroll"><AssetsList msg="Show assets list form" /></div>
      <multipane-resizer class="resizer" :style="{ 'height': viewHeight + 'px' }">-</multipane-resizer>
      <div class="verticalflex" :style="{ 'height': viewHeight + 'px', 'z-index': 1 }">
        <div class="overlay1" v-if="moving"></div>
        <BMviewer class="viewer" ref="myviewer" msg="Show asset viewer form" />
      </div>
    </Multipane>
  </div>
</template>

<script>
// @ is an alias to /src
import AssetsList from '../components/AssetsList.vue'
import BMviewer from '../components/BMviewer.vue'
import "vuetify/dist/vuetify.min.css";
import { mapMutations } from "vuex";
import store from '../store';
import { Multipane, MultipaneResizer } from 'vue-multipane';

export default {
  name: 'AssetslistV',
  components: {
    AssetsList,
    BMviewer,
    Multipane,
    MultipaneResizer
  },
  data: function() {
    return {
      bimVisible: true,
      viewHeight: 600,
      moving: false,
    };
  },
  methods: {
    ...mapMutations('bv', ["RESIZE"]),
    updateCanvasSize(){
      const myviewer = this.$refs.myviewer
      console.log(myviewer);
      const height = window.innerHeight-60;
      this.viewHeight = height;
      const width = myviewer.$el.clientWidth+10
      console.log("Width is: "+width);
    },
    toggleViews(){
      const leftpane = this.$refs.leftpane
      console.log(leftpane)
      if(leftpane.clientWidth>0){
        leftpane.style.width = "0vw";
      } else {
        leftpane.style.width = "100vw";
      }
      this.updateCanvasSize();
    },
    windowResize(){
      this.updateCanvasSize();
    },
    paneresizestarted: function(event) {
      console.log("resize start...")
      this.moving = true;
    },
    paneresizestopped: function(event) {
      console.log("resize stop...")
      this.updateCanvasSize();
      window.dispatchEvent(new Event('resize'));
      this.moving = false;
    },
  },
  mounted(){
    this.windowResize();
  },
  created() {
    window.addEventListener("resize", this.windowResize);
  },
  destroyed() {
    window.removeEventListener("resize", this.windowResize);
  }
}
</script>

