<template>
  <div class="inputwrapper" align="left">
    <v-form
      ref="form"
      lazy-validation
    >
    <v-file-input
          v-model="file"
          truncate-length="15"
        ></v-file-input>
        <v-btn
          :to="{name: 'ImportData'}"
          color="#3577cb"
          class="white--text"
        >
        {{ $t('button_back') }}
        </v-btn>
    </v-form>
  </div>
</template>

<script>
import store from '../store';
import router from '../router';
import u from './util.js';
//import { LBDParser } from "ifc-lbd";
const WebIFC = require("web-ifc/web-ifc-api.js");
const ifcApi = new WebIFC.IfcAPI();

export default {
  name: 'ImportIFC',
  data () {
    return {
      file: "",
    };
  },
  props: {
    msg: String
  },
  methods: {
    getCirculationData(modelID, IFCDoors){
      for (var i = 0; i < IFCDoors.length; i++) {
        //console.log(IFCDoors[i]);
        const doorLineID = IFCDoors[i].substring(0, IFCDoors[i].indexOf('='));
        //console.log(doorLineID);
        let doorValues = IFCDoors[i].substring(IFCDoors[i].indexOf('IFCDOOR(')+8);
        doorValues = doorValues.replace(');', '');
        doorValues = doorValues.replaceAll('\'', '');
        console.log(doorValues);
        const doorValuesArray = doorValues.split(',');
        //const doorIFCuID = doorValuesArray[0];
        const doorID = doorValuesArray[2];
        console.log(doorID);
        const IfcLocalPlacement = ifcApi.GetLine(modelID, parseInt(doorValuesArray[5].substring(1)));
        //console.log(IfcLocalPlacement);
        const ifcToAxis2Placement3d = ifcApi.GetLine(modelID, parseInt(IfcLocalPlacement.RelativePlacement.value));
        //console.log(ifcToAxis2Placement3d);
        const location = ifcApi.GetLine(modelID, ifcToAxis2Placement3d.Location.value);
        //console.log(location);
        const doorLevel = location.Coordinates[0].value; // Mest sannsynlig hyde i meter
        console.log(doorLevel);

        const value1 = doorValuesArray[8];
        const value2 = doorValuesArray[9];
        console.log("Hyde: "+value1+" Bredde:"+value2);

        /*const IfcProductDefinitionShape = ifcApi.GetLine(modelID, parseInt(doorValuesArray[6].substring(1)));
        console.log(IfcProductDefinitionShape);
        const IfcShapeRepresentation = ifcApi.GetLine(modelID, parseInt(IfcProductDefinitionShape.Representations[0].value));
        console.log(IfcShapeRepresentation);
        const IfcMappedItem = ifcApi.GetLine(modelID, parseInt(IfcShapeRepresentation.Items[0].value));
        console.log(IfcMappedItem);
        if(IfcMappedItem.MappingSource){
          const IfcRepresentationMap = ifcApi.GetLine(modelID, parseInt(IfcMappedItem.MappingSource.value));
          console.log(IfcRepresentationMap);
          const IfcFacetedBrep = ifcApi.GetLine(modelID, parseInt(IfcRepresentationMap.MappedRepresentation.value));
          console.log(IfcFacetedBrep);
          if(IfcFacetedBrep.Outer){
            const IfcMappedItem2 = ifcApi.GetLine(modelID, parseInt(IfcFacetedBrep.Outer.value));
            console.log(IfcMappedItem2);
          }
        }*/

      }
    },
    getPolyLineData(modelID, ifcSpaceLoc, ifcSpacePoly, ifcSpaceRoomID, lineID, ifcSpaceHeight){
      // Finding location of room box
      const ifcLocalPlacement = ifcApi.GetLine(modelID, parseInt(ifcSpaceLoc[lineID].substring(1)));
      //console.log(ifcLocalPlacement);
      const placementRelTo = ifcApi.GetLine(modelID, parseInt(ifcLocalPlacement.PlacementRelTo));
      //console.log(placementRelTo);
      const ifcToAxis2Placement3d = ifcApi.GetLine(modelID, ifcLocalPlacement.RelativePlacement.value);
      //console.log(ifcToAxis2Placement3d);
      const location = ifcApi.GetLine(modelID, ifcToAxis2Placement3d.Location.value);
      //console.log(location);
      const ifcaxis = ifcApi.GetLine(modelID, ifcToAxis2Placement3d.Axis.value);
      //console.log(ifcaxis);
      const refDirection = ifcApi.GetLine(modelID, ifcToAxis2Placement3d.RefDirection.value);
      //console.log(refDirection);
      //The relation between the following two values will create the rotation
      const directionRatios = refDirection.DirectionRatios;
      const axisDirectionRatios = ifcaxis.DirectionRatios; // Mostly { 0, 0, 1 }
      //console.log(directionRatios);
      const coordinates = location.Coordinates; // Location of the bounding box relative to IFC site zero
      //console.log(coordinates);


      // Finding polyline
      const IfcProductDefinitionShape = ifcApi.GetLine(modelID, parseInt(ifcSpacePoly[lineID].substring(1)));
      //console.log(IfcProductDefinitionShape);
      const IfcShapeRepresentation = ifcApi.GetLine(modelID, parseInt(IfcProductDefinitionShape.Representations[1].value));
      //console.log(IfcShapeRepresentation);
      const IfcGeometricCurveSet = ifcApi.GetLine(modelID, parseInt(IfcShapeRepresentation.Items[0].value));
      //console.log(IfcGeometricCurveSet);
      const IfcPolyline = ifcApi.GetLine(modelID, parseInt(IfcGeometricCurveSet.Elements[0].value));
      //console.log(IfcPolyline);

      //console.log(directionRatios[0].value+","+directionRatios[1].value+","+directionRatios[2].value);
      //console.log(axisDirectionRatios[0].value+","+axisDirectionRatios[1].value+","+axisDirectionRatios[2].value);

      // Functionality to find rotation START ---------------------
      const inRotation = { x:directionRatios[0].value, y:directionRatios[1].value, z:directionRatios[2].value };
      const inAxis = { x: axisDirectionRatios[0].value, y:axisDirectionRatios[1].value, z:axisDirectionRatios[2].value };
      const crossProduct = this.v3cross(inRotation, inAxis);
      const myAxis = { x:0, y:-1, z:0 };

      var angle = this.getAngleRadians(crossProduct.x, crossProduct.y, inRotation.x, inRotation.y);
      var angle2 = this.getAngleRadians(inAxis.x, inAxis.y, myAxis.x, myAxis.y);
      angle = (angle2-angle)+(Math.PI/4);
      // Functionality to find rotation END ---------------------


      const polyCoordinates = [];
      if(IfcPolyline.Points){
        for (var i = 0; i < IfcPolyline.Points.length; i++) {
          const IfcCartesianPoint = ifcApi.GetLine(modelID, parseInt(IfcPolyline.Points[i].value));
          polyCoordinates.push(IfcCartesianPoint.Coordinates);
        }
        //console.log(polyCoordinates);

        // Create string of polyline
        var polyString = "polyline;"+encodeURI(ifcSpaceRoomID[lineID])+";"+coordinates[0].value/1000+";"+coordinates[1].value/1000+";"+ifcSpaceHeight+";2.9;["; // Includes x, y, z and height of room. Must set z and height dynamic

        for (var i = 0; i < polyCoordinates.length; i++) {
          var point1 = (polyCoordinates[i][0].value/1000);
          var point2 = (polyCoordinates[i][1].value/1000);

          const rotatedPoints = this.rotateRadians(0, 0, point1, point2, angle);
          point1 = rotatedPoints[0];
          point2 = rotatedPoints[1];

          polyString = ""+polyString+point1+","+point2+",0"; // Switches points because of current version, may need to change
          if(i<polyCoordinates.length-1){
            polyString = polyString+",";
          }
        }
        polyString = polyString+"]";
        //console.log(polyString);
        return polyString;
      } else {
        return "";
      }

    },
    rotateRadians(cx, cy, x, y, radians) {
        const cos = Math.cos(radians);
        const sin = Math.sin(radians);
        const nx = (cos * (x - cx)) + (sin * (y - cy)) + cx;
        const ny = (cos * (y - cy)) - (sin * (x - cx)) + cy;
        return [nx, ny];
    },
    getAngleRadians(fromX,fromY,toX,toY) {
      let deltaX = fromX - toX;
      let deltaY = fromY - toY;
      let radians = Math.atan2(deltaY, deltaX)
      return radians;
    },
    v3cross(v1, v2){
      let v3 = {x:0,y:0,z:0};
      v3.x = (v1.y*v2.z)-(v1.z*v2.y);
      v3.y = (v1.z*v2.x)-(v1.x*v2.z);
      v3.z = (v1.x*v2.y)-(v1.y*v2.x);
      return v3;
    },
  },
  watch: {
    file: function(file) {
      let reader = new FileReader()
      reader.onload = (event) => {
        var encoder = new TextEncoder("utf-8");
        var resultArray = encoder.encode(event.target.result);
          ifcApi.SetWasmPath("../");
          // initialize the library
          ifcApi.Init().then((result) => {
              const modelID = ifcApi.OpenModel(resultArray);

              // Init LBD Parser and parse BOT
              /*const lbdParser = new LBDParser();
              const botTriples = lbdParser.parseBOTTriples(ifcApi, modelID).then((result) => {
                console.log(result);
                // Close the model, all memory is freed
                //ifcApi.CloseModel(modelID);
              });*/

              const allLines = event.target.result.split(/\r\n|\n/);
              var ifcSpaces = [];
              var ifcRelAggregates = []; // Array for getting relations between storeys and spaces
              var ifcStoreys = [];
              var ifcDoors = [];
              for (var i = 0; i < allLines.length; i++) {
                if(allLines[i].includes("IFCSPACE(")){
                  ifcSpaces.push(allLines[i]);
                }
                if(allLines[i].includes("IFCRELAGGREGATES(")){
                  ifcRelAggregates.push(allLines[i]);
                }
                if(allLines[i].includes("IFCBUILDINGSTOREY(")){
                  ifcStoreys.push(allLines[i]);
                }
                if(allLines[i].includes("IFCDOOR(")){
                  ifcDoors.push(allLines[i]);
                }
              }


              // Funtionality to find relation between storey and rooms.
              var relValuesArray = [];
              for (var i = 0; i < ifcRelAggregates.length; i++) {
                let relValues = ifcRelAggregates[i].substring(ifcRelAggregates[i].indexOf('IFCRELAGGREGATES(')+17);
                relValues = relValues.replace(');', '');
                relValues = relValues.replaceAll('\'', '');
                relValues = relValues.replaceAll('(', '');
                relValues = relValues.replaceAll(')', '');
                relValuesArray.push(relValues.split(','));
              }
              //console.log(relValuesArray);

              // Functionality for getting sub values for storeys
              const storeyLineIDs = [];
              for (var i = 0; i < ifcStoreys.length; i++) {
                //console.log("Storey:");
                //console.log(ifcStoreys[i]);
                const storeyLineID = ifcStoreys[i].substring(0, ifcStoreys[i].indexOf('='));
                let storeyValues = ifcStoreys[i].substring(ifcStoreys[i].indexOf('IFCBUILDINGSTOREY(')+17);
                storeyValues = storeyValues.replace(');', '');
                storeyValues = storeyValues.replaceAll('\'', '');
                const storeyValuesArray = storeyValues.split(',');
                //console.log(storeyValuesArray[5]);
                const IfcLocalPlacement = ifcApi.GetLine(modelID, parseInt(storeyValuesArray[5].replace('#', '')));
                //console.log(IfcLocalPlacement);
                const IfcAxis2Placement3D = ifcApi.GetLine(modelID, parseInt(IfcLocalPlacement.RelativePlacement.value));
                //console.log(IfcAxis2Placement3D);
                const IfcLocation = ifcApi.GetLine(modelID, parseInt(IfcAxis2Placement3D.Location.value));
                //console.log(IfcLocation);
                const heightAboveGround = IfcLocation.Coordinates[2].value/1000;
                //console.log("Storey: "+storeyLineID+" HeightAboveGround: "+heightAboveGround);
                for (var j = 0; j < relValuesArray.length; j++) {
                  //console.log(relValuesArray[j][4]+":"+storeyLineID);
                  for (var k = 5; k < relValuesArray[j].length; k++) {
                    if(relValuesArray[j][4] == storeyLineID){
                      storeyLineIDs.push({ roomid:relValuesArray[j][k], height:heightAboveGround, id:storeyLineID });
                    }
                  }
                }
              }
              //console.log(storeyLineIDs);


              // Functionality for getting sub values for spaces
              var ifcSpaceGUID = [];
              var ifcSpaceHeight = [];
              var ifcSpaceRoomID = [];
              var ifcSpaceLoc = []; // 5
              var ifcSpacePoly = []; // 6
              var ifcSpaceNames = [];
              for (var i = 0; i < ifcSpaces.length; i++) {
                const spaceLineID = ifcSpaces[i].substring(0, ifcSpaces[i].indexOf('='));
                //console.log(spaceLineID);
                let spaceValues = ifcSpaces[i].substring(ifcSpaces[i].indexOf('IFCSPACE(')+9);
                spaceValues = spaceValues.replace(');', '');
                spaceValues = spaceValues.replaceAll('\'', '');
                const spaceValuesArray = spaceValues.split(',');
                if(spaceValuesArray[2] != '$'){
                  for (var j = 0; j < storeyLineIDs.length; j++) { // Function for setting roomheight from IFCSPACE relation
                    if(storeyLineIDs[j].roomid == spaceLineID){
                      ifcSpaceHeight.push(storeyLineIDs[j].height);
                    }
                  }
                  ifcSpaceGUID.push(spaceValuesArray[0]);
                  //ifcSpaceRoomID.push(spaceValuesArray[2].replace('\\X2\\00D8\\X0\\', '')); // Replaces shit with 
                  ifcSpaceRoomID.push(u.formatString(spaceValuesArray[2]));
                  ifcSpaceLoc.push(spaceValuesArray[5]);
                  ifcSpacePoly.push(spaceValuesArray[6]);
                  ifcSpaceNames.push(spaceValuesArray[7]);
                }
              }
              //console.log(ifcSpaceRoomID);

              // Write polylines to file
              const tempArray = [];
              let polystring = "";
              for (var i = 0; i < ifcSpaceRoomID.length; i++) {
                let newPolyLine = this.getPolyLineData(modelID, ifcSpaceLoc, ifcSpacePoly, ifcSpaceRoomID, i, ifcSpaceHeight[i]);
                if(newPolyLine != ""){
                  tempArray.push(newPolyLine);
                  polystring = polystring+newPolyLine+"\n";
                }
              }
              store.state.rs.geometryArray = tempArray;

              this.getCirculationData(modelID, ifcDoors);

              // Save to file download
              var blob = new Blob([ polystring ], { "type" : "text/plain" });
              let link = document.createElement('a');
              link.href = window.URL.createObjectURL(blob);
              link.download = 'geometry-file.csv';
              //link.click();
          });
      }
      reader.readAsText(file);
    },
  }
}
</script>

