bmu.getMeshHeightAboveGround = function(mesh){
  let minHeightAboveGround = 1000;
  let maxHeightAboveGround = -1000;
  //console.log(mesh);
  try{
    if(mesh.geometry){
      for(let y = 1; y<=mesh.geometry.attributes.position.array.length; y=y+3){
        minHeightAboveGround = Math.min(mesh.geometry.attributes.position.array[y], minHeightAboveGround);
        maxHeightAboveGround = Math.max(mesh.geometry.attributes.position.array[y], maxHeightAboveGround);
      }
    }
    if(mesh.children[0].geometry){
      for(let y = 1; y<=mesh.children[0].geometry.attributes.position.array.length; y=y+3){
        minHeightAboveGround = Math.min(mesh.children[0].geometry.attributes.position.array[y], minHeightAboveGround);
        maxHeightAboveGround = Math.max(mesh.children[0].geometry.attributes.position.array[y], maxHeightAboveGround);
      }
    }
    if(mesh.children[0].children[0].geometry){
      for(let y = 1; y<=mesh.children[0].children[0].geometry.attributes.position.array.length; y=y+3){
        minHeightAboveGround = Math.min(mesh.children[0].children[0].geometry.attributes.position.array[y], minHeightAboveGround);
        maxHeightAboveGround = Math.max(mesh.children[0].children[0].geometry.attributes.position.array[y], maxHeightAboveGround);
      }
    }
  } catch(error){
    //console.log(error);
  }
  return {"min": minHeightAboveGround, "max": maxHeightAboveGround};
}

bmu.checkIfMeshCrossStoreyLevel = function(level, min, max){
  if(level==min){
    return true;
  } else {
    if((min-0.5<level && max>level+2) && max+0.1>level){
      return true;
    } else {
      return false;
    }
  }
}

bmu.downloadGLTFfromTrinpod = async function(GLTFurl, mTag){
  try {
    const res = await window.solid.session.fetch(
      GLTFurl, {
        method: "GET",
        credentials: "include",
        });
        console.log(res);
      let filedata = await res.arrayBuffer();
      let fileurl = URL.createObjectURL(new Blob([filedata], { type: "application/gltf"}));
      console.log("Flag: "+store.state.bv.GLTFloadingFlag);
      if(!store.state.bv.GLTFloadingFlag){ // Check if something already loading
        bmu.loadGLTF(fileurl, mTag);
        store.state.bv.GLTFloadingQueue.push(fileurl);
      } else {
        console.log("Something is already loading, need queue. Putting in queue");
        store.state.bv.GLTFloadingQueue.push(fileurl);
      }
  } catch (error) {
    console.log(error);
  }
}

bmu.downloadImagefromTrinpod = async function(imageurl){
  try {
    const res = await window.solid.session.fetch(
      imageurl, {
        method: "GET",
        credentials: "include",
        });
        console.log(res);
      let filedata = await res.arrayBuffer();
      var urlCreator = window.URL || window.webkitURL;
      let fileurl = urlCreator.createObjectURL(new Blob([filedata], { type: "image/jpeg"}));
      return fileurl;
  } catch (error) {
    console.log(error);
  }
}

bmu.addRoomMatrix = async function(polyline, color, opacity, roomuri){
  //Parse room matrix
  var obb = polyline+"";
  obb = obb.replace('^^neo:a_matrix-3m', '');
  obb = obb.replaceAll('"', '');
  const obbArray = obb.split(";");
  let obbString = '{"x":'+obbArray[2]+', "y":'+obbArray[3]+', "z":'+obbArray[4]+', "height":'+obbArray[5]+', "color":'+color+', "opacity":'+opacity+', "roomuri":"'+roomuri+'", "polyline":"'+obbArray[6]+'"}';
  let obbJson = JSON.parse(obbString);
  let found = false;
  store.state.bv.roomGeometry.forEach(object => {
    if(!object.roomuri==roomuri) found = true;
  });
  if(!found) store.state.bv.roomGeometry.push(obbJson);
}

bmu.loadGLTF = async function(fileurl, mTag, load=true, scale=0){
  return new Promise(async (resolve,reject)=>{
    const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
    const loader = new GLTFLoader();
    console.log("Starting to load "+mTag+" gltf..")
    store.state.bv.GLTFloadingFlag = true;
    loader.load(
      fileurl, //"./gltf/NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG.gltf",
      function ( gltf ) {
        const model = gltf.scene;
        if(scale==0){
          if(store.state.bv.mapMode) model.scale.set(store.state.bv.scaleFactor, store.state.bv.scaleFactor, store.state.bv.scaleFactor);
        } else {
          model.scale.set(scale, scale, scale);
        }
        model.name = mTag;
        if(load) buildingbox.add( model );
        resolve(model);
      },
      function ( progress ) {
        let progressPercent = ( progress.loaded / progress.total * 100 );
        console.log(progressPercent+'% loaded' );
        if(progressPercent==100){ 
          console.log("GLTF loading finished!");
          setTimeout(function() { window.requestAnimationFrame(() => { store.state.bv.needsrender = true; }); }, 300);
          store.state.bv.GLTFloadingFlag = false;
          // Check the loading queue
          store.state.bv.GLTFloadingQueue = a.removeFirstElement(store.state.bv.GLTFloadingQueue);
          console.log(store.state.bv.GLTFloadingQueue.length);
          if(store.state.bv.GLTFloadingQueue.length>=1){
            console.log("Something found in queue");
            bmu.loadGLTF(store.state.bv.GLTFloadingQueue[0], mTag);
          }
        }
      },
      function ( error ) {
        console.log( 'An error happened: '+error );
        store.state.bv.GLTFloadingFlag = false;
        // Check the loading queue
        store.state.bv.GLTFloadingQueue = a.removeFirstElement(store.state.bv.GLTFloadingQueue);
        if(store.state.bv.GLTFloadingQueue.length>=1){
          bmu.loadGLTF(store.state.bv.GLTFloadingQueue[0], mTag);
        }
      }
    );
  });
}

bmu.loadGeometryUsingTag = function(mTag, currentURL, ifcTypes){
  ifcTypes.forEach(ifctype => {
    let currentTag = mTag+"."+ifctype;
    if(!store.state.bv.gltfLoadedList.includes(currentTag)){
      const GLTFurl = currentURL+"home/GLTF/"+currentTag+".gltf";
      bmu.downloadGLTFfromTrinpod(GLTFurl, currentTag);
      store.state.bv.gltfLoadedList.push(currentTag);
    }
  });
}

bmu.unloadGeometryUsingTag = function(mTag, ifcTypes){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  ifcTypes.forEach(ifctype => {
    let currentTag = mTag+"."+ifctype;
    if(store.state.bv.gltfLoadedList.includes(currentTag)){
      try{
        let meshToRemove = store.state.bv.scene.getObjectByName(currentTag);
        buildingbox.remove(meshToRemove);
        store.state.bv.gltfLoadedList = a.removeValueFromArray(store.state.bv.gltfLoadedList, currentTag);
        //store.state.bv.renderer.render(store.state.bv.scene, store.state.bv.camera);
      } catch(error){
        console.log(error);
      }
    }
  });
}

bmu.unloadGeometryContainingString = function(contString){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  buildingbox.children.forEach(subMesh => {
    if(subMesh.name.includes(contString)) buildingbox.remove(subMesh);
  });
}

bmu.checkIfPointInsideMesh = function(point, mesh){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  const direction = new Vector3(1, 1, 1);
  store.state.bv.raycaster.set(point.applyMatrix4(buildingbox.matrixWorld), direction);
  let intersects = store.state.bv.raycaster.intersectObject(mesh, true);
  try{
    if (intersects.length && direction.dot(intersects[0].face.normal) > 0) {
      //console.log(`Point is in object`);
      return true;
    } else {
      //console.log(`Point is out of object`);
      return false;
    }
  } catch(error){}
}

bmu.raycastRoomsFromPoint = function(point, direction){
  store.state.bv.raycaster.set(point, direction);
  let intersects = store.state.bv.raycaster.intersectObject(mesh, true);
  const intersectNames = [];
  intersects.forEach(intersect => {
    if(!intersectNames.includes(intersect.name)) intersectNames.push(intersect.name);
  });
  return intersectNames;
}

bmu.raycastUsingVector = function(point, direction){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  buildingbox.updateMatrixWorld();
  store.state.bv.raycaster.set(point.applyMatrix4(buildingbox.matrixWorld), direction.applyMatrix4(buildingbox.matrixWorld));
  let intersects = store.state.bv.raycaster.intersectObjects( buildingbox.children, true );
  const buildingMatrix = buildingbox.matrixWorld.invert();
  for(let i=0; i<intersects.length-1; i++){
    intersects[i].point.applyMatrix4(buildingMatrix);
  }
  return intersects;
}

bmu.raycastUsingVectorDir = function(point, direction){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  buildingbox.updateMatrixWorld();
  store.state.bv.raycaster.set(point.applyMatrix4(buildingbox.matrixWorld), direction); // Do not change matrix on direction.
  let intersects = store.state.bv.raycaster.intersectObjects( buildingbox.children, true );
  const buildingMatrix = buildingbox.matrixWorld.invert();
  for(let i=0; i<intersects.length-1; i++){
    intersects[i].point.applyMatrix4(buildingMatrix);
  }
  return intersects;
}

bmu.zoomTwoDimCamera = function() {
  let start, previousTimestamp;
  let cameraHeight = 300;
  let oldCameraHeight = 300;
  let newCameraHeight = 50;
  store.state.bv.animate = true;
  function step(timestamp){
    if(start === undefined){
      start = timestamp;
    }
    const elapsed = timestamp-start;
    if(previousTimestamp !== timestamp){
      cameraHeight = (cameraHeight-((oldCameraHeight-newCameraHeight)/150));
      store.state.bv.twodimcamera.position.set(store.state.bv.twodimcamera.position.x, cameraHeight, store.state.bv.twodimcamera.position.z);
      store.state.bv.controls2d.update();
    }
    if(counter <= 150){
      previousTimestamp = timestamp;
      counter = counter+1;
      window.requestAnimationFrame(step);
    } else {
      if(!store.state.bv.autorotate){
        store.state.bv.animate = false;
      }
    }
  }
  window.requestAnimationFrame(step);
}

bmu.moveToObject = function() {
  var target = new Vector3();
  var size = new Vector3();
  var center = new Vector3();
  let oldZoom = store.state.bv.twodimcamera.zoom;
  let currentZoom = oldZoom;
  var boundingBox = new Box3().setFromObject( store.state.bv.INTERSECTED );
  store.state.bv.animate = true;
  size = boundingBox.getSize(size);
  center = boundingBox.getCenter( center );
  console.log("SizeX:"+size.x+" SizeY:"+size.y);
  if(size.x >= size.y){ // Find longest side of box
    store.state.bv.camerafocuswidth = size.x;
  } else {
    store.state.bv.camerafocuswidth = size.y;
  }
  let start, previousTimestamp;
  var animateDown = false;
  //this.CONVERT_TO_SPHERICAL({ lat: store.state.bv.focuslat, long: store.state.bv.focuslong }).then(u => {
  store.state.bv.INTERSECTED.getWorldPosition( target );
  var newCameraPosX = center.x + (15+store.state.bv.camerafocuswidth*0.5);
  var newCameraPosZ = center.y + (15+store.state.bv.camerafocuswidth*0.5);
  var newCameraPosY = center.z + (15+store.state.bv.camerafocuswidth*0.5);

  var newTargetPosX = center.x;
  var newTargetPosY = center.y;
  var newTargetPosZ = center.z;

  var oldCameraPosX = store.state.bv.camera.position.x;
  var oldCameraPosY = store.state.bv.camera.position.z;
  var oldCameraPosZ = store.state.bv.camera.position.y;

  var oldTargetPosX = store.state.bv.controls3d.target.x;
  var oldTargetPosY = store.state.bv.controls3d.target.y;
  var oldTargetPosZ = store.state.bv.controls3d.target.z;

  var counter = 2;

  var currentCameraPosX = oldCameraPosX;
  var currentCameraPosY = oldCameraPosY;
  var currentCameraPosZ = oldCameraPosZ;
  var currentTargetPosX = oldTargetPosX;
  var currentTargetPosY = oldTargetPosY;
  var currentTargetPosZ = oldTargetPosZ;

  function step(timestamp){
    if(start === undefined){
      start = timestamp;
    }
    const elapsed = timestamp-start;
    if(previousTimestamp !== timestamp){
      currentCameraPosX = (currentCameraPosX-((oldCameraPosX-newCameraPosX)/15));
      currentCameraPosY = (currentCameraPosY-((oldCameraPosY-newCameraPosY)/15));
      currentCameraPosZ = (currentCameraPosZ-((oldCameraPosZ-newCameraPosZ)/15));
      currentTargetPosX = (currentTargetPosX-((oldTargetPosX-newTargetPosX)/15));
      currentTargetPosY = (currentTargetPosY-((oldTargetPosY-newTargetPosY)/15));
      currentTargetPosZ = (currentTargetPosZ-((oldTargetPosZ-newTargetPosZ)/15));
      currentZoom = (currentZoom-((oldZoom-1)/15));
      store.state.bv.camera.position.set(currentCameraPosX, currentCameraPosZ, currentCameraPosY);
      store.state.bv.controls3d.target.set(currentTargetPosX, currentTargetPosZ, currentTargetPosY);
      store.state.bv.twodimcamera.zoom = currentZoom;
      store.state.bv.twodimcamera.position.set(0, 300, 0);
      store.state.bv.twodimcamera.aspect = store.state.bv.width/store.state.bv.height;
      store.state.bv.twodimcamera.updateProjectionMatrix();
      store.state.bv.controls2d.target.set(0, 0, 0);
      store.state.bv.controls2d.update();


    }
    if(counter <= 15){
      previousTimestamp = timestamp;
      counter = counter+1;
      window.requestAnimationFrame(step);
    } else {
      if(!store.state.bv.autorotate){
        store.state.bv.animate = false;
      }
    }
  }
  window.requestAnimationFrame(step);
  //});
  console.log("Zoom to object");
}

bmu.createBuildingLocationList = async function(buildingURIs){
  console.log("BuildingURIs");
  console.log(buildingURIs);
  store.state.cp.buildingLocationList = [];
  store.state.bv.scene.children.forEach(object => {
    if(object.name=="buildinglabel") store.state.bv.scene.remove(object);
  });
  for(let i=0; i<buildingURIs.length; i++){
    try{
      let bURI = buildingURIs[i];
      console.log("bURI")
      console.log(bURI);
      const jsonURI = bURI.replace("/i", "/home/IFC/building.json");
      const IFCmetadata = await ifcu.getIFCmetaDataFromJson(jsonURI); // Create function for storing in local storage
      const position2d = UnitsUtils.datumsToSpherical(IFCmetadata.buildinglat, IFCmetadata.buildinglong);
      const position = new Vector3( -store.state.bv.buildingboxPosition.x+position2d.x, 0, store.state.bv.buildingboxPosition.z-position2d.y);
      let addFlag = true;
      if(IFCmetadata.uri+""=="") addFlag=false;
      store.state.cp.buildingLocationList.forEach(label => {
        if(label.uri==buildingURIs[i]) addFlag=false;
      });
      console.log(buildingURIs[i]+"Add flag: "+addFlag)
      let buildingName = "";
      if(store.state.rs.rdfStore.match($rdf.sym(buildingURIs[i]), ns.FOAF("name"), null)[0]) buildingName = store.state.rs.rdfStore.match($rdf.sym(buildingURIs[i]), ns.FOAF("name"), null)[0].object.value;
      if(addFlag) store.state.cp.buildingLocationList.push({ name: buildingName, uri: buildingURIs[i], buildinglat: IFCmetadata.buildinglat, buildinglong: IFCmetadata.buildinglong, position: position, cluster: i, rotation: IFCmetadata.rotation, buildingdimensions: IFCmetadata.buildingdimensions });
      if(addFlag) bmu.createBuildingLabelHTML(buildingName, position, "", buildingURIs[i]);
    } catch(error){
      console.log("Error: "+error);
    }
  }
  console.log("Building list")
  console.log(store.state.cp.buildingLocationList)
}

bmu.createBuildingLabelHTML = async function(title, location, descript, buildinguri){
  const base = document.createElement( 'div' );
  base.classList.add('building-base-label');
  base.uri = buildinguri;

  const postit = document.createElement( 'div' );
  postit.classList.add('building-label');
  base.appendChild(postit);

  const spacer = document.createElement( 'div' );
  spacer.setAttribute('style', "display: flex;");
  postit.appendChild(spacer);

  const numberButton = document.createElement( 'button' );
  numberButton.classList.add('number-button');
  numberButton.textContent = "1";
  numberButton.classList.add('hidden');
  base.appendChild(numberButton);

  const imgFile = require('../assets/building3.png');
  const labelImage = document.createElement( 'img' );
  labelImage.setAttribute('style', "width: 35px; margin-right: 0px; margin-top: 0px");
  labelImage.setAttribute('src', imgFile);
  spacer.appendChild(labelImage);

  const h4 = document.createElement( 'h4' );
  h4.classList.add('hidden');
  h4.setAttribute('style', "margin-left: 5px; margin-top: 4px");
  h4.textContent = title;
  spacer.appendChild(h4);

  //postit.appendChild(document.createElement( 'br' ));

  const labelContainer = document.createElement( 'div' );
  labelContainer.classList.add('hidden');
  labelContainer.classList.add('texttop');
  postit.appendChild(labelContainer);
  
  const labelDescription = document.createElement( 'span' );
  labelDescription.classList.add('texttop');
  labelDescription.textContent = descript;
  labelContainer.appendChild(labelDescription);

  const viewerButton = document.createElement( 'button' );
  viewerButton.classList.add('details-button');
  viewerButton.classList.add('hidden');
  viewerButton.textContent = "Asset view";
  labelContainer.appendChild(viewerButton);

  const detailsButton = document.createElement( 'button' );
  detailsButton.classList.add('details-button');
  detailsButton.classList.add('hidden');
  detailsButton.textContent = "Details";
  labelContainer.appendChild(detailsButton);

  const zoomButton = document.createElement( 'button' );
  zoomButton.classList.add('details-button');
  zoomButton.classList.add('hidden');
  zoomButton.textContent = "Zoom";
  labelContainer.appendChild(zoomButton);

  base.onmouseenter = () => { 
    h4.classList.remove('hidden');
    labelContainer.classList.remove('hidden');
    detailsButton.classList.remove('hidden');
    viewerButton.classList.remove('hidden');
    zoomButton.classList.remove('hidden');
  }
  base.onmouseleave = () => {
    h4.classList.add('hidden');
    labelContainer.classList.add('hidden');
    detailsButton.classList.add('hidden');
    viewerButton.classList.add('hidden');
    zoomButton.classList.add('hidden');
  }

  detailsButton.onclick = () => {
    console.log("Button clicked.")
    store.state.cp.buildingDetailURI = buildinguri;
  }

  viewerButton.onclick = () => {
    if(title.includes("111")){ 
      store.state.redirect = {"path": "BMViewer", "query": {"select": "moir.scs", "location": store.state.location} };
    }
    if(title.includes("311")){ 
      store.state.redirect = {"path": "BMViewer", "query": {"select": "sand.scs", "location": ""} };
    }
  }

  zoomButton.onclick = () => {
    store.state.bv.INTERSECTED = store.state.bv.scene.getObjectByName("buildingbox");
    bmu.moveToObject();
  }

  const labelObject = new CSS2DObject( base );
  labelObject.position.copy(location);
  labelObject.name = "buildinglabel";
  store.state.bv.scene.add(labelObject);
}

