bmu.polylineToShape = function(polyline){ // Not used at the moment
  const polyArray = bmu.convertPolylineToPolyarray(polyline);
  const polyShape = new Shape();
  var i = 0;
  while (polyArray.length > 1) {
    const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
    const x = (polyXYZ[0]);
    const y = (polyXYZ[1]);
    if(i == 0){
      polyShape.moveTo( x, y );
      i = i+1;
    } else {
      polyShape.lineTo( x, y );
    }
  }
  polyShape.autoClose = true;
  //var geometry = new ShapeBufferGeometry(polyShape);
  return polyShape;
}

bmu.convertPolylineToPolyarray = function(polyline) {
  var obb = polyline+"";
  obb = obb.replace('^^neo:a_matrix-3m', '');
  obb = obb.replaceAll('"', '');
  const obbArray = obb.split(";");
  polyline = obbArray[6];
  let polyArray = [];
  if(polyline != ""){
    polyline = polyline.replace("^^neo:a_matrix-3xm", "");
    polyline = polyline.replace("[", "");
    polyline = polyline.replace("]", "");
    polyArray = polyline.split(",");
  }
  return polyArray;
}

bmu.convertPolylineToXY = function(polyline) {
  var obb = polyline+"";
  obb = obb.replace('^^neo:a_matrix-3m', '');
  obb = obb.replaceAll('"', '');
  const obbArray = obb.split(";");
  const returnPos = {"x": Number(obbArray[2]), "y": Number(obbArray[3])};
  return returnPos;
}

bmu.convertPolyStringToarray = function(polyline) {
  var polyline = polyline+"";
  let polyArray = [];
  if(polyline != ""){
    polyline = polyline.replaceAll("[", "");
    polyline = polyline.replaceAll("]", "");
    polyline = polyline.replaceAll('"', '');
    //console.log("Polyline: "+polyline);
    polyArray = polyline.split(",");
  }
  return polyArray;
}

bmu.convertPolylineToPolyJson = function(polyline){
  var obb = polyline+"";
  obb = obb.replace('^^neo:a_matrix-3m', '');
  obb = obb.replaceAll('"', '');
  const obbArray = obb.split(";");
  let obbString = '{"roomname": "'+obbArray[1]+'", "x":'+obbArray[2]+', "y":'+obbArray[3]+', "z":'+obbArray[4]+', "height":'+obbArray[5]+', "color":'+0x1B4F72+', "opacity":'+1+', "roomuri":"'+""+'", "polyline":"'+obbArray[6]+'"}';
  return JSON.parse(obbString);
}

bmu.trinagulateAndCalculateArea = function(polyline){
  const polyArray = bmu.convertPolylineToPolyarray(polyline);
  // Create points from polyline
  var aPoints = [];
  if(polyArray.length >= 10){
    while (polyArray.length > 1) {
      const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
      const x = (polyXYZ[0]);
      const y = (polyXYZ[1]);
      var arr = [x, y]; // Will be mirrored without geoL-y
      aPoints.push(arr);
    }
  }

  // Start triangulate
  var poly2tri = require('poly2tri');
  var shape = [];
  for(var i=0; i<aPoints.length-1; ++i) {
    shape.push(new poly2tri.Point(aPoints[i][0], aPoints[i][1]));
  }

  let totalArea = 0;
  try{ 
    var swctx = new poly2tri.SweepContext(shape);
    var triangles = swctx.triangulate();

    // Calculate area of triangles
    for(var i=0; i<triangles.triangles_.length; ++i) {
      let v1 = new Vector3(triangles.triangles_[i].points_[0].x, triangles.triangles_[i].points_[0].y, 0);
      let v2 = new Vector3(triangles.triangles_[i].points_[1].x, triangles.triangles_[i].points_[1].y, 0);  
      let v3 = new Vector3(triangles.triangles_[i].points_[2].x, triangles.triangles_[i].points_[2].y, 0);  
      const currentTriangle = new Triangle(v1, v2, v3);
      totalArea = totalArea+currentTriangle.getArea();
    }
  } catch (error){}
  console.log("Total area: "+totalArea);
  
  return totalArea;
}

bmu.createPlaceMarker = function(pos, color=0xff0000, removePrevious, height=5, markername="placemarker", width=0.16){
    const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
    const previousPlacemarker = store.state.bv.scene.getObjectByName("placemarker");
    if(previousPlacemarker && removePrevious){
      buildingbox.remove(previousPlacemarker);
    }
    const material = new MeshLambertMaterial({
        transparent: true,
        opacity: 0.6,
        color: color,
        depthTest: false
    });
    const geometry = new BoxGeometry(width, height, width);
    geometry.translate(pos.x,pos.y+(height/2)+0.2,pos.z);
    const marker = new Mesh(geometry, material);
    marker.name = markername;
    buildingbox.add(marker);
    return marker;
}

