bmu.placeArrow = async function(origin, dir, sf){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  dir.normalize();  //normalize the direction vector (convert to vector of length 1)
  if(!store.state.bv.arrowModelStart){
    store.state.bv.arrowModel = await bmu.loadGLTF("../arrow.glb", "arrow", false, 0.02*sf);
    store.state.bv.arrowModelStart = true;
  } 
  if(store.state.bv.arrowModel){
    let model = store.state.bv.arrowModel.clone();
    console.log(model)
    model.translateX(0.2)
    model.translateY(0.3)
    model.translateX(origin.x);
    model.translateZ(origin.z);
    const angle = bmu.angleThreeVectors(new Vector3(1,0,0), new Vector3(0,0,0), dir) * (dir.z > 0 ? -1 : 1);
    console.log("Angle: "+angle);
    model.rotateOnAxis(new Vector3(0,1,0), angle);
    model.rotateOnAxis(new Vector3(0,1,0), -Math.PI/2);
    model.translateY(origin.y); // Sets height above ground
    //model.position = origin;
    buildingbox.add( model );
  } else {
    setTimeout(function() { 
      let model = store.state.bv.arrowModel.clone();
      model.translateX(origin.x-0.2);
      model.translateY(origin.z+0.2);
      let calculatedDirection = new Vector3(0,1,0).angleTo(dir);
      model.rotateOnAxis(new Vector3(0,1,0), calculatedDirection);
      //model.position = origin;
      buildingbox.add( model ); 
    }, 5000);
  }
  
}

