bmu.createPointsFromPolyArray = function(polyArray, polyPos){
  const pointsArray = [];
  while (polyArray.length > 1) {
    //console.log(polyPos)
    const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
    pointsArray.push(new Vector3(parseFloat(polyXYZ[1])+polyPos.x, polyPos.z, polyXYZ[0]-polyPos.y));
  }
  return pointsArray;
}

bmu.draw2dspace = function(myPolyArray, polyPos, color=0xbbbbdd, name=""){
  const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
  const material = new MeshLambertMaterial({color: color, transparent:true, opacity:0.3});
  let polyShape = new Shape();
  let polyArray = [];
  myPolyArray.forEach(point => {polyArray.push(point)});
  //console.log(polyArray)
  let i = 0;
  while (polyArray.length > 1) {
    const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
    const x = (polyXYZ[0]);
    const y = (polyXYZ[1]);
    if(i == 0){
      polyShape.moveTo( (-x+polyPos.y), (-y-polyPos.x), polyPos.z );
      i = i+1;
    } else {
      polyShape.lineTo( (-x+polyPos.y), (-y-polyPos.x), polyPos.z );
    }
  }
  polyShape.autoClose = true;
  const extrudeSettings = { extrudePath: new LineCurve3( new Vector3( 0, polyPos.z, 0 ), new Vector3( 0, polyPos.z+0.5, 0 ) ) };
  const polyGeometry = new ExtrudeGeometry( polyShape, extrudeSettings );
  const polyMesh = new Mesh( polyGeometry, material ) ;
  polyMesh.name = name;
  buildingbox.add(polyMesh);
}

bmu.angleThreeVectors = function( vec1, vecAngle, vec2) {
  let angle = 0;

	if ( vec1.x === 0 && vec1.y === 0 && vec1.z === 0 ) return 0;
	if ( vec2.x === 0 && vec2.y === 0 && vec2.z === 0 ) return 0;

	vec1.sub( vecAngle );
	vec2.sub( vecAngle );

	angle = vec1.angleTo( vec2 );

	vec1.add( vecAngle );
	vec2.add( vecAngle );

	return angle ;
}

bmu.getDoorDirectionVector = function(polyLine, doorLocation, debugF=false){
  console.log("Getting door direction vector");
  if(debugF) bmu.createPlaceMarker(doorLocation, 0x00ff00);
  const polyJson = bmu.convertPolylineToPolyJson(polyLine);
  const polyPositions = { x: polyJson.x, y: polyJson.y, z: polyJson.z, height: polyJson.height, roomuri: polyJson.roomuri };
  const polyArray = bmu.convertPolyStringToarray(polyJson.polyline);
  if(debugF) bmu.draw2dspace(polyArray, polyPositions, 0x9999AA, "Name");

  const pointsArray = bmu.createPointsFromPolyArray(polyArray, polyPositions);
  const lineVectors = [];
  let angleSum = 0; // If anglesum is positive, right hand side is pointing out of space
  //console.log(pointsArray)
  for(let ap=0; ap<pointsArray.length; ap++){
    if(debugF) bmu.createPlaceMarker(pointsArray[ap]);
    if(ap!=pointsArray.length-1){
      lineVectors.push(v.v3subtract(pointsArray[ap+1], pointsArray[ap]));
    } else {
      lineVectors.push(v.v3subtract(pointsArray[ap], pointsArray[0])); // Only needed if first and last point is not the same.
    }
  }
  // Calculating way of drawing room - Clockwise or anticlockwise
  for(let ap=0; ap<pointsArray.length; ap++){
    let v1 = lineVectors[pointsArray.length-2].clone();
    if(ap!=0) v1 = lineVectors[ap-1].clone();
    const v2 = lineVectors[ap].clone();
    //console.log(v1);
    //console.log(v2);
    const angle = bmu.angleThreeVectors(v1, new Vector3(0,0,0), v2);
    //console.log("Angle: "+angle);
    angleSum = angleSum+angle;
  }
  if(debugF) console.log("Angle sum: "+angleSum);
  let rotateClockwise = true;
  if(angleSum>=0){
    if(debugF) console.log("Angle sum is positive. Use right hand side for out.");
    rotateClockwise = false
  } else {
    if(debugF) console.log("Angle sum is negative. Use left hand side for out.");
    rotateClockwise = true;
  }
  // Setting directions
  let rotateDirection = new Vector3( 0, -1, 0 );
  let directionBefore = new Vector3( 0, 0, 0 );
  let closestVector = new Vector3( 0, 0, 0 );
  let foundLineCenter = new Vector3( 0, 0, 0 );
  let closestDistance = 1000;
  for(let ap=0; ap<pointsArray.length-1; ap++){
    const quaternion = new Quaternion().setFromAxisAngle( rotateDirection, Math.PI / 2 ); // Rotate around Y
    // Finding direction before corner
    if(ap==0){
      directionBefore = lineVectors[pointsArray.length-2].clone().applyQuaternion(quaternion);
      if(debugF) bmu.createVisibleVector(pointsArray[ap], directionBefore, 0x999900);
    }
    let directionAfter = undefined;
    let myLine = undefined
    
    if(ap==lineVectors.length-1){ // Handling the last point in array
      directionAfter = lineVectors[1].clone().applyQuaternion(quaternion);
      myLine = new Line3(pointsArray[ap].clone(), pointsArray[0].clone());
    } else {
      directionAfter = lineVectors[ap].clone().applyQuaternion(quaternion);
      myLine = new Line3(pointsArray[ap].clone(), pointsArray[ap+1].clone());
    }
    let distanceVector = new Vector3( 0, 0, 0 );
    myLine.closestPointToPoint(doorLocation.clone(), false, distanceVector);
    const distance = distanceVector.distanceTo(doorLocation.clone());
    console.log("Distance: "+distance);
    if(distance<closestDistance){
      closestDistance = distance;
      closestVector = directionAfter.clone();
      if(myLine) myLine.getCenter(foundLineCenter);
    }
    if(debugF) bmu.createVisibleVector(pointsArray[ap], directionAfter, 0x009900);
    directionBefore.normalize();
    directionAfter.normalize();
    directionBefore = directionAfter;
  }
  const foundDoorVector = closestVector.negate();
  bmu.createVisibleVector(doorLocation, foundDoorVector, 0x000000);
  //bmu.createPlaceMarker(foundLineCenter, 0x000000);
  return foundDoorVector;
}

