const wf = {}

import { rdf } from '@inrupt/solid-client/dist/constants';
import { nullDependencies } from 'mathjs';
import { stringify } from 'uuid';
import store from '../store';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
const $rdf = require('rdflib');
let wayRdfStore = $rdf.graph();

export default {
  wf: wf,
  name: 'Wayfinder',
  data () {
    return {

    };
  },
  methods: {
    populateWayRdfStore(){
      // Only adding room that have more than 1 door or is a shared space.
      console.log("Populating way RDF Store")
      wayRdfStore = $rdf.graph();
      const uriArray = [];
      store.state.rs.rdfStore.match(null, ns.NEO("m_join"), null).forEach(st =>{
        if(!uriArray.includes(st.subject.value)) uriArray.push(st.subject.value);
        if(!uriArray.includes(st.object.value)) uriArray.push(st.object.value);
      });
      store.state.rs.rdfStore.match(null, ns.NEO("m_port"), null).forEach(st =>{
        if(!uriArray.includes(st.subject.value)) uriArray.push(st.subject.value);
      });
      store.state.rs.rdfStore.match(null, ns.IFOUR("islocked"), null).forEach(st =>{
        wayRdfStore.add(st.subject, st.predicate, st.object, $rdf.default);
      });
      uriArray.forEach(uriVal =>{
        if(store.state.rs.rdfStore.match($rdf.sym(uriVal), ns.NEO("m_port"), null).length>1 || store.state.rs.rdfStore.match($rdf.sym(uriVal), ns.NEO("m_join"), null).length>0 || store.state.rs.rdfStore.match(null, ns.NEO("m_join"), $rdf.sym(uriVal))>0){
          // Room has several doors and should be added
          store.state.rs.rdfStore.match($rdf.sym(uriVal), ns.NEO("m_port"), null).forEach(st =>{
            wayRdfStore.add(st.subject, st.predicate, st.object, $rdf.default);
          });
          store.state.rs.rdfStore.match($rdf.sym(uriVal), ns.NEO("m_join"), null).forEach(st =>{
            wayRdfStore.add(st.subject, st.predicate, st.object, $rdf.default);
          });
          store.state.rs.rdfStore.match(null, ns.NEO("m_join"), $rdf.sym(uriVal)).forEach(st =>{
            wayRdfStore.add(st.subject, st.predicate, st.object, $rdf.default);
          });
        }
      });
    },
    wayBetweenRooms(room1, room2, numberOfWays, debug=false){
      //console.log("Finding way: "+room1+" : "+room2);
      let exitFunction = false;
      const startTime = performance.now();
      let finalWays = [];
      //let room1 = $rdf.literal("Rom 125", 'no');
      //let room2 = $rdf.literal("Rom F121", 'no');
      let roomID1 = "";
      let roomID2 = "";
      store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal("Room "+room1, 'en')).forEach(st => {
        roomID1 = st.subject.value;
        if(debug) console.log("RoomID1:", roomID1);
      });
      store.state.rs.rdfStore.match(null, ns.RDFS("label"), room1).forEach(st => {
        roomID1 = st.subject.value;
        if(debug) console.log("RoomID1:", roomID1);
      });
      store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal("Room "+room2, 'en')).forEach(st => {
        roomID2 = st.subject.value;
        if(debug) console.log("RoomID2:", roomID2);
      });
      store.state.rs.rdfStore.match(null, ns.RDFS("label"), room2).forEach(st => {
        roomID2 = st.subject.value;
        if(debug) console.log("RoomID2:", roomID2);
      });
      // Get all doors into array
      /*let doorArray = [];
      store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.NEO("x_door")).forEach(st => {
        doorArray.push(st.subject);
      });*/

      if(wayRdfStore.match(null, ns.NEO("m_port"), null).length==0) this.populateWayRdfStore(); // Populate only if empty
      let pathLeftArray = [];
      let pathRightArray = [];
      // Add the first rooms to the array
      pathLeftArray.push([roomID1]);
      pathRightArray.push([roomID2]);
      // Add the first doors to the array
      store.state.rs.rdfStore.match($rdf.sym(roomID1), ns.NEO("m_port"), null).forEach(st => {
        pathLeftArray.push([roomID1, st.object.value]);
      });
      store.state.rs.rdfStore.match($rdf.sym(roomID2), ns.NEO("m_port"), null).forEach(st => {
        pathRightArray.push([roomID2, st.object.value]);
      });
      // Add the second rooms then doors to the array - loop

      let foundLink = 0;
      var wl = 2; // Starts after first room and door
      do {
        //Finding rooms and doors from left
        let somethingAdded = false;
        let alreadyExist = false;
        pathLeftArray.forEach((item, i) => {
          let last = item.length-1;
          let currentNode = item[last];
          if(currentNode){ // Check if defined
            // Check type of node - Door or Room
            let isDoor = false;
            store.state.rs.rdfStore.match($rdf.sym(currentNode), ns.RDF("type"), null).forEach(st => {
              if(st.object.value.includes("x_door")){
                //console.log("Confirm door");
                isDoor = true;
              }
            });
            if(isDoor){
              this.getRelatedDoors(currentNode).forEach(nURI => {
                let doorLabel = store.state.rs.rdfStore.match($rdf.sym(nURI), ns.RDFS("label"), null)[0].object.value;
                if(!doorLabel.includes("undefined") && !doorLabel.includes("Outdoor")){
                  //console.log("Found door");
                  let tempArray = [];
                  alreadyExist = false;
                  item.forEach((itemPart, j) => {
                    tempArray.push(itemPart);
                    if(itemPart==nURI) alreadyExist = true; // Prevent loops back to previous room
                  });
                  if(alreadyExist) return; // Break out of forEach loop
                  const checkArray = pathLeftArray;//.slice(-100); // Set limit to check the last 100 added items, will be a limit of hall with 100 rooms
                  checkArray.forEach(iexist => {
                    if(iexist.toString()===tempArray.toString()){
                      alreadyExist = true;
                      return;
                    }
                  });
                  if(!alreadyExist){
                    pathLeftArray.push(tempArray); // M splitte ut item til  bli liste over named nodes
                    somethingAdded = true;
                  }
                }
              });
            } else {
              this.getRelatedSpaces(currentNode).forEach(nURI => {
                // Found a room, should do some checks.
                //console.log("Found room");
                let tempArray = [];
                alreadyExist = false;
                item.forEach((itemPart, j) => {
                  tempArray.push(itemPart);
                  if(itemPart==nURI) alreadyExist = true; // Prevent loops back to previous room
                });
                if(alreadyExist) return; // Break out of forEach loop
                tempArray.push(nURI);
                const checkArray = pathLeftArray;//.slice(-100); // Set limit to check the last 100 added items, will be a limit of hall with 100 rooms
                checkArray.forEach(iexist => {
                  if(iexist.toString()===tempArray.toString()){
                    alreadyExist = true;
                    return;
                  }
                });
                if(!alreadyExist){
                  pathLeftArray.push(tempArray); // M splitte ut item til  bli liste over named nodes
                  somethingAdded = true;
                }
                pathRightArray.forEach((item2, i) => {
                  //if(item2[last-1]) if(item2[last-1] == st.object.value) console.log("Found some other relation!!");
                  if(item2[item2.length-1] == nURI){
                    const foundWay = tempArray.concat(item2.reverse()); // Combining the way list from both sides
                    if(foundWay.length<7) exitFunction = true; // Prevents from searching for alternative ways when the way is short
                    //console.log(tempArray);
                    let wayAlreadyExist = false;
                    finalWays.forEach(way => {
                      if(way.way.toString()===foundWay.toString()) wayAlreadyExist = true;
                    });
                    if(!wayAlreadyExist){
                      console.log("WOW, found the way from left!!!");
                      finalWays.push({"way": foundWay, "priority": 0 });
                      foundLink++;
                      wl = 37;
                    } 
                  }
                });
              });
            }
          }
        });

        //Finding rooms and doors from right
        pathRightArray.forEach((item, i) => {
          let last = item.length-1;
          let currentNode = item[last];
          if(currentNode){ // Check if defined
            // Check type of node - Door or Room
            let isDoor = false;
            store.state.rs.rdfStore.match($rdf.sym(currentNode), ns.RDF("type"), null).forEach(st => {
              //console.log("Compare:"+st.object+":"+ns.NEO("x_door"));
              if(st.object.value.includes("x_door")){
                //console.log("Confirm door");
                isDoor = true;
              }
            });
            alreadyExist = false;
            if(isDoor){
              this.getRelatedDoors(currentNode).forEach(nURI => {
                let doorLabel = store.state.rs.rdfStore.match($rdf.sym(nURI), ns.RDFS("label"), null)[0].object.value;
                if(!doorLabel.includes("undefined") && !doorLabel.includes("Outdoor")){
                  //console.log("Found door");
                  let tempArray = [];
                  alreadyExist = false;
                  item.forEach((itemPart, j) => {
                    tempArray.push(itemPart);
                    if(itemPart==nURI) alreadyExist = true; // Prevent loops back to previous room
                  });
                  if(alreadyExist) return; // Break out of forEach loop
                  tempArray.push(nURI);
                  const checkArray = pathRightArray;//.slice(-100); // Set limit to check the last 100 added items, will be a limit of hall with 100 rooms
                  checkArray.forEach(iexist => {
                    if(iexist.toString()===tempArray.toString()){
                      alreadyExist = true;
                      return;
                    }
                  });
                  if(!alreadyExist){
                    pathRightArray.push(tempArray); // M splitte ut item til  bli liste over named nodes
                    somethingAdded = true;
                  }
                }
              });
            } else {
              this.getRelatedSpaces(currentNode).forEach(nURI => {
                //console.log("Found room");
                let tempArray = [];
                alreadyExist = false;
                item.forEach((itemPart, j) => {
                  tempArray.push(itemPart);
                  if(itemPart==nURI) alreadyExist = true; // Prevent loops back to previous room
                });
                if(alreadyExist) return; // Break out of forEach loop
                tempArray.push(nURI);
                const checkArray = pathRightArray;//.slice(-100); // Set limit to check the last 100 added items, will be a limit of hall with 100 rooms
                checkArray.forEach(iexist => {
                  if(iexist.toString()===tempArray.toString()){
                    alreadyExist = true;
                    return;
                  }
                });
                if(!alreadyExist){
                  pathRightArray.push(tempArray); // M splitte ut item til  bli liste over named nodes
                  somethingAdded = true;
                }
                pathLeftArray.forEach((item3, i) => {
                  //if(item3[last-1]) if(item3[last-1] == st.object) console.log("Found some other relation!!");
                  if(item3[item3.length-1] == nURI){
                    //tempArray.push(st.object); // Should not be included, creates duplicates
                    //tempArray.splice(-1); // Remove last element
                    tempArray = tempArray.concat(item3.reverse()); // Combining the way list from both sides
                    const foundWay = tempArray.reverse();
                    //console.log(tempArray);
                    // Add to button list
                    //store.state.bv.selectedRoomIDs = [];
                    //store.state.bv.selectedRoomNames = [];
                    /*tempArray.forEach((wayPart, j) => {
                      store.state.bv.selectedRoomNames.push(wayPart);
                    });*/
                    if(foundWay.length<7) exitFunction = true; // Prevents from searching for alternative ways when the way is short
                    let wayAlreadyExist = false;
                    finalWays.forEach(way => {
                      if(way.way.toString()===foundWay.toString()) wayAlreadyExist = true;
                    });
                    if(!wayAlreadyExist){
                      console.log("WOW, found the way from right!!!");
                      finalWays.push({"way": foundWay, "priority": 0 });
                      foundLink++;
                      wl = 47;
                    } 
                  }
                });
              });
            }
          }
        });
        //console.log("Round "+wl+" Completed...");
        if((performance.now()-startTime)>=15000) foundLink++;
        if((performance.now()-startTime)>=20000) exitFunction = true;
        wl++;
        if(wl==50||exitFunction){ // Set max number to prevent infinit loop
          foundLink = numberOfWays;
        }
        //if(!somethingAdded) return;
      }
      while (foundLink<numberOfWays);

      // Select the way based on parameters
      finalWays.forEach(way => {
        way.priority = way.way.length*-15; // M ha denne over dobbelt s hy som fratrekk for interface point for ikke  ta omveier pga mange drer.
      });
      finalWays.forEach(way => {
        way.way.forEach(waySpace => {
          //if(store.state.rs.rdfStore.match($rdf.sym(waySpace), ns.RDF("type"), ns.NEO("m_interface-point")).length != 0) way.priority = way.priority+5; // Do not count when part of same space without door
          store.state.rs.rdfStore.match($rdf.sym(waySpace), ns.NEO("m_port"), null).forEach(port => {
            way.priority = way.priority+(store.state.rs.rdfStore.match($rdf.sym(port.object.value), ns.RDF("type"), ns.NEO("x_door")).length*1);
            //way.priority = way.priority+(store.state.rs.rdfStore.match($rdf.sym(port.object.value), ns.RDF("type"), ns.NEO("m_interface-point")).length*3);
          });
        });
      });
      finalWays.sort((a, b) => a.priority - b.priority);
      const finalWaysSorted = finalWays.reverse();
      if(debug) console.log(finalWaysSorted);

      // Create function to display way names instead of URIs ( Just for testing )
      if(debug){
        const finalNames = [];
        finalWaysSorted.forEach(way => {
          const nameArray = [];
          way.way.forEach(waySpace => {
            nameArray.push(store.state.rs.rdfStore.match($rdf.sym(waySpace), ns.RDFS("label"), null)[0].object.value);
          });
          finalNames.push(nameArray);
        });
        if(debug) console.log(finalNames);
      }

      const finalWaysUniqueSorted = [];
      // Remove duplicates in wayfinding
      finalWaysSorted.forEach((element) => {
        if (!finalWaysUniqueSorted.includes(element)) {
          finalWaysUniqueSorted.push(element);
        }
      });

      if(debug) console.log("Number of loops in wayfinding: "+wl);
      const endTime = performance.now();
      if(debug) console.log(`Wayfinding used: ${endTime - startTime} milliseconds`);
      if(finalWaysUniqueSorted[0]){
        return finalWaysUniqueSorted[0].way;
      } else {
        console.log("Warning: Did not find any Way..");
        return [];
      }
    },
    getRelatedSpaces(uri){
      const relatedURIs = [];
      wayRdfStore.match($rdf.sym(uri), ns.NEO("m_port"), null).forEach(triple =>{
        if(!this.checkIfDoorIsLocked(triple.object.value)) relatedURIs.push(triple.object.value);
      });
      wayRdfStore.match($rdf.sym(uri), ns.NEO("m_join"), null).forEach(triple =>{
        if(!this.checkIfDoorIsLocked(triple.object.value)) relatedURIs.push(triple.object.value);
      });
      wayRdfStore.match(null, ns.NEO("m_join"), $rdf.sym(uri)).forEach(triple =>{
        if(!this.checkIfDoorIsLocked(triple.subject.value)) relatedURIs.push(triple.subject.value);
      });
      return relatedURIs;
    },
    getRelatedDoors(uri){
      const relatedURIs = [];
      wayRdfStore.match(null, ns.NEO("m_port"), $rdf.sym(uri)).forEach(triple =>{
        // Kan la vre  returnere drer til spaces med kun en dr
        let shouldReturn = true;
        wayRdfStore.match($rdf.sym(triple.subject.value), ns.NEO("m_port"), null).forEach(triple =>{
          if(triple.object.value!=uri){
            if(wayRdfStore.match(null, ns.NEO("m_port"), $rdf.sym(triple.object.value)).length<2&&!wayRdfStore.match(null, ns.NEO("m_port"), $rdf.sym(triple.object.value))[0]){
              shouldReturn = false;
            }
          }
        });
        if(shouldReturn&&!this.checkIfDoorIsLocked(triple.subject.value)) relatedURIs.push(triple.subject.value);
      });
      wayRdfStore.match($rdf.sym(uri), ns.NEO("m_join"), null).forEach(triple =>{
        if(!this.checkIfDoorIsLocked(triple.object.value)) relatedURIs.push(triple.object.value);
      });
      wayRdfStore.match(null, ns.NEO("m_join"), $rdf.sym(uri)).forEach(triple =>{
        if(!this.checkIfDoorIsLocked(triple.subject.value)) relatedURIs.push(triple.subject.value);
      });
      return relatedURIs;
    },
    checkIfDoorIsLocked(uri){
      let islocked = false;
      if(wayRdfStore.match($rdf.sym(uri), ns.IFOUR("islocked"), null)[0]) islocked = true;
      return islocked;
    },
    getWayPointsFromURIs(wayURIs){
      // Get all the xyz points where to go for finding the way to target
    },
  },
  watch: {

  },
};
