ur.isType = function (item,type){
    var type = u.getURI(type)
    var uri = u.getURI(item)
    if (uri && type){
	var types = ur.getTypes(uri)
	if (Array.isArray(types)){
	    if (types.includes(type)){
		return true
	    } else {
		return false
	    }
	} else {
	    return false
	}
    } else {
	return false
    }
}

