ur.location = function (uri, app) {
	//log.debug("ur.location",uri,app)
	var node
	if (app === "Files") {
		node = u.getURI(uri)
		if (!ur.containerP(node)) {
			node = ur.getContainer(node)
		}
	}
	if (!node) {
		node = uri
	}
	return u.getURI(node)
}

