    blackListAllExeptNearestStairs(roomID1, roomID2){
      const blacklist = [];
      // Get start room height above ground
      let startRoomHAG = 0;
      store.state.rs.rdfStore.match($rdf.sym(roomID1), ns.IFOUR("roomhag"), null).forEach(st => {
        startRoomHAG = Number(st.object.value);
      });
      let endRoomHAG = 0;
      store.state.rs.rdfStore.match($rdf.sym(roomID2), ns.IFOUR("roomhag"), null).forEach(st => {
        endRoomHAG = Number(st.object.value);
      });
      if(startRoomHAG==endRoomHAG) return []; // Do not need stairs..
      // Find nearest stairs
      let nearestStairID = undefined;
      let nearestStairsDistance = 5000;
      let startRoomLoc = undefined;
      if(store.state.rs.rdfStore.match($rdf.sym(roomID1), ns.SIO("SIO_000507"), null)[0]){
        const startRoomLocString = store.state.rs.rdfStore.match($rdf.sym(roomID1), ns.SIO("SIO_000507"), null)[0].object.value;
        startRoomLoc = u.pointStringToVector3(startRoomLocString);
      } 
      if(!startRoomLoc) return [];
      store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.SEAS("Stairs")).forEach(st => {
        const stairID = st.subject.value;
        let stairLoc = undefined;
        if(store.state.rs.rdfStore.match($rdf.sym(stairID), ns.SIO("SIO_000507"), null)[0]){
          const stairLocString = store.state.rs.rdfStore.match($rdf.sym(stairID), ns.SIO("SIO_000507"), null)[0].object.value;
          stairLoc = u.pointStringToVector3(stairLocString);
          if(startRoomLoc.distanceTo(stairLoc)<=nearestStairsDistance){
            nearestStairsDistance = startRoomLoc.distanceTo(stairLoc);
            nearestStairID = stairID;
          }
        }
      });
      // Add all staris on same level to blacklist
      store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.SEAS("Stairs")).forEach(st => {
        const stairID = st.subject.value;
        let stairRoomHAG = undefined;
        store.state.rs.rdfStore.match($rdf.sym(stairID), ns.IFOUR("roomhag"), null).forEach(st => {
          stairRoomHAG = Number(st.object.value);
        });
        if(stairRoomHAG) if(startRoomHAG==stairRoomHAG&&nearestStairID!=stairID) blacklist.push(rdfjs.namedNode(stairID));
      });
      return blacklist;
    }
