const r = {};

const $rdf = require('rdflib');
import store from '../store';
import u from './util.js';

r.searchAndGetURIs = async function(conceptName){
    let uri = 'https://helsenorge'+store.state.testNum+'.'+ store.state.serverPath +'/search/'+encodeURIComponent(conceptName);
    try {
      let res = await window.solid.session.fetch(
        uri, {
          method: "GET",
          credentials: "include",
          headers: {
            "Accept": "text/turtle"
          },
      }).then(res => res.text());
      console.log(res);
      $rdf.parse(res, store.state.rs.rdfStore, uri, 'text/turtle');
      return res;
    } catch (error) {
      console.log(error);
      console.log("Search failed");
    }
  }

  r.uploadFile = async function(file, container){
    window.solid.session.fetch(
      container+file.name, {
      method: "PUT",
      credentials: "include",
      body: file,
      headers: {
        "Content-Type": "",
      },
      onUploadProgress: function( progressEvent ) {
        this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded / progressEvent.total ) * 100 ) );
        console.log(this.uploadPercentage);
      }.bind(this)}).then(res => {
        console.log('done uploading', res);
    })
    .catch(e => {
      console.error(JSON.stringify(e.message));
    });
  }

  r.populateStoreFromTurtle = async function(){
    console.log("Populating store from turtle...")
    let TurtleURI = store.state.location+"home/TTL/rooms.rtf"
    try {
      window.solid.session.fetch(TurtleURI, { method: "GET", credentials: "include" }).then((res) => {
        res.arrayBuffer().then((filedata) => {
          let file = new Blob([filedata]);
          const fr = new FileReader();
          fr.onload = function() {
              $rdf.parse(this.result, store.state.rs.rdfStore, TurtleURI, 'text/turtle');
              var turtle = u.storeToTurtle(store.state.rs.rdfStore, TurtleURI);
              //console.log(turtle);
          };
          fr.readAsText(file);
        });
      });
    } catch (error) {
      console.log(error);
      console.log("Failed to parse Turtle from file...");
    }
  }

export default r;