  data () {
    return {
      height: 0,
      autoRotate: true,
      bval: -0.01,
      file: "",
      colors: false,
      viewerWindow: null,
      labelDialog: false,
      labelLocation: null,
      loading: false,
      labelTagList: ["Bug", "Warning", "Info", "Unknown"],
      labelObject: {title: "", description: "", tags: ["Unknown"], responsible: "", imageuri: ""},
      employeeNameList: ["https://espen.trinpod.us/i"],
      employeeList: ["Espen Marthiniussen"],
      background: "#ffff00",
      ifcloadpercentage: "0",
      ifcloading: false,
      progress: null,
      error: null,
      floorSplitFactor: 1,
      gltfGeoVisible: false,
      spacesVisible: false,
