      icon_camera: require('./images/icon-walk.png'),
      icon_rotate: require('./images/icon-turntable.png'),
      icon_click: require('./images/icon-pointer.png'),
      icon_edgeface: require('./images/icon-wireframeshaded.png'),
      icon_cuttingplane: require('./images/icon-clippingtool.png'),
    };
  },
  methods: {
    ...mapMutations('bv', ["RESIZE"]),
    ...mapActions('bv', ["INIT", "ANIMATE", "RENDER", "CALL_RAYCAST", "CONVERT_TO_UTM", "CONVERT_FROM_UTM", "CONVERT_TO_SPHERICAL", "ADD_TO_SCENE", "INITIALIZE_PLAYER"]),
    addEventListerners(){
      document.addEventListener('dblclick', this.pickFromIFC, false );
      //document.addEventListener('mousedown', this.mouseDown, false);
      document.addEventListener('keyup',  this.keyEvents);
      document.addEventListener('dblclick', this.setIssueLabel, false);
    },
    removeEventListerners(){
      document.removeEventListener('dblclick', this.pickFromIFC, false );
      //document.removeEventListener('mousedown', this.mouseDown, false);
      document.removeEventListener('keyup',  this.keyEvents);
      document.removeEventListener('dblclick', this.setIssueLabel, false);
    },
    mouseDown(event){
      event.stopImmediatePropagation(); 
      let buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      let canvasBounds = this.$el.getBoundingClientRect();
      store.state.bv.mouseVector.x = ((event.clientX - canvasBounds.left) / (canvasBounds.right - canvasBounds.left)) * 2 - 1;
      store.state.bv.mouseVector.y  = -((event.clientY - canvasBounds.top) / (canvasBounds.bottom - canvasBounds.top)) * 2 + 1;
      store.state.bv.raycaster.setFromCamera( store.state.bv.mouseVector, store.state.bv.camera );
      const intersects = store.state.bv.raycaster.intersectObjects( buildingbox.children , true );
      if(!intersects.length) {
        return;
      };
      const firstIntersection = intersects[0];
      const location =  firstIntersection.point;
      store.state.bv.controls3d.target.set(location.x, location.y, location.z);
      store.state.bv.controls3d.update();
    },
    createBuildingObb: function(lat, long, oBB, bRotation, color, position={x:0, y:0, z:0}){
      // obb = oBB = NEO("m_matrix")
      // Scalefactor is used in all xyz directions
      console.log("oBB: "+oBB);
      let u = UnitsUtils.datumsToSpherical(lat, long);

      var v2Length = Math.sqrt((oBB.v2.x*oBB.v2.x)+(oBB.v2.y*oBB.v2.y)+(oBB.v2.z*oBB.v2.z));
      var v3Length = Math.sqrt((oBB.v3.x*oBB.v3.x)+(oBB.v3.y*oBB.v3.y));

      // Setting the dimentions of the bounding box
      var geoW, geoH, geoL;
      geoW = v2Length*this.scaleFactor;
      geoH = oBB.v3.z*this.scaleFactor;
      geoL = v3Length*this.scaleFactor;
      store.state.bv.coords = { x:geoW/2, y:geoH/2, z:-geoL/2 };
      //var geometry = new BoxGeometry( geoW, geoH, geoL );
      var geometry = this.lineDashBox( geoW, geoH, geoL );
      // Moving anchor from center of the bounding box

      // Set the initial position of the bounding box. Based on gps anchor u.
      //const material = new MeshPhongMaterial({color: 0x6666FF, side: DoubleSide, emissive: 0x666666, transparent:true, opacity:1});
      //var mesh = new Mesh( geometry, material );
      var mesh = new LineSegments( geometry, new LineDashedMaterial( { color: 0xffaa00, dashSize: 3, gapSize: 1 } ) );
      mesh.computeLineDistances();
      mesh.position.x = oBB.v1.x+position.x;
      mesh.position.y = oBB.v1.y+position.y;
      mesh.position.z = oBB.v1.z+position.z;

      if(bRotation){
        mesh.setRotationFromEuler(new Euler(bRotation.x, bRotation.y, bRotation.z, "XYZ"));
      }

      mesh.updateMatrix();
      mesh.matrixAutoUpdate = false;
      mesh.castShadow = false;
      mesh.renderOrder=1;
      return(mesh);
    },
    drawRoomFromPolyline(buildingbox, vx, vy, vz, height, polyline, lat, roomuri, color, opacity){
      var polyArray = [];
      if(polyline != ""){
        polyline = polyline.replace("^^neo:a_matrix-3xm", "");
        polyline = polyline.replace("[", "");
        polyline = polyline.replace("]", "");
        polyArray = polyline.split(",");
      }
      vx = vx*this.scaleFactor;
      vy = vy*this.scaleFactor;
      vz = vz*this.scaleFactor;
      const material = new MeshPhongMaterial({color: color, side: DoubleSide, emissive: 0x555555, transparent:true, opacity:opacity});
      const wireMaterial = new MeshBasicMaterial({
        color: 'white',
        polygonOffset: true,
        polygonOffsetFactor: 1,
      });
      const polyShape = new Shape();
      const points = [];
      var v1 = new Vector3( 0, vz, 0 );
      var v2 = new Vector3( 0, vz+(height*this.scaleFactor), 0 );
      var path = new LineCurve3( v1, v2 )
      var i = 0;
      while (polyArray.length > 1) {
        const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
        const x = (polyXYZ[0]*this.scaleFactor);
        const y = (polyXYZ[1]*this.scaleFactor);
        if(i == 0){
          polyShape.moveTo( (-x+vy), (-y-vx), vz );
          i = i+1;
        } else {
          polyShape.lineTo( (-x+vy), (-y-vx), vz );
        }
      }
      polyShape.autoClose = true;
      const extrudeSettings = { 
        extrudePath: path,
      };
      const polyGeometry = new ExtrudeGeometry( polyShape, extrudeSettings );
      const polyMesh = new Mesh( polyGeometry, material ) ;

      // Test to add border on box
      const wireMesh = new Mesh( polyGeometry, wireMaterial );
      polyMesh.add(wireMesh);

      //console.log(roomuri);
      polyMesh.name = roomuri;
      polyMesh.castShadow = true;
      polyMesh.receiveShadow = true;
      store.state.bv.boundingBoxDrawed.push(String(polyline));
      buildingbox.add(polyMesh);
      return(buildingbox);
    },
    moveToObject(){
      bw.moveToObject();
    },
    raycast( event ) {
      if(!store.state.bv.lockSelection){
        let canvasBounds = this.$el.getBoundingClientRect();
        store.state.bv.mouseVector.x = ((event.clientX - canvasBounds.left) / (canvasBounds.right - canvasBounds.left)) * 2 - 1;
        store.state.bv.mouseVector.y  = -((event.clientY - canvasBounds.top) / (canvasBounds.bottom - canvasBounds.top)) * 2 + 1;
        //console.log(store.state.bv.mouseVector.x+":"+store.state.bv.mouseVector.y);
        return this.CALL_RAYCAST();
      }
    },
    checkIfPointInsideAnyRoomGeometry(point){
      const foundRoomNames = [];
      store.state.bv.scene.getObjectByName("buildingbox").children.forEach((roomMesh, i) => {
          if(bmu.checkIfPointInsideMesh(point, roomMesh)){
            const rname = u.decodeCharacters(roomMesh.name);
            if(!foundRoomNames.includes(rname)){
              foundRoomNames.push(rname);
              console.log("Yay. Found inside: "+rname);
            }
          } 
      });
      return foundRoomNames;
    },
    async setIssueLabel( event ) {
      event.stopImmediatePropagation(); // May need to be removed.
      let buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      let canvasBounds = this.$el.getBoundingClientRect();
      store.state.bv.mouseVector.x = ((event.clientX - canvasBounds.left) / (canvasBounds.right - canvasBounds.left)) * 2 - 1;
      store.state.bv.mouseVector.y  = -((event.clientY - canvasBounds.top) / (canvasBounds.bottom - canvasBounds.top)) * 2 + 1;
      store.state.bv.raycaster.setFromCamera( store.state.bv.mouseVector, store.state.bv.camera );
      const intersects = store.state.bv.raycaster.intersectObjects( buildingbox.children , true );

      if(!intersects.length) {
        return;
      };

      const firstIntersection = intersects[0];
      const location =  firstIntersection.point;
      this.labelLocation = location;
      let foundRooms = this.checkIfPointInsideAnyRoomGeometry(location);
      this.labelDialog = true;
      if(this.$route.query.location){
        this.employeeList = await bmu.getEmployeeList(this.$route.query.location+"i");
        this.employeeList.forEach(empl => {
          this.employeeNameList.push(empl.name);
        });
      }
    },
    setRoomLabel( location, foundObjectName ) {
      // Remove all previous labels:
      console.log("Setting label for: "+foundObjectName);
      store.state.bv.selectedRoomNames.push(foundObjectName);
      let foundObjectURI = "";
      if(store.state.rs.rdfStore.match(null, ns.FOAF("name"), $rdf.literal(foundObjectName))[0]) foundObjectURI = store.state.rs.rdfStore.match(null, ns.FOAF("name"), $rdf.literal(foundObjectName))[0].subject.value;
      console.log("URI: "+foundObjectURI);
      if(foundObjectURI!=""){
        if(store.state.rs.rdfStore.match($rdf.sym(foundObjectURI), ns.IFOUR("trafficload"), null)[0]) console.log("Trafficload: "+store.state.rs.rdfStore.match($rdf.sym(foundObjectURI), ns.IFOUR("trafficload"), null)[0].object.value);
      }
      try { 
        let foundRooms = [];
        let previousLabel = store.state.bv.scene.getObjectByName("roomlabel");
        if(foundObjectName) {
          foundRooms.push(foundObjectName);
        } else {
          foundRooms = this.checkIfPointInsideAnyRoomGeometry(location);
        }

        this.labelLocation = location;
      
        let roomName = store.state.rs.rdfStore.match($rdf.sym(foundRooms[0]), ns.RDFS("label"), null)[0].object.value;
        roomName = u.decodeCharacters(roomName);

        if(!store.state.bv.multiSelect) store.state.bv.selectedRoomNames = [];
        store.state.bv.selectedRoomNames.push(roomName.replace("Rom ", "").replace("Room ", ""));

        let roomuri = store.state.rs.rdfStore.match($rdf.sym(foundRooms[0]), ns.RDFS("label"), null)[0].subject.value;
        let roomGeo = store.state.rs.rdfStore.match($rdf.sym(foundRooms[0]), ns.IFOUR("m_polyline"), null)[0].object.value;
        console.log("Roomgeo");
        console.log(roomGeo);
        this.addRoomLabelHTML(roomName, location, foundRooms[0], roomuri);
        store.state.bv.scene.remove(previousLabel);
      } catch (error){}

