    createLabel(){
      this.labelDialog = false;
      let labelID = Date.now();
      this.addLabelToDatabase(this.labelObject.title, this.labelObject.description, this.labelLocation, this.labelObject.tags, this.labelObject.responsible, this.labelObject.imageuri, "Not started", labelID);
      store.state.tasks.push({ id: labelID, uri: "", position: this.labelLocation, title: this.labelObject.title, description: this.labelObject.description, imageuri: this.labelObject.imageuri, responsible: this.labelObject.responsible, state: "Not started", date: "16.09.2022", type: "issue" });
      this.addLabelHTML(this.labelObject.title, this.labelLocation, this.labelObject.description, this.labelObject.imageuri);
      store.state.bv.labelrenderer.render(store.state.bv.scene, store.state.bv.camera);
    },
    addLabelToDatabase(title, description, position, tags, responsible, imageuri, state, labelID){
        let matrixString = "[0,0,0,0,0,0,"+position.x+","+position.y+","+position.z+"]^^neo:a_matrix-3m";
        const labelBlank = u.getBlankNode("Issuelabel: "+title)
        this.employeeList.forEach(empl => {
          if(empl.name == responsible){
            responsible = empl.personuri;
          }
        });
        bmu.sendEmailToResponsible(responsible);
        if(!store.state.blankExisted){
          store.state.rs.tempRdfStore = $rdf.graph();
          tags.forEach(tag => {
            addToStores(labelBlank, ns.TAG("tag"), tag);
          });
          console.log("Adding label to store...");
          addToStores(labelBlank, ns.RDF("type"), ns.CTO("TaskContext"));
          addToStores(labelBlank, ns.RDFS("label"), $rdf.literal(title, 'en'));
          addToStores(labelBlank, ns.RDFS("comment"), description);
          addToStores(labelBlank, ns.TAG("tag"), "i4task"+"."+labelID);
          addToStores(labelBlank, ns.IFOUR("taskStatus"), state);
          addToStores(labelBlank, ns.FOAF("aimChatID"), $rdf.sym(responsible)); // Who the issue is assigned to.
          addToStores(labelBlank, ns.FOAF("maker"), $rdf.sym(store.state.webId)); // Concept verified and used by Fred - foaf:Maker
          addToStores(labelBlank, ns.FOAF("image"), $rdf.sym(imageuri));
          addToStores(labelBlank, ns.NEO("m_matrix"), matrixString);
          let turtle = u.storeToTurtle(store.state.rs.tempRdfStore, "");
          turtle = ur.modifyTurtle(turtle);
          console.log(turtle);
          ur.uploadTurtle(this.$route.query.location+"i", turtle, false).then((successfulFetch) => {
            console.log(successfulFetch);
          });
        }
    },
