    loadIssueLabelsFromStore(tag){
      console.log("loading labels..")
      store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
        if(item.object.value.includes("i4task")){
            console.log("Found item: "+item.object.value);
            let title = "";
            let descript = "";
            let imageuri = "";
            let roomuri = "";
            let responsible = "";
            let responsibleName = "";
            let state = "Not started";
            //state = decodeURIComponent(item.object.value.replace("i4task.", ""));
            store.state.rs.rdfStore.match(item.subject, ns.IFOUR("taskStatus"), null).forEach(foundItem => {
                //console.log("Found state: "+foundItem.object.value);
                state = foundItem.object.value;
            });
            store.state.rs.rdfStore.match(item.subject, ns.RDFS("label"), null).forEach(foundItem => {
                title = foundItem.object.value;
                roomuri = item.subject.value;
                console.log("Label", title);
            });
            store.state.rs.rdfStore.match(item.subject, ns.RDFS("comment"), null).forEach(foundItem => {
                descript = foundItem.object.value;
            });
            store.state.rs.rdfStore.match(item.subject, ns.FOAF("image"), null).forEach(foundItem => {
                imageuri = foundItem.object.value;
            });
            store.state.rs.rdfStore.match(item.subject, ns.FOAF("aimChatID"), null).forEach(foundItem => {
                responsible = foundItem.object.value;
                responsibleName = store.state.rs.rdfStore.match($rdf.sym(foundItem.object.value), ns.FOAF("name"), null)[0].object.value || "";
            });
            let matrix = "";
            let position = new Vector3( 0, 0, 0);
            store.state.rs.rdfStore.match(item.subject, ns.NEO("t_"), null).forEach(foundMatrix => {
                matrix = foundMatrix.object.value;
            });
            console.log("Matrix: "+matrix);
            if(matrix.length>=10){
                matrix = matrix.replace(']^^neo:a_matrix-3m', '');
                matrix = matrix.replace('[0,0,0,0,0,0,', '');
                const matrixArray = matrix.split(",");
                position.x = parseInt(matrixArray[0]);
                position.y = parseInt(matrixArray[1]);
                position.z = parseInt(matrixArray[2]);
            }
            console.log(position);
            let found = false;
            store.state.tasks.forEach(task => {
              if(task.uri===item.subject.value) found = true;
            });
            this.addLabelHTML(title, position, descript, imageuri, roomuri); 
          }
      });
      this.RENDER();
