    async uploadImage(fileChooser) {
      console.log(fileChooser);
      this.progress = 0;
      let formData = new FormData();
      formData.append('image', fileChooser);
      let imageuri = this.$route.query.location+"Home/Issues/"+fileChooser.file.name;
      try {
        window.solid.session.fetch(
            imageuri, {
              method: "PUT",
              credentials: "include",
