    markIFCElementWithColor(elementid, modelid, geometry){
      const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      let material = new MeshLambertMaterial({
          transparent: true,
          opacity: 0.6,
          color: 0xff88ff,
          depthTest: false
      });
      let subset = ifcLoader.ifcManager.createSubset({ // Problem with create subset is that it does not support scale the geometry using the scalefactor.
          modelID: modelid,
          ids: [elementid],
          material: material,
          scene: buildingbox,
          removePrevious: true
      });
      buildingbox.add(subset);
      //geometry.scale(this.scaleFactor, this.scaleFactor, this.scaleFactor);
      this.RENDER();
