    async setupBuildingBox() {
      const axesHelper = new AxesHelper();
      store.state.bv.scene.add( axesHelper );
      const IFCmetadata = await ifcu.getIFCmetaDataFromJson(this.$route.query.location+"home/IFC/building.json"); // Create function for storing in local storage
      const buildingLat = IFCmetadata.buildinglat;
      const buildingLong = IFCmetadata.buildinglong;
      const bRotation = IFCmetadata.rotation;
      const color1 = 0xFF4433;
      let oBB = IFCmetadata.buildingdimensions; // First vector goes from buildingpoint2 to buildingpoint1 in meters
      this.buildingLat = buildingLat;
      this.scaleFactor = 1/(Math.cos(buildingLat*Math.PI/180)); // Translates meters into epsg-3857

      const buildingbox = this.createBuildingObb(buildingLat, buildingLong, oBB, bRotation, color1);
      buildingbox.name = "buildingbox";
      
      const u = UnitsUtils.datumsToSpherical(buildingLat, buildingLong);
      store.state.bv.buildingboxPosition = {x: u.x, y: 0, z:u.y };
      store.state.currentBuildingTag = IFCmetadata.tag;
      // Move the map so the zero point of the scene is same as zero point of buildingbox
      store.state.bv.scene.getObjectByName("map").position.set(-store.state.bv.buildingboxPosition.x, 0, store.state.bv.buildingboxPosition.z);
      this.ADD_TO_SCENE({ buildingbox: buildingbox });
      store.state.bv.focusobject = store.state.bv.scene.getObjectByName("buildingbox");
      bw.zoomToObject();
      store.state.bv.selectedRoomURI = this.$route.query.select;
      this.selectRoomFromURI();
      this.RENDER();
    },
    async changeRoomGeometry(){
      let buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      if(!buildingbox) buildingbox = await this.setupBuildingBox();
      store.state.bv.boundingBoxDrawed = [];
      store.state.bv.roomGeometry.forEach((item, i) => {
        if(!store.state.bv.scene.getObjectByName(item.roomuri)){ // Check if mesh already exist
          buildingbox = this.drawRoomFromPolyline(buildingbox, item.x, item.y, item.z, item.height, item.polyline, this.buildingLat, item.roomuri, item.color, item.opacity);
          console.log(item);
        }
      });
      this.RENDER();
    },
    selectRoomFromURI(){
      if(store.state.bv.selectedRoomURI){
        const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
        let roomURI = store.state.bv.selectedRoomURI;
        let polyline = store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.IFOUR("m_polyline"), null)[0].object.value;
        var obb = polyline+"";
        obb = obb.replace('^^neo:a_matrix-3m', '');
        obb = obb.replaceAll('"', '');
        const obbArray = obb.split(";");
        let obbString = '{"x":'+obbArray[2]+', "y":'+obbArray[3]+', "z":'+obbArray[4]+', "height":'+obbArray[5]+', "color":'+0xff0000+', "opacity":'+1+', "roomuri":"'+roomURI+'", "polyline":"'+obbArray[6]+'"}';
        console.log(obbString);
        let obbJson = JSON.parse(obbString);
        store.state.bv.roomGeometry.push(obbJson);
        setTimeout(function() { 
          store.state.bv.INTERSECTED = store.state.bv.scene.getObjectByName(roomURI);
          bw.moveToObject();
         }, 100);
         if(this.previousSelectedRoom) store.state.bv.scene.getObjectByName(this.previousSelectedRoom).material.color.setHex( 0x225577 );
         store.state.bv.scene.getObjectByName(roomURI).material.color.setHex( 0xff0000 );
         this.previousSelectedRoom = roomURI;
         const xyz = bmu.getMeshCenterPointTop(store.state.bv.scene.getObjectByName(roomURI)).applyMatrix4(buildingbox.matrixWorld);
         this.setRoomLabel(xyz, roomURI);
         this.RENDER();
      }
    },
