    zoomToLatLong(lat, long) {
      var cameraHeight = 2;
      const siteLat = lat;
      const siteLong = long;
      let coords = UnitsUtils.datumsToSpherical(siteLat, siteLong);
      store.state.bv.controls3d.target.set(coords.x, 0, -coords.y);
      store.state.bv.camera.position.set(coords.x+5, cameraHeight, -coords.y+5);
      store.state.bv.scene.getObjectByName("sdfmesh").position.set(coords.x, 0, -coords.y);
      this.RENDER();
