    async highlight(event) {
      let model = { id: - 1};
      // Creates subset material
      let material = new MeshLambertMaterial({
          transparent: true,
          opacity: 0.6,
          color: 0xff88ff,
          depthTest: false
      })
       let found = await this.raycast(event);
       console.log(found);
       if (found[0]) found = found[0];
       if (found) {
           // Gets model ID
           model.id = found.object.modelID;
           // Gets Express ID
