    lineDashBox( width, height, depth ) {
      width = width * 0.5,
      height = height * 0.5,
      depth = depth * 0.5;
      const geometry = new BufferGeometry();
      const position = [];

      position.push(
        - width, - height, - depth,
        - width, height, - depth,

        - width, height, - depth,
        width, height, - depth,

        width, height, - depth,
        width, - height, - depth,

        width, - height, - depth,
        - width, - height, - depth,

        - width, - height, depth,
        - width, height, depth,

        - width, height, depth,
        width, height, depth,

        width, height, depth,
        width, - height, depth,

        width, - height, depth,
        - width, - height, depth,

        - width, - height, - depth,
        - width, - height, depth,

        - width, height, - depth,
        - width, height, depth,

        width, height, - depth,
        width, height, depth,

        width, - height, - depth,
        width, - height, depth
       );
      geometry.setAttribute( 'position', new Float32BufferAttribute( position, 3 ) );
      return geometry;
    },
    keyEvents(event) {
      if (event.isComposing || event.keyCode === 90) {
        console.log("Z key down 1");
        bw.moveToObject();
        return;
      } else if (event.keyCode === 76) {
        console.log("L key down 2");
        this.lockSelection();
        return;
      } else if (event.keyCode === 73){
        console.log("I key down");
        this.drawIFC();
        return;
      } else if (event.keyCode === 81){
        console.log("Q key down");
        this.drawIFCWalls();
        return;
      }
    },
