    floorSplitFactor: function (newvalue, prevvalue){
      store.state.bv.animate = true;
      console.log("Detected splitfactor change...", newvalue, " : ", prevvalue);
      var buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      if(buildingbox){
        for( var i = buildingbox.children.length - 1; i >= 0; i--) { // Note that buildingbox.children changes every time you call .remove
            console.log("Found a child to move..");
            const heightAboveGround = bmu.getMeshHeightAboveGround(buildingbox.children[i]).min;
            let factor = 3;
            if(newvalue>prevvalue){
              factor = 4;
              if(buildingbox.children[i]){
                const currentMesh = buildingbox.children[i];
                const newPosition = {x:0, y:heightAboveGround*factor, z:0};
                const currentPosition = {x:currentMesh.position.x, y:currentMesh.position.y, z:currentMesh.position.z};
                const tween = new store.state.bv.TWEEN.Tween(currentPosition) // Create a new tween that modifies 'coords'.
                  .to({y: newPosition.y}, 1500) // Move to (300, 200) in 1 second.
                  .easing(store.state.bv.TWEEN.Easing.Quadratic.Out) // Use an easing function to make the animation smooth.
                  .onUpdate(() => {
                    currentMesh.position.y = currentPosition.y;
                  })
                  .onComplete(function() {
                    setTimeout(function() { store.state.bv.animate = false; }, 1000);
                  })
                  .start()
              }
            } else {
              factor = 0.25;
              if(buildingbox.children[i]){
                const currentMesh = buildingbox.children[i];
                const newPosition = {x:0, y:-(heightAboveGround*factor), z:0};
                const currentPosition = {x:currentMesh.position.x, y:currentMesh.position.y, z:currentMesh.position.z};
                const tween = new store.state.bv.TWEEN.Tween(currentPosition) // Create a new tween that modifies 'coords'.
                  .to({y: newPosition.y}, 1500) // Move to (300, 200) in 1 second.
                  .easing(store.state.bv.TWEEN.Easing.Quadratic.Out) // Use an easing function to make the animation smooth.
                  .onUpdate(() => {
                    currentMesh.position.y = currentPosition.y;
                  })
                  .onComplete(function() {
                    setTimeout(function() { store.state.bv.animate = false; }, 1000);
                  })
                  .start()
              }
            }
        }
      }
    },
