<template>
  <v-card>
    <v-toolbar
      dark
      color="primary"
    >
      <v-btn
        icon
        dark
        @click="closeview"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>{{ $t("text_room_details")}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn
          dark
          text
          @click="getRoomDetails"
        >
          Reload
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-container style="align-items: left; background-color: white; border: 0px; margin: 0px;" fluid>
      <v-row no-gutters>
      <v-card class="pa-2" style="margin-bottom: 10px; margin-right: 10px;">
          <v-list-item three-line>
            <v-list-item-content style="max-width: 350px;">
              <v-list-item-title class="roomtitle" v-text="roomName"></v-list-item-title>
              <v-list-item-subtitle class="roomuri" v-text="roomURI"></v-list-item-subtitle>
              <v-simple-table dense class="simpletable">
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td><b>Navn:</b></td>
                      <td>{{ roomName }}</td>
                    </tr>
                    <tr>
                      <td><b>RomNr:</b></td>
                      <td>{{ roomNum }}</td>
                    </tr>
                    <tr>
                      <td><b>Bygg:</b></td>
                      <td>{{ building }}</td>
                    </tr>
                    <tr>
                      <td><b>Etasje:</b></td>
                      <td>{{ storey }}</td>
                    </tr>
                    <tr>
                      <td><b>Hovedfunksjon:</b></td>
                      <td>{{ mainfunction }}</td>
                    </tr>
                    <tr>
                      <td><b>Delfunksjon:</b></td>
                      <td>{{ partfunction }}</td>
                    </tr>
                    <tr>
                      <td><b>Type:</b></td>
                      <td>{{ renttype }}</td>
                    </tr>
                    <tr>
                      <td><b>Utleieareal:</b></td>
                      <td>{{ rentarea+" \u33A1" }}</td>
                    </tr>
                    <tr>
                      <td><b>Fri Andel:</b></td>
                      <td>{{ freesharepercent+" %" }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-list-item-content>

            <v-list-item-avatar
              tile
              size="100"
            ><img src='../assets/room.png'>
            </v-list-item-avatar>
          </v-list-item>
        </v-card>

        <v-card class="pa-2" style="margin-bottom: 10px; margin-right: 0px;">
          Fordeling av kontrakter<br>
          <Pie
            :chart-options="chartOptions"
            :chart-data="chartData"
            :chart-id="chartId"
            :dataset-id-key="datasetIdKey"
            :plugins="plugins"
            :css-classes="cssClasses"
            :styles="styles"
            :width="width"
            :height="height"
          />
        </v-card>

        <v-card class="pa-2" style="margin-bottom: 10px; margin-right: 10px;">
          <v-list-item three-line>
            <v-list-item-content style="max-width: 350px;">
              <v-list-item-title class="roomtitle" v-text="$t('text_room_geometry')"></v-list-item-title>
              <v-simple-table dense class="simpletable">
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td><b>{{ $t('text_netarea') }}</b></td>
                      <td>{{ netarea }}</td>
                    </tr>
                    <tr>
                      <td><b>{{ $t('text_netarea_calculated') }}</b></td>
                      <td>{{ netareacalculated }}</td>
                    </tr>
                    <tr>
                      <td><b>{{ $t('text_grossarea') }}</b></td>
                      <td>{{ grossarea }}</td>
                    </tr>
                    <tr>
                      <td><b>{{ $t('text_doors') }}</b></td>
                      <td>{{ doors }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-list-item-content>

            <v-list-item-avatar
              tile
              size="100"
            ><img src='../assets/geometry2.png'>
            </v-list-item-avatar>
          </v-list-item>
        </v-card>

        </v-row>
       </v-container>

      <div class="inputwrapper">
        <h2>{{ $t('text_contracts') }}</h2>
       <hr class="hr1"/><br>
      <v-card>
      <v-card-title>
        <v-text-field
          v-model="contractSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="contractTableHeaders"
        :items="contractTableList"
        :search="contractSearch"
        flat
        class="elevation-0"
        item-key="contractid"
        v-model="contractTableSelectedRow"
        show-select
      ></v-data-table>
      </v-card>
      </div>

  </v-card>

</template>


<script>

const N3 = require('n3');
const n3store = new N3.Store();
const $rdf = require('rdflib');
import store from '../store';
import router from '../router';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import so from './util-s.js';
import u from './util.js';
import ur from './util-rdf.js';
import bmu from './BIMviewer-utils.js';
import ifcu from './BIMviewer-ifc-utils.js';
require('./tailwind.min.css');
import { Pie } from 'vue-chartjs/legacy';
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
  Plugin,
Chart
} from 'chart.js';

const rd = {}

ChartJS.register(Title, Tooltip, Legend, ArcElement, CategoryScale)

export default {
  id: rd,
  name: 'RoomDetails',
  props: {
    chartId: {
      type: String,
      default: 'pie-chart'
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 460
    },
    height: {
      type: Number,
      default: 310
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {}
    },
    plugins: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      documenturl: '',
      roomURI: "",
      roomName: "",
      roomNum: "",
      building: "",
      storey: "",
      mainfunction: "",
      partfunction: "",
      renttype: "",
      rentarea: "",
      freesharepercent: "",
      contractSearch: "",
      netarea: "",
      netareacalculated: "",
      grossarea: "",
      doors: "",
      contractTableHeaders: [
        {
          text: '#',
          align: 'start',
          filterable: false,
          value: 'contractid',
        },
        { text: this.$t('text_owner'), value: 'owner' },
        { text: this.$t('text_contract_nr'), value: 'contractnr' },
        { text: this.$t('text_contract_type'), value: 'type' },
        { text: this.$t('text_contractor'), value: 'contractor' },
        { text: this.$t('text_from'), value: 'from' },
        { text: this.$t('text_to'), value: 'to' },
        { text: this.$t('text_rented_area'), value: 'area' },
        { text: this.$t('text_share'), value: 'share' }
      ],
      contractTableList: [
        {contractid: 1, owner: "Helgelandssykehuset HF", contractnr: "2016001", type: "Standard", contractor: "Bildediagnostikk", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}, 
        {contractid: 2, owner: "Helgelandssykehuset HF", contractnr: "2016002", type: "Standard", contractor: "Medisinsk klinikk", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}, 
        {contractid: 3, owner: "Helgelandssykehuset HF", contractnr: "2016003", type: "Standard", contractor: "NH-, kjeve og yesykdommer", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}, 
        {contractid: 4, owner: "Helgelandssykehuset HF", contractnr: "2016004", type: "Standard", contractor: "Nevroklinikken", from: "4/1/2022", to: "", area: "0.3 m2", share: 12, color: "B"}, 
        {contractid: 5, owner: "Helgelandssykehuset HF", contractnr: "2016005", type: "Standard", contractor: "Kliniske servicefunksjoner", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}, 
        {contractid: 6, owner: "Helgelandssykehuset HF", contractnr: "2016006", type: "Standard", contractor: "Fysikalsk medisin og rehab.", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}, 
        {contractid: 7, owner: "Helgelandssykehuset HF", contractnr: "2016007", type: "Standard", contractor: "Mat og Cafe", from: "4/1/2022", to: "", area: "0.87 m2", share: 4.5}, 
        {contractid: 8, owner: "Helgelandssykehuset HF", contractnr: "2016008", type: "Standard", contractor: "Klinikk for anestesi og intensivmedisin", from: "4/1/2022", to: "", area: "0.87 m2", share: 12}

      ],
      contractTableSelectedRow: [],
      colorSet: ['#3cb44b', '#ffe119', '#4363d8', '#f58231', '#911eb4', '#46f0f0', '#f032e6', '#bcf60c', '#fabebe', '#008080', '#e6beff', '#9a6324', '#fffac8', '#800000', '#aaffc3', '#808000', '#ffd8b1', '#000075', '#808080'],
      chartData: {
        labels: ["Half", "And"],
        datasets: [
          {
            backgroundColor: ['#3cb44b', '#ffe119'],
            data: [50, 50]
          }
        ]
      },
      chartOptions: {
        responsive: false,
        maintainAspectRatio: true
      }
    }
  },
  components: {
    Pie
  },
  methods: {
    closeview(){
      console.log("Close view");
      store.state.cp.roomDetailURI = "";
    },
    async getRoomDetails(){
      console.log("roomDetailURI: "+store.state.cp.roomDetailURI)
      this.roomURI = store.state.cp.roomDetailURI;
      let roomTAG = "";
      store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.TAG("tag"), null).forEach(st => {
          roomTAG = st.object.value;
      });
      store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.RDFS("label"), null).forEach(st => {
        this.roomName = u.decodeCharacters(st.object.value);
      });
      this.roomNum = this.roomName.replace("Rom ", "").replace("Room ", "");
      store.state.rs.rdfStore.match(null, ns.DUL("hasPart"), $rdf.sym(this.roomURI)).forEach(st => {
        if(store.state.rs.rdfStore.match($rdf.sym(st.subject.value), ns.RDF("type"), ns.BOT("Storey")).length >= 0){
          store.state.rs.rdfStore.match($rdf.sym(st.subject.value), ns.RDFS("label"), null).forEach(st => {
            this.storey = st.object;
          });
        }
      });
      // Get room functions
      let partfunctions = store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.NEO("b_capable"), null);
      for(let p=0; p<partfunctions.length; p++){
        // Fetch the relation between partfunction and mainfunction
        let fetchrooms = await ur.fetchAndSaveTurtle(partfunctions[p].object.value, true).then((successBool) => {
          return successBool;
        });
        if(fetchrooms){}
        // Get the partfunction label
        store.state.rs.rdfStore.match($rdf.sym(partfunctions[p].object.value), ns.RDFS("label"), null).forEach(st => {
          this.partfunction = st.object.value.replace("Delfunksjon: ", "");
        });
        // Get the mainfunction label
        store.state.rs.rdfStore.match($rdf.sym(partfunctions[p].object.value), ns.NEO("t_genus"), null).forEach(st => {
          store.state.rs.rdfStore.match($rdf.sym(st.object.value), ns.RDFS("label"), null).forEach(st => {
            this.mainfunction = st.object.value.replace("Hovedfunksjon: ", "");
          });
        });
      }

      // Get building name
      let buildingURI = so.removeCharactersAfter(this.roomURI, "/node")+"/i";
      store.state.rs.rdfStore.match($rdf.sym(buildingURI), ns.FOAF("name"), null).forEach(st => {
          this.building = st.object.value;
      });

      // Get storey name
      let storeyTAG = "";
      storeyTAG = roomTAG.replace("."+so.getCharactersAfter(roomTAG, "."), ""); // NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG.E01
      let buildingTAG = storeyTAG.replace("."+so.getCharactersAfter(storeyTAG, "."), ""); // NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG
      const storeyTAGPart = so.getCharactersAfter(storeyTAG, "."); // E01
      this.storey = storeyTAGPart.replace("E", "");
      // Get real storey name if this works:
      store.state.rs.rdfStore.match(null, ns.SIO("SIO_000202"), $rdf.sym(this.roomURI)).forEach(st => {
        if(store.state.rs.rdfStore.match($rdf.sym(st.subject.value), ns.RDF("type"), ns.BOT("Storey"))>0){
          store.state.rs.rdfStore.match($rdf.sym(st.subject.value), ns.RDFS("label"), null).forEach(st => {
            this.storey = st.object.value;
          });
        }
      });

      // Caclulate room geometry
      store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.IFOUR("m_polyline"), null).forEach(st => {
        const polyline = st.object.value;
        const calculatedArea = bmu.trinagulateAndCalculateArea(polyline);
        this.netareacalculated = ""+calculatedArea;
      });

      // Get net area
      this.netarea = "0";
      store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.NEO("m_area-net"), null).forEach(st => {
        this.netarea = st.object.value;
      });

      // Get gross area
      this.grossarea = "0";
      store.state.rs.rdfStore.match($rdf.sym(this.roomURI), ns.NEO("m_area-gross"), null).forEach(st => {
        this.grossarea = st.object.value;
      });

      console.log("Found in Json:");
      //let storeys = Object.values(store.state.bv.IFCPropertyJson).filter(item => item.type === 'IFCBUILDINGSTOREY');

      ifcu.clearContainValues();
      //let storeys = ifcu.ContainsKeyValue(store.state.bv.IFCPropertyJson, "Name", 'N140');
      //let storeys = ifcu.ContainsKeyValue(store.state.bv.IFCPropertyJson, "expressID", 814530);
      let storeys = ifcu.ContainsKeyValue(store.state.bv.IFCPropertyJson, "expressID", 814526);
      console.log(storeys);
      

      this.renttype = "Leie plass";
      this.rentarea = 6.73;

      this.setSharePercentage();
      this.setDiagramValues();
      //this.width = 462;
      //this.height = 311;
    },
    setSharePercentage(){
      let usedPercentages = 0;
      this.contractTableList.forEach((contract, cnum) => {
          if(contract.owner == "Ingen") {
            this.contractTableList.splice(cnum, 1); // Remove from array
          } else {
            contract.area = ((this.rentarea*contract.share)/100).toFixed(2);
            usedPercentages = usedPercentages+contract.share;
          }
      });
      this.freesharepercent = (100-usedPercentages).toFixed(1);
      this.contractTableList.push({contractid: 0, owner: "Ingen", contractnr: "", type: "", contractor: "Ikke utleid", from: "", to: "", area: ((this.rentarea*this.freesharepercent)/100).toFixed(2), share: this.freesharepercent, color: "B"});
    },
    setDiagramValues(){
      this.chartData.labels = [];
      this.chartData.datasets[0].data = [];
      this.chartData.datasets[0].backgroundColor = [];
      this.contractTableList.forEach((contract, cnum) => {
        this.chartData.labels.push(contract.contractor);
        this.chartData.datasets[0].data.push(contract.area);
        if(contract.owner == "Ingen"){
          this.chartData.datasets[0].backgroundColor.push('#ffffff');
        } else {
          this.chartData.datasets[0].backgroundColor.push(this.colorSet[cnum]);
        }
      });
    },
    reloadview(){
      this.getRoomDetails();
    }
  },
  computed: {

  },
  watch: {
    "$store.state.cp.roomDetailURI": function (val) {
        this.reloadview();
    },
  },
  mounted(){
      this.documenturl = this.$route.query.select;
      if(!this.$route.query.location){
        this.$router.push({path: this.$route.path, query: {select: this.documenturl} });
      }
      this.getRoomDetails();
      this.setSharePercentage();
      this.setDiagramValues();
  },
  created(){

  }
};
</script>

