function prettyURI(st) {
  let s = st.replace(/\s/g, "");
  return encodeURI(s);
}

function arr2exists(arr, search) {
    return arr.some(row => row.includes(search));
    /*let found = false;
    arr.forEach((item, i) => {
      if(item[0] === search) { found = true; }
    });
    return found;*/
}

function addToStores(subject, predicate, object){
  tempRdfStore.add(subject, predicate, object, $rdf.default);
  store.state.rs.rdfStore.add(subject, predicate, object, $rdf.default);
}

function createSiteSystemQuads(lineDetails, circulationArray, geometryArray){
  let sFacility = "";

  lineDetails.forEach((element, i) => {
    let el = element.split(":", 2);
  });

  // Site circulation system ----------
  let siteCirculationBlank = u.getBlankNode("Site circulation "+sFacility)
  if(!store.state.blankExisted){
    addToStores(siteCirculationBlank, ns.RDF("type"), ns.NEO("y_system"));
    addToStores(siteCirculationBlank, ns.TAG("tag"), "SiteCirculationSystem");
    addToStores(siteCirculationBlank, ns.RDFS("label"), $rdf.literal("Site circulation "+sFacility, 'en'));
    addToStores(siteCirculationBlank, ns.RDFS("label"), $rdf.literal("Omrde sirkulasjon "+sFacility, 'no'));
    addToStores(siteCirculationBlank, ns.RDFS("comment"), "Circulation system for the Site: "+sFacility);
    addToStores($rdf.sym(siteURI), ns.SIO("SIO_000202"), siteCirculationBlank);

    ur.checkIfContainLoop(siteCirculationBlank.value, containsTypes);
  }
  if(!arr2exists(systemURIs, "SiteCirculationSystem")){
    systemURIs.push(["SiteCirculationSystem", ""]);
  }
  return true;
}

function createBuildingSystemQuads(lineDetails, circulationArray, geometryArray){
  let tripleArray = [];
  let sRHF = "";
  let sHF = "";
  let sFacility = "";
  let sBuilding = "";
  let sStorey = "";
  let sWing = "";
  let sBuildingCode = "";

  lineDetails.forEach((element, i) => {
    let el = element.split(":", 2);
    if(el[0] == "RHF"){ sRHF = el[1]; }
    if(el[0] == "HF"){ sHF = el[1]; }
    if(el[0] == "Lokasjon"){ sFacility = el[1]; }
    if(el[0] == "Bygg"){ sBuilding = el[1]; }
    if(el[0] == "Etasje"){ sStorey = el[1]; }
    if(el[0] == "Fly"){ sWing = el[1]; }
  });

  let locationTAG = "NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG";
  let wingTAG = ".FLOY_"+so.getCharactersBefore(sWing, " - ");
  let sstoreyTAG = ".E"+so.getCharactersBefore(sStorey, " - ");

  // Building circulation system ----------
  let buildingCirculationBlank = u.getBlankNode("Building circulation "+sBuilding)
  if(!store.state.blankExisted){
    addToStores(buildingCirculationBlank, ns.RDF("type"), ns.NEO("y_system"));
    addToStores(buildingCirculationBlank, ns.TAG("tag"), "BuildingCirculationSystem");
    addToStores(buildingCirculationBlank, ns.RDFS("label"), $rdf.literal("Building circulation "+sBuilding, 'en'));
    addToStores(buildingCirculationBlank, ns.RDFS("label"), $rdf.literal("Bygning sirkulasjon "+sBuilding, 'no'));
    //addToStores(buildingCirculationBlank, ns.RDFS("label"), $rdf.literal('2016-02-29T21:44:31+00:00', '', ns.XSD('dateTime'))); // How to use XSD
    addToStores(buildingCirculationBlank, ns.RDFS("comment"), "Circulation system for the Building: "+sBuilding);
  }
  if(!arr2exists(systemURIs, "BuildingCirculationSystem")){
    systemURIs.push(["BuildingCirculationSystem", ""]);
  }

  // Wing ----------
  let wingBlank = u.getBlankNode(sWing)
  let wingLocTAG = u.prettyTAG(locationTAG+wingTAG);
  if(!store.state.blankExisted){
    if(sWing === ""){ sWing = "Ingen"; }
    addToStores(wingBlank, ns.RDF("type"), ns.NEO("y_wing"));
    addToStores(wingBlank, ns.TAG("tag"), wingLocTAG);
    addToStores(wingBlank, ns.RDFS("label"), $rdf.literal(sWing, 'en'));
    //addToStores(buildingURI, ns.DUL("hasPart"), wingBlank);
  }
  if(!arr2exists(systemURIs, wingLocTAG)){
    systemURIs.push([wingLocTAG, ""]);
  }

  // Storey ----------
  let storeyBlank = u.getBlankNode(sStorey+wingTAG)
  let storeyTAG = u.prettyTAG(locationTAG+wingTAG+sstoreyTAG);
  if(!store.state.blankExisted){
    addToStores(storeyBlank, ns.RDF("type"), ns.BOT("Storey"));
    addToStores(storeyBlank, ns.TAG("tag"), storeyTAG);
    addToStores(storeyBlank, ns.RDFS("label"), $rdf.literal(sStorey, 'en'));
    addToStores(wingBlank, ns.SIO("SIO_000202"), storeyBlank);
    ur.checkIfContainLoop(storeyBlank.value, containsTypes);
    console.log("Storey: "+sStorey);
  }
  if(!arr2exists(systemURIs, storeyTAG)){
    systemURIs.push([storeyTAG, ""]);
  }

  // Floor - Combination between storey and wing ----------
  /*let floorBlank = u.getBlankNode(sStorey+sWing)
  let wingFloorTAG = u.prettyTAG(locationTAG+wingTAG+"_"+floorTAG.replace(".", ""));
  if(!store.state.blankExisted){
    addToStores(floorBlank, ns.RDF("type"), ns.NEO("s_floor"));
    addToStores(floorBlank, ns.TAG("tag"), wingFloorTAG);
    addToStores(floorBlank, ns.RDFS("label"), $rdf.literal("Floorarea: "+sStorey+sWing, 'en'));
    addToStores(floorBlank, ns.RDFS("label"), $rdf.literal("Etasjedel: "+sStorey+sWing, 'no'));
    addToStores(storeyBlank, ns.SIO("SIO_000202"), floorBlank);
    addToStores(wingBlank, ns.SIO("SIO_000202"), floorBlank);
    ur.checkIfContainLoop(floorBlank.value, containsTypes);
  }
  if(!arr2exists(systemURIs, wingFloorTAG)){
    systemURIs.push([wingFloorTAG, ""]);
  }
  */


  return true;
}

function findTagById(source, id) {
  for (var i = 0; i < source.length; i++) {
    if (source[i].id === id) {
      return source[i].tag;
    }
  }
  console.log("Did not find: "+id);
  return "UNDEFINED";
}

function findTagByNum(source, id) {
  for (var i = 0; i < source.length; i++) {
    if (source[i].sysid === id) {
      console.log("Did successfully find: "+id);
      return source[i].tag;
    }
  }
  console.log("Did not find: "+id);
  return "UNDEFINED";
}

