function createHospitalFunctions(){
  let functionTAG = "NMH.F.SYKEHUS.";

  let hospitalFunctionBlank = u.getBlankNode("HospitalRoomFunctions")
  if(!store.state.blankExisted){
    addToStores(hospitalFunctionBlank, ns.NEO("t_genus"), ns.NEO("f_hospital"));
    addToStores(hospitalFunctionBlank, ns.TAG("tag"), "HospitalRoomFunctions");
    addToStores(hospitalFunctionBlank, ns.RDFS("label"), $rdf.literal("Hospital functions", 'en'));
    addToStores(hospitalFunctionBlank, ns.RDFS("label"), $rdf.literal("Sykehus funksjoner", 'no'));
  }
  if(!arr2exists(systemURIs, "HospitalRoomFunctions")){
    systemURIs.push(["HospitalRoomFunctions", ""]);
  }

  mfTagList.forEach(mf => {
    // System: 1A ----------
    const mainFunctionBlank = u.getBlankNode("System: "+mf.tag)
    const mainFunctionTag = u.prettyTAG(functionTAG+mf.tag);
    if(!store.state.blankExisted){
      addToStores(mainFunctionBlank, ns.TAG("tag"), mainFunctionTag);
      addToStores(mainFunctionBlank, ns.RDFS("label"), $rdf.literal("Hovedfunksjon: "+mf.sysid+" - "+mf.id, 'en'));
      addToStores(mainFunctionBlank, ns.NEO("t_genus"), hospitalFunctionBlank);
    }
    if(!arr2exists(systemURIs, mainFunctionTag)){
      systemURIs.push([mainFunctionTag, ""]);
    }
  });

  pfTagList.forEach(pf => {
    // System: 1A.9 ----------
    const mainFunctionBlank = u.getBlankNode("System: "+so.getCharactersBefore(pf.tag, "."));
    const partFunctionBlank = u.getBlankNode("System: "+pf.tag)
    const partFunctionTag = u.prettyTAG(functionTAG+pf.tag);
    if(!store.state.blankExisted){
      addToStores(partFunctionBlank, ns.TAG("tag"), partFunctionTag);
      addToStores(partFunctionBlank, ns.RDFS("label"), $rdf.literal("Delfunksjon: "+pf.sysid+" - "+pf.id, 'en'));
      addToStores(partFunctionBlank, ns.NEO("t_genus"), mainFunctionBlank);
    }
    if(!arr2exists(systemURIs, partFunctionTag)){
      systemURIs.push([partFunctionTag, ""]);
    }
 });
}

async function fetchAllStoreyURIs(){
  let locationTAG = "NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG";
  let storeySearch = await r.searchAndGetURIs("#"+locationTAG+".FLOY*");
  const floorURIs = [];
  if(storeySearch){}
  store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.BOT("Storey")).forEach(item => {
    if(!floorURIs.includes(item.subject.value)){
      console.log("Found storey: " + item.subject.value);
      floorURIs.push(item.subject.value);
    }
  });
  // Fetch all storey data
  for(let i=0; i<floorURIs.length; i++) {
    let res = await ur.fetchAndSaveTurtle(floorURIs[i], false).then((response) => {});
  }
  return floorURIs;
}

async function createStoreyContainsRoomQuads(lineDetails){
  let floorURIs = await fetchAllStoreyURIs();
  if(floorURIs) console.log("Storeys fetched.");
  for( var floorCount=0; floorCount<floorURIs.length; floorCount++){ // for( var floorCount=0; floorCount<floorURIs.length; floorCount++){
    for (var rcount=0; rcount<lineDetails.length; rcount++) {
      let sStorey = "";
      let sWing = "";
      let sRoomid = "";
      lineDetails[rcount].forEach((element, i) => {
        let el = element.split(":", 2);
        if(el[0] == "Etasje"){ sStorey = el[1]; }
        if(el[0] == "Rom ID"){ sRoomid = el[1]; }
        if(el[0] == "Fly"){ sWing = el[1]; }
      });
      let locationTAG = "NMH.Y.SYKEHUS.HN.H.MOIRANA.SJOFORSGATA.HOVEDBYGG";
      let wingTAG = ".F"+so.getCharactersBefore(sWing, " - ");
      let wingTAGfull = ".FLOY_"+so.getCharactersBefore(sWing, " - ");
      let sstoreyTAG = ".E"+so.getCharactersBefore(sStorey, " - ");
      let roomTAG = u.prettyTAG(locationTAG+wingTAG+sstoreyTAG+"."+encodeURI(sRoomid));
      let storeyTAG = u.prettyTAG(locationTAG+wingTAGfull+sstoreyTAG);
      let storeyURI = "";
      let roomURI = "";
      // Get storey URI
      //let storeyData = await id.searchAndGetURIs(storeyTAG);
      let tagitem = store.state.rs.rdfStore.match(null, ns.TAG("tag"), storeyTAG)[0];
      if(tagitem) {
        storeyURI = tagitem.subject.value;
      } else {
        console.log("Warning, did not find Storey URI for tag: "+storeyTAG);
      }
      if (!storeyURI) continue;
      if (storeyURI!=floorURIs[floorCount]) continue;
      // Get room URI
      let roomData = await id.searchAndGetURIs(roomTAG);
      if(roomData){}
      let tagitem2 = store.state.rs.rdfStore.match(null, ns.TAG("tag"), roomTAG)[0]
      if(tagitem2){
        console.log("Room URI for tag: "+roomTAG)
        console.log(tagitem2.subject.value)
        roomURI = tagitem2.subject.value;
        // Fetch storey URI to check if triple already exist
        if(!store.state.rs.rdfStore.match($rdf.sym(storeyURI), ns.SIO("SIO_000202"), $rdf.sym(roomURI))[0]){
          tempRdfStore.add($rdf.sym(storeyURI), ns.SIO("SIO_000202"), $rdf.sym(roomURI), $rdf.default);
        }
      }
    }
    // Create turtle file from store
    let turtle = u.storeToTurtle(tempRdfStore, buildingPODurl);
    turtle = ur.modifyTurtle(turtle);
    console.log(turtle);

    // Upload the turtle
    if (window.solid.session.info.isLoggedIn && turtle!=""){
      let uploadPromise = await ur.uploadTurtle(buildingURI, turtle, tst);
      if(uploadPromise) console.log("Turtle should have been uploaded...");
      tempRdfStore = $rdf.graph();
    } else {
      console.log("Is logged out");
    }
  }
}

