id.searchAndGetURIs = async function(searchWord){
  let uri = store.state.webId.replace("/i", "/search/")+encodeURIComponent(searchWord);
  try {
    let res = await window.solid.session.fetch(
      uri, {
        method: "GET",
        credentials: "include",
        headers: {
          "Accept": "text/turtle"
        },
    }).then(res => res.text());
    $rdf.parse(res, store.state.rs.rdfStore, uri, 'text/turtle');
    store.state.rs.rdfStore.match(null, ns.TAG("tag"), null).forEach(item => {
        //console.log("Found "+searchWord+" uri: " + item.subject.value);
        for(var i = 0; i < systemURIs.length; i++) {
          if(systemURIs[i][0] == item.object.value){
            systemURIs[i][1] = item.subject.value;
          }
        }
    });
    return res;
  } catch (error) {
    console.log(error);
    console.log("Search failed");
  }
}

