async function uploadSystems(allText, circulationArray, geometryArray) {
    var allTextLines = allText.split(/\r\n|\n/);
    var headers = allTextLines[0].split(';');
    var lines = [];

    for (var i=1; i<allTextLines.length; i++) {
        var data = allTextLines[i].split(';');
        if (data.length == headers.length) {

            var tarr = [];
            for (var j=0; j<headers.length; j++) {
                tarr.push(headers[j]+":"+data[j]);
            }
            lines.push(tarr);
        }
    }

    // ---- Code for creating system triples - START ----------
    let siteURIfetch = await ur.fetchAndSaveTurtle(siteURI);
    let buildingURIfetch = await ur.fetchAndSaveTurtle(buildingURI);
    let facilityURIfetch = await ur.fetchAndSaveTurtle(ministryURI);
    console.log(siteURIfetch+buildingURIfetch+facilityURIfetch);
    for (var i = 0; i < lines.length; i++) {
      createSiteSystemQuads(lines[i], circulationArray, geometryArray);
    };
    var turtle = u.storeToTurtle(tempRdfStore, buildingPODurl);
    turtle = ur.modifyTurtle(turtle);
