id.uploadBatch = async function(lines, circulationArray, geometryArray, uploadedcount, batchSize, uploadLimit){
  //tempRdfStore.match(null, null, null).forEach(st => {
    //console.log("Adding triple: ", st);
    //store.state.rs.rdfStore.add(st);
  //});
  tempRdfStore = $rdf.graph(); // Clear the  temp store
  let quads = [];
  console.log("Uploading rooms "+uploadedcount+"-"+(uploadedcount+batchSize)+"!");
  let uploadToCount = uploadedcount+batchSize;
  if(uploadToCount >= lines.length){ // Make sure not to try to upload something outside array
    uploadToCount = lines.length;
  }
  for (var i = uploadedcount; i < uploadToCount; i++) {
    const created = await createRoomQuads(lines[i], circulationArray, geometryArray);
  };
  var turt = id.quadsToTurtle(quads);
  turt = ur.modifyTurtle(turt);
  console.log(turt);

  // Test code
  /*uploadedcount = uploadedcount+batchSize;
  if(uploadedcount <= uploadLimit){
    id.uploadBatch(lines, circulationArray, geometryArray, uploadedcount, batchSize, uploadLimit);
  }*/

  if (window.solid.session.info.isLoggedIn){
      //if(ur.checkIfContainLoopInStore(store.state.rs.rdfStore, false)) return;
      //if(ur.checkIfmPortLoopInStore(store.state.rs.rdfStore)) return;
      ur.uploadTurtle(buildingURI, turt, tst).then((successfulFetch) => {
        uploadedcount = uploadedcount+batchSize;
        console.log(uploadedcount+" rooms should be uploaded..");
        if(uploadedcount <= uploadLimit && successfulFetch){
          id.uploadBatch(lines, circulationArray, geometryArray, uploadedcount, batchSize, uploadLimit);
        }
      });
  } else {
    console.log("Is logged out");
  }
}

function createRelativeNodes(turt){
  turt = turt.replace(/shbsand:\w+/g, '<\$&>'); // Regex replace \$& is the whole found match
  return turt.replaceAll('<shbsand:', '<#');
}

id.checkIfAppAuthenticated = async function(containerName) {
  // This functionality will ask for app authorization for the linked pod - Organizational POD - When using persional pod as login
  let currentListUrl = buildingPODurl + "/" + encodeURI(containerName); // Very important to add the / in end of url - Espen
  try {
    let res = await window.solid.session.fetch(
      buildingPODurl, {
        method: "HEAD",
        credentials: "include"
    });
    console.log("-- Auth response below --");
    console.log(res);
    var appAuthReq = res.headers.get('App-Authorization-Required');
    var status = res.status;
    if (status === 401 && appAuthReq){
      console.log("Redirecting..");
      window.location.href = appAuthReq + '&redirect_uri=' + window.location.href;
    }
  } catch (error) {
    console.log(error);
  }
}


