<template>
  <div class="loginwrapper">
    <div class="loginview">
          <ul>
            <v-container center>
          <br><v-img contain lazy-src="./img/osv-logo.png" max-height="80" src="./img/logo.svg"/><br>
        </v-container>
        <v-progress-circular v-if="progressvisible" indeterminate color="primary"></v-progress-circular>
          <div>
            <v-btn v-if="!progressvisible" color="primary" levation="2" large name=" btnLogin" @click="triggerlogin">{{ loginButtonText }}</v-btn>
          </div>
          <!--<router-link to="/feedback">Feedback</router-link>-->
          </ul>
    </div>
  </div>
</template>

<script>

const $rdf = require('rdflib');
import store from '../store';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import moment from 'moment';
import so from './util-s.js';

let NOT_ENTERED_WEBID = "...not logged in yet - but enter any WebID to read from its profile...";

import { Session } from '@inrupt/solid-client-authn-browser';
window.solid = {};
window.solid.session = new Session();
window.solidFetcher = window.solid.session.fetch;

var loginButtonTextString = 'Login';
var userTextString = '';

const l = {}

async function login() {
  if (
    !window.solid.session.info.isLoggedIn &&
    !new URL(window.location.href).searchParams.get("code")
  ) {
    await window.solid.session.login({
      oidcIssuer: store.state.server,
      //oidcIssuer: "https://inrupt.net",
      clientName: "sykehusbygg-new",
      redirectUrl: window.location.href
    }).then((res) => {
      console.log("Logged in..");
      store.state.isLoggedIn = true;
      handleRedirectAfterLogin();
    });
  }
}

async function handleRedirectAfterLogin() {
  console.log("Handle redirect..");
  await window.solid.session.handleIncomingRedirect({
      url: window.location.href,
      restorePreviousSession: true
    })
      .then(sessionInfo => {
        l.checkIfLoggedIn(sessionInfo);
      }).finally(() => {
        console.log("Redirect finished do request here..");
      });
}

// 2. Create the Reading List
async function fetchGETURL(webId) {
  let res = [];
  try {
    res = await window.solid.session.fetch(
      webId, {
        method: "GET",
        credentials: "include",
        headers: {
          "Accept": "text/turtle"
        },
    }).then(res => res.text());
    return res;
  } catch (error) {
    console.log(error);
  }
}

l.checkIfLoggedIn = async function (sessionInfo){
  console.log(sessionInfo);
  if (sessionInfo){
    if (sessionInfo.isLoggedIn){
      console.log("Is logged in");
      loginButtonTextString = 'Log out';
        store.state.isLoggedIn = true;
        store.state.webId = sessionInfo.webId;
        if(store.state.webId.includes("https://espen")) store.state.devMode = true;
        store.state.loginExpire = moment(sessionInfo.expirationDate).format('MMMM Do YYYY, HH:mm:ss');
        fetchGETURL(sessionInfo.webId).then((userData) => {
          var graph = new $rdf.IndexedFormula();
          $rdf.parse(userData, graph, sessionInfo.webId, 'text/turtle', (err, graph) => {
            graph.match($rdf.sym(sessionInfo.webId), ns.FOAF("name"), null).forEach(st => {
              console.log("Username: "+st.object.value);
              store.state.loggedInName = st.object.value;
              store.state.loggedInInitials = so.getInitials(st.object.value);
            });
          });
        });
    } else {
      console.log("Is logged out");
      loginButtonTextString = 'Login';
      store.state.isLoggedIn = false;
    }
  } else {
    loginButtonTextString = 'Login';
    store.state.isLoggedIn = false;
    throw console.error("Session object is not found!");
  }
}

export default {
  l: l,
  name: 'TrinityLogin',
  data() {
    return {
      progressvisible: true,
      loggedin: false,
      loginButtonText: loginButtonTextString,
      userText: userTextString
      }
  },
  watch: {
    // watching top-level property
    loginButtonText(val, oldVal) {
      console.log(`new: ${val}, old: ${oldVal}`)
    }
  },
  methods: {
    doRoute(){
      this.$router.push({ path: '/podselect' })
      //this.$router.push({ path: '/addcustomer' })
    },
    triggerlogin(){
      this.loginButtonText = 'Logging in, please wait..'; // Does not work
      login();
    },
    checklogin(){
      console.log("SessionInfo:");
      console.log(window.solid.session.info);
      if (window.solid.session.info.isLoggedIn){
        console.log("Test logged in");
        this.loginButtonText = "Log out";
        this.doRoute();
      } else {
        console.log("Test logged out");
        this.progressvisible = false;
        this.loginButtonText = "Login";
      }
    }
  },
  created(){
      //this.interval = setInterval(() => this.checklogin(), 10000);

      //setTimeout(this.checklogin(), 5000);
      /*if (window.sessionStorage.getItem("trinityToken")){
        window.solid.session = JSON.parse(window.sessionStorage.getItem("trinityToken"));
      }*/
      //setTimeout(this.doRoute(), 15000);
  },
  mounted() {
    handleRedirectAfterLogin();
    setTimeout(() => this.checklogin(), 5000);
  }
};



</script>

