import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Logout from '../views/Logout.vue'
import About from '../views/About.vue'
import ImportData from '../views/ImportDataV.vue'
import BIMviewer from '../views/BIMviewerV.vue'
import BMviewer from '../views/TSviewerV.vue'
import AssetsList from '../views/AssetsListV.vue'
import Capacity from '../views/CapacityV.vue'
import FilesLib from '../views/FilesLibV.vue'
import PodSelect from '../views/PodSelectV.vue'
import FileEdit from '../views/FileEditV.vue'
import Search from '../views/SearchV.vue'
import ImportIFC from '../views/ImportIFCV.vue'
import PDFviewer from '../views/PDFviewerV.vue'
import WorkList from '../views/WorkListV.vue'
import IFCexporter from '../views/IFCexporterV.vue'
import RentModule from '../views/RentModuleV.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/logout',
    name: 'Logout',
    component: Logout
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/importdata',
    name: 'ImportData',
    component: ImportData,
    props: true
  },
  {
    path: '/bimviewer',
    name: 'BIMviewer',
    component: BIMviewer
  },
  {
    path: '/bmviewer',
    name: 'BMviewer',
    component: BMviewer
  },
  {
    path: '/assetslist',
    name: 'AssetsList',
    component: AssetsList
  },
  {
    path: '/fileslib',
    name: 'FilesLib',
    component: FilesLib
  },
  {
    path: '/podselect',
    name: 'PodSelect',
    component: PodSelect
  },
    {
    path: '/fileedit',
    name: 'FileEdit',
    component: FileEdit
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/capacity',
    name: 'Capacity',
    component: Capacity,
    props: true
  },
  {
    path: '/importifc',
    name: 'ImportIFC',
    component: ImportIFC,
    props: true
  },
  {
    path: '/pdfviewer',
    name: 'PDFviewer',
    component: PDFviewer,
  },
  {
    path: '/worklist',
    name: 'WorkList',
    component: WorkList,
  },
  {
    path: '/ifcexporter',
    name: 'IFCexporter',
    component: IFCexporter,
  },
  {
    path: '/rentmodule',
    name: 'RentModule',
    component: RentModule,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: '/',
  routes
})


