<template>
  <v-card>
    <v-toolbar
      dark
      color="primary"
    >
      <v-btn
        icon
        dark
        @click="closeview"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>{{ $t("text_building_details")}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-progress-circular v-if="loading" :key="loading" style="width: 25px; height: 25px; margin-right: 15px; margin-top: 20px;" indeterminate color="white"></v-progress-circular>
        <v-btn
          dark
          text
          @click="reloadview"
        >
          Reload
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-container style="align-items: left; background-color: white; border: 0px; margin: 0px;" fluid>
      <v-row no-gutters>
      <v-card class="pa-2" style="margin-bottom: 10px; margin-right: 10px;">
          <v-list-item three-line>
            <v-list-item-content style="max-width: 350px;">
              <v-list-item-title class="title" v-text="buildingName"></v-list-item-title>
              <v-list-item-subtitle class="uri" v-text="buildingURI"></v-list-item-subtitle>
              <v-simple-table dense class="simpletable">
                <template v-slot:default>
                  <tbody>
                    <tr>
                      <td><b>Matrikkelnr:</b></td>
                      <td>{{ buildingNum }}</td>
                    </tr>
                    <tr>
                      <td><b>Bygger:</b></td>
                      <td>{{ buildingYear }}</td>
                    </tr>
                    <tr>
                      <td><b>Bygningstype:</b></td>
                      <td>{{ buildingType }}</td>
                    </tr>
                    <tr>
                      <td><b>Vernestatus:</b></td>
                      <td>{{ buildingProtectionStatus }}</td>
                    </tr>
                    <tr>
                      <td><b>Adresse:</b></td>
                      <td>{{ buildingAddress }}</td>
                    </tr>
                    <tr>
                      <td><b>Nettoareal:</b></td>
                      <td>{{ buildingNetArea }}</td>
                    </tr>
                    <tr>
                      <td><b>Bruttoareal:</b></td>
                      <td>{{ buildingGrossArea }}</td>
                    </tr>
                    <tr>
                      <td><b>Antall etasjer:</b></td>
                      <td>{{ numberofstoreys }}</td>
                    </tr>
                    <tr>
                      <td><b>Antall rom:</b></td>
                      <td>{{ numberOfRooms }}</td>
                    </tr>
                    <tr>
                      <td><b>Driftsansvarlig:</b></td>
                      <td>{{ operationsManager }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-list-item-content>

            <v-list-item-avatar
              tile
              size="100"
            ><img src='../assets/building2.png'>
            </v-list-item-avatar>
          </v-list-item>
        </v-card>
        <v-card class="pa-2" style="margin-bottom: 10px; margin-right: 0px">
          <v-img
            :src="buildingImage"
            height="250"
            max-width="495px"
          ></v-img>
          <div class="mapouter"><div class="gmap_canvas"><iframe width="495" height="250" id="gmap_canvas" src="https://maps.google.com/maps?q=Helgelandsykehuset%20mo&t=&z=15&ie=UTF8&iwloc=&output=embed" frameborder="0" scrolling="no" marginheight="0" marginwidth="0"></iframe></div></div>
        </v-card>
        </v-row>
    </v-container>
      
   </v-card>
</template>


<script>

const N3 = require('n3');
const n3store = new N3.Store();
const $rdf = require('rdflib');
//const Comunica = require('@comunica/actor-init-sparql-rdfjs');
import store from '../store';
import router from '../router';
import { ns, nu, nl, neo, trinity } from './ontologies.js';
import ifcu from './BIMviewer-ifc-utils.js';
import bmu from './BIMviewer-utils.js';
import so from './util-s.js';
import ur from './util-rdf.js';
require('./tailwind.min.css');

const bd = {}

export default {
  id: bd,
  name: 'BuildingDetails',
  data() {
    return {
      documenturl: '',
      buildingURI: "",
      buildingName: "",
      buildingNum: "",
      buildingYear: "",
      buildingType: "",
      buildingImage: "",
      buildingProtectionStatus: "",
      buildingAddress: "",
      buildingNetArea: "",
      buildingGrossArea: "",
      numberofstoreys: "",
      numberOfRooms: "",
      operationsManager: "",
      loading:false,
    }
  },
  methods: {
    closeview(){
      console.log("Close view");
      store.state.cp.buildingDetailURI = "";
      this.buildingURI = "";
      this.buildingName = "";
      this.buildingNum = "";
      this.buildingYear = "";
      this.buildingType = "";
      this.buildingProtectionStatus = "";
      this.buildingAddress = "";
      this.buildingNetArea = "";
      this.buildingGrossArea = "";
      this.numberofstoreys = "";
      this.numberOfRooms = "";
      this.operationsManager = "";
    },
    async getBuildingDetails(){
      this.loading = true;
      this.buildingImage = await bmu.downloadImagefromTrinpod(store.state.cp.buildingDetailURI.replace('/i', '/home/IFC/building.jpg'));
      const jsonURI = store.state.cp.buildingDetailURI.replace("/i", "/home/IFC/building.json")
      const IFCmetadata = await ifcu.getIFCmetaDataFromJson(jsonURI); // Create function for storing in local storage
      this.buildingURI = store.state.cp.buildingDetailURI;
      store.state.rs.rdfStore.match($rdf.sym(this.buildingURI), ns.RDFS("label"), null).forEach(st => {
        if(!st.object=="") this.buildingName = st.object;
      });
      this.buildingNum = IFCmetadata.matrikkelnr;
      this.buildingYear = IFCmetadata.buildingYear;
      this.buildingType = IFCmetadata.buildingType;
      this.buildingProtectionStatus = IFCmetadata.buildingProtectionStatus;
      this.buildingAddress = IFCmetadata.buildingAddress;
      this.buildingNetArea = IFCmetadata.buildingNetArea;
      this.buildingGrossArea = IFCmetadata.buildingGrossArea;
      this.numberofstoreys = IFCmetadata.numberofstoreys;
      this.numberOfRooms = IFCmetadata.numberOfRooms;
      this.operationsManager = IFCmetadata.operationsManager;
      this.loading = false;
    },
    reloadview(){
      this.getBuildingDetails();
    }
  },
  computed: {

  },
  watch: {
    "$store.state.cp.buildingDetailURI": function (val) {
        this.reloadview();
    },
  },
  mounted(){
      this.documenturl = this.$route.query.select;
      if(!this.$route.query.location){
        this.$router.push({path: this.$route.path, query: {select: this.documenturl} });
      }
      this.getBuildingDetails();
  },
  created(){

  }
};
</script>

