const approximatelyEqual = (v1, v2, tolerance) => {
  return Math.abs(v1 - v2) < tolerance;
}

const findDuplicates = (arr, tolerance) => {
  let newArr = [];
  for (let i=0; i<arr.length-1; i++) {
    for (let j = i + 1; j < arr.length; j++) {
      if (approximatelyEqual(arr[i], arr[j], tolerance)) {
        newArr[i] = arr[i];
        newArr[100+j] = arr[j];
      }
    }
  }
  return newArr.filter(Number).sort(function(a, b){return a-b});
}

