    getRoomURIbyName(roomName){
      let roomTriple = store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal("Room "+roomName, 'en'))[0];
      if(roomTriple){
        return roomTriple.subject.value;
      } else {
        console.log("Warning: Did not find room URI!..");
        return u.getBlankNode("Room "+roomName);
      }
