    generateRoomList(){
      console.log("Generating room list...");
      const roomList = [];
      let selectedStoreyHeightAboveGround = undefined;
      // Get the selected storey height
      if(this.floorsselected.length>0){
        for(let i=0; i<this.floorlist.length; i++){
          if(this.floorsselected==this.floorlist[i]){
            selectedStoreyHeightAboveGround = this.floorheights[i];
          }
        }
      }
      store.state.rs.rdfStore.match(null, ns.IFOUR("roomhag"), null).forEach(obj => {
        let loc = new Vector3(0,0,0);
        if(store.state.rs.rdfStore.match($rdf.sym(obj.subject.value), ns.SIO("SIO_000507"), null)[0]){
          let locVal = store.state.rs.rdfStore.match($rdf.sym(obj.subject.value), ns.SIO("SIO_000507"), null)[0].object.value;
          loc = u.pointStringToVector3(locVal);
          //console.log(loc);
        }
        let trafficLoad = 0;
        try{
          if(store.state.rs.rdfStore.match($rdf.sym(obj.subject.value), ns.IFOUR("trafficload"), null)[0]){
            trafficLoad = Number(store.state.rs.rdfStore.match($rdf.sym(obj.subject.value), ns.IFOUR("trafficload"), null)[0].object.value);
            console.log("Trafficload = "+trafficLoad);
          } else {
            console.log("Trafficload was not found.")
          }
        } catch (e) {
          console.log(e)
        }
        let found = false;
        roomList.forEach(r => {
          if(r.uri==obj.subject.value) found = true;
        }); 
        let polyline = this.getRoomPolylinebyURI(obj.subject.value);
        let roomname = this.getRoomNamebyURI(obj.subject.value).replace(" ", "")+"";
        if(selectedStoreyHeightAboveGround){
          // Select only rooms for selected storey
          //console.log("Room height above ground: "+obj.object.value);
          if(selectedStoreyHeightAboveGround==obj.object.value&&!found) roomList.push({"uri": obj.subject.value, "name": roomname, "trafficload": trafficLoad, "trafficAreaType": "", "heightaboveground": obj.object.value, "loc": loc, "polyline": polyline, "newname": "", "globalid": this.getGlobalIDbyURI(obj.subject.value), "floorprefix": ""});
        } else {
          // Select all rooms for all storeys
          if(!found) roomList.push({"uri": obj.subject.value, "name": roomname, "trafficload": trafficLoad, "trafficAreaType": "", "heightaboveground": obj.object.value, "loc": loc, "polyline": polyline, "newname": "", "expressid": this.getExpressIDbyURI(obj.subject.value), "globalid": this.getGlobalIDbyURI(obj.subject.value), "floorprefix": ""});
        }
      });
      console.log("Roomlist length: "+roomList.length)
      console.log(roomList)
      return roomList;
    },
