            const room2 = $rdf.literal(room.name, 'en');
            //console.log("Room1: "+room1+" Room2: "+room2);
            let way = wf2.methods.wayBetweenRooms(room1, room2, 1, "Stair", this.uriBlackList, false);
            if(way.length>0){
              this.roomList.forEach((roomcheck, chid) => {
