    addLockedDoorsToStore(){
      this.uriBlackList = [];
      this.lockedDoors.forEach(name => {
        // Add locked rooms to store
        if(store.state.rs.rdfStore.match(null, ns.FOAF("name"), $rdf.literal(name))[0]){
          const roomURI = store.state.rs.rdfStore.match(null, ns.FOAF("name"), $rdf.literal(name))[0].subject.value;
          this.uriBlackList.push(roomURI);
          if(!store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.IFOUR("islocked"), null)[0]){
            addToStores($rdf.sym(roomURI), ns.IFOUR("islocked"), $rdf.literal("yes"));
          }
        }
        // Add locked doors to store
        if(store.state.rs.rdfStore.match(null, ns.IFOUR("expressid"), name)[0]){
          const roomURI = store.state.rs.rdfStore.match(null, ns.IFOUR("expressid"), name)[0].subject.value;
          this.uriBlackList.push(roomURI);
          if(!store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.IFOUR("islocked"), null)[0]){
            addToStores($rdf.sym(roomURI), ns.IFOUR("islocked"), $rdf.literal("yes"));
          }
        }
      });
    },
    async nameTechnicalRooms(floorPrefix){
      let floorExpressID = 0;
      for(let i=0;i<this.floorids.length;i++){
        if(this.floorids[i].name==this.floorsselected) floorExpressID = this.floorids[i].expressid;
      }
      let spaces = await this.getSpacesRelatedToStorey(floorExpressID, ifcModel.modelID, true);
      let roomNum = 0;
      for(let i=0; i<this.roomList.length; i++){
        if(this.roomList[i].trafficload==0&&this.roomList[i].newname.length<1&&spaces.includes(Number(this.roomList[i].expressid))){
          roomNum++;
          this.roomList[i].newname = "T"+floorPrefix+"."+(roomNum.pad(2));
          const poly = [this.polyLineTo2dArray(this.roomList[i].polyline)];
          const pos = polylabel(poly, 1); // Legge til denne til nullpunkt til polyline, ikke til senter.
          const polyPos = bmu.convertPolylineToXY(this.roomList[i].polyline);
          let position = new Vector3(polyPos.x+pos[1], this.roomList[i].loc.y, -polyPos.y+pos[0]);
          console.log("Technical: "+this.roomList[i].name);
          this.setSpaceLabel(position, this.roomList[i].newname);
        }
      }
    },
