          this.wingEntranceList.push({"name": "J214", "uri": "_:t337", "doorlocation": this.getDoorLocation("_:t337"), "wingtag": "B2"})
          this.wingEntranceList.push({"name": "G214", "uri": "_:t270", "doorlocation": this.getDoorLocation("_:t270"), "wingtag": ""})
