    newTrafficArea(spaceURI, roomNum, doorLocation, wingtag, floorPrefix, roomNumbersApproved=true){
      let labelTag = "simplelabel";
      if(!roomNumbersApproved) labelTag = "templabel";
      roomNum++;
      console.log("Running trafficarea: "+roomNum+" with URI: "+spaceURI);
      let currentTrafficLoad = 0;
      let position = new Vector3(0,0,0);
      let currentSpaceName = "";
      let currentRoomListIndex = 0;
      let currentRoomHag = 0;
      this.roomList.forEach((room, index) => {
        if(room.uri==spaceURI){
          const poly = [this.polyLineTo2dArray(room.polyline)];
          const pos = polylabel(poly, 1); // Legge til denne til nullpunkt til polyline, ikke til senter.
          const polyPos = bmu.convertPolylineToXY(room.polyline);
          position = new Vector3(polyPos.x+pos[1], room.loc.y, -polyPos.y+pos[0]);
          currentTrafficLoad = room.trafficload;
          currentSpaceName = room.name;
          currentRoomListIndex = index;
          currentRoomHag = room.heightaboveground;
          room.floorprefix = floorPrefix;
        }
      });

      let doorArray = this.getDoorsInSpace(spaceURI, doorLocation.clone());
      console.log("Door array before:")
      console.log(doorArray);

      //const polyline = "polyline;1N01;62.205509379700004;30.75;3;2.9;[0,0,0,-2.54,-3.110602869834277e-16,0,-2.5393294347499995,-2.2650000000000006,0,-2.3018294347499997,-2.2650000000000006,0,-2.3018294347499997,-2.6900000000000004,0,-2.5529999999999995,-2.6900000000000004,0,-2.5529999999999995,-3.53550937975,0,0.009117699423760433,-3.5355093797499997,0,0.00747572507190033,-2.69,0,-0.23817056524899965,-2.69,0,-0.2381705652489997,-2.2655093797499997,0,2.774136024559312e-16,-2.26525397077,0,0,0,0]";
      const polyline = this.getRoomPolylinebyURI(spaceURI)
      const doorDirectionVector = bmu.getDoorDirectionVector(polyline, doorLocation.clone(), false);

      doorArray.forEach(door => {
        const roomObject = this.getRoomBehindDoor(door.uri, spaceURI);
        if(this.checkIfVerticalPassage(spaceURI)) {
          currentTrafficLoad = 2;
          roomObject.roomtype = "Subrooms";
        }
        door.roomobject = roomObject;
        if(door.type=="Join"){
          this.spaceIntersections.forEach(intersection => {
            if(door.name.replace("Room ", "").replace("Rom ", "") == intersection.room1 && currentSpaceName == intersection.room2 || door.name.replace("Room ", "").replace("Rom ", "") == intersection.room2 && currentSpaceName == intersection.room1 ){
              door.position = new Vector3(intersection.point.x, intersection.point.y, intersection.point.z);
            }
          });
        }
      });

      doorArray.forEach(door => {
        // Must be run in own loop after Join door postitions have been changed.
        if(doorArray.length>=3){
          door.x = this.getDoorXDistance(door.position.clone(), doorLocation.clone(), doorDirectionVector.clone());
          door.y = this.getDoorYDistance(door.position.clone(), doorLocation.clone(), doorDirectionVector.clone());
        }
      });

      // Do the classifying of roomType here
      let trafficAreaType = this.getCurrentTrafficAreaType(doorArray);
      if(this.checkIfVerticalPassage(spaceURI)) trafficAreaType = "Verticalpassage";
      this.roomList[currentRoomListIndex].trafficAreaType = trafficAreaType;
      this.setSpaceLabelType(position, wingtag+(roomNum.pad(2)), trafficAreaType, labelTag);
      this.roomList[currentRoomListIndex].newname = wingtag+(roomNum.pad(2));
      this.roomURIsNumbered.push(spaceURI);

      // Sort doorarray based on y value
      console.log("TrafficAreaType: "+trafficAreaType);
      if(trafficAreaType=="Wideareasmall") doorArray = this.sortWideAreaSmall(doorArray);
      if(trafficAreaType=="Widearealarge") doorArray = this.sortWideAreaLarge(doorArray);
      if(trafficAreaType=="Hallcross") doorArray = this.sortHallCross(doorArray);
      if(trafficAreaType=="Hallaway") doorArray = this.sortHallAway(doorArray);
      
      console.log("Number of doors: "+doorArray.length);

      // Loop the joined areas and doors
      for(let d=0; d<doorArray.length; d++){
        const door = doorArray[d];
        if(door.roomobject.trafficload!=0&&!this.roomURIsNumbered.includes(door.roomobject.uri)){ // Sort out Outdoor etc.
          //if(door.pri==1){
            if(!approximatelyEqual(door.roomobject.heightaboveground, currentRoomHag, 0.5)) continue;
            position = new Vector3(0,0,0);
            if(door.roomobject.position) position = door.roomobject.position;
            const polyLine = this.polyLineTo2dArray(door.roomobject.polyline);
            if(polyLine.length>8){
              const poly = [polyLine];
              const pos = polylabel(poly, 1); // Legge til denne til nullpunkt til polyline, ikke til senter.
              const polyPos = bmu.convertPolylineToXY(door.roomobject.polyline);
              position = new Vector3(polyPos.x+pos[1], door.roomobject.position.y, -polyPos.y+pos[0]);
            }
            if(door.roomobject.roomtype=="Single"){
              roomNum++;
              if(this.checkIfVerticalPassage(door.roomobject.uri)){
                this.setSpaceLabelType(position, wingtag+(roomNum.pad(2)), "Verticalpassage", labelTag);
              } else {
                this.setSpaceLabel(position, wingtag+(roomNum.pad(2)), labelTag);
              }
              this.roomList[currentRoomListIndex].newname = wingtag+(roomNum.pad(2));
              this.roomURIsNumbered.push(door.roomobject.uri);
            }
            if(door.roomobject.roomtype=="Subrooms"){
              roomNum++;
              if(this.checkIfVerticalPassage(door.roomobject.uri)){
                this.setSpaceLabelType(position, wingtag+(roomNum.pad(2)), "Verticalpassage", labelTag);
              } else {
                this.setSpaceLabel(position, wingtag+(roomNum.pad(2)), labelTag);
              }
              this.roomList[currentRoomListIndex].newname = wingtag+(roomNum.pad(2));
              this.roomURIsNumbered.push(door.roomobject.uri);
              this.numberSubRooms(door.roomobject.uri, wingtag, roomNum);
            }
          //}
        }
      }

      // Loop the Traffic Areas
      doorArray.sort((p1, p2) => (p1.roomobject.trafficload < p2.roomobject.trafficload) ? 1 : (p1.roomobject.trafficload > p2.roomobject.trafficload) ? -1 : 0);
      for(let d=0; d<doorArray.length; d++){
        const door = doorArray[d];
        if(this.lockedDoors.includes(door.roomobject.name.replace("Rom ", "").replace("Room ", ""))&&door.type=="Join") continue;
        if(this.lockedDoors.includes(door.uri)) continue;
        if(!approximatelyEqual(door.roomobject.heightaboveground, currentRoomHag, 0.5)) continue;
        if(door.roomobject.trafficload!=0&&!this.roomURIsNumbered.includes(door.roomobject.uri)){ // Sort out Outdoor etc.
          let foundNewWing = false;
          this.wingEntranceNames.forEach(name => { 
            if(name==door.roomobject.name.replace("Room ", "")) foundNewWing = true; 
            if(name==door.roomobject.name.replace("Rom ", "")) foundNewWing = true; 
          });
          if(door.roomobject.roomtype=="Trafficarea"&&!foundNewWing&&door.roomobject.trafficload<currentTrafficLoad){
            this.deep++;
            if(this.deep<25){
              console.log("Running new trafficarea: "+door.roomobject.name);
              console.log(door.position);
              bmu.createPlaceMarker(door.position.clone(), 0x00FF00, false, 2, "placemarker", 0.25);
              if(door.roomobject.uri=="_:t1105"){
                this.nextTrafficArea = { "spaceURI": door.roomobject.uri, "roomNum": roomNum, "doorLocation": door.position.clone(), "wingtag": wingtag, "floorPrefix": floorPrefix, "roomNumbersApproved": true };
              } else {
                roomNum = this.newTrafficArea(door.roomobject.uri, roomNum, door.position.clone(), wingtag, floorPrefix, true);
              }
            }
          }
        }
      }
      return roomNum;
    },
