    checkIfVerticalPassage(spaceURI){
      if(store.state.rs.rdfStore.match($rdf.sym(spaceURI), ns.NEO("b_capable"), ns.SBEO("VerticalPassage"))[0]){
        return true;
      } else {
        return false;
      }
    },
    sortWideAreaSmall(doorArray){
      return doorArray.sort(function (a, b) {
        // First compare y direction with deviation of 0.5 meters
        if (Math.abs(b.y-a.y)>0.3) {
          return a.y - b.y;
        } else {
          return a.x - b.x;
        }
      });
    },
