      // Start doing first two lines
      // Finished doing first two lines

      // Check if duplicates found with only two doors - then remove
      //removeTwoDoorDuplicates(xDuplicates, doorArray, false);

      // Test if there is most x duplicates or y duplicates
      let mostX = false;
      const xDoorCount = this.countDuplicates(xDuplicates, doorArray, true); 
      const yDoorCount = this.countDuplicates(yDuplicates, doorArray, false); 
