          });
        }
        while(yDuplicates.length>0){
          returnValues = this.moveYLineValues(yDuplicates, doorArray, maxX, minX, debugF);
          yDuplicates = returnValues[0];
          doorArray = returnValues[1];
          splittedArray = this.checkForGapInDistances(returnValues[2]);
          splittedArray.forEach(arr => {
            sortedDoorArray.push(arr);
          });
        }
      } else {
        while(yDuplicates.length>0){
          returnValues = this.moveYLineValues(yDuplicates, doorArray, maxX, minX, debugF);
          yDuplicates = returnValues[0];
          doorArray = returnValues[1];
          splittedArray = this.checkForGapInDistances(returnValues[2]);
          splittedArray.forEach(arr => {
            sortedDoorArray.push(arr);
          });
        }
        while(xDuplicates.length>0){
