      if(debugF) console.log("Sorted door array after :"+sortedDoorArray.length);
      for(var i = 0; i < doorArray.length; i++) {
        const doorObject = doorArray[i];
        sortedDoorArray.push({"x": doorArray[i].x, "y": doorArray[i].y, "doorarray": [doorObject]});
        doorArray.splice(i, 1);
        i--;
      }
      sortedDoorArray = this.sortHallAway(sortedDoorArray);
      console.log("sortedDoorArray: "+sortedDoorArray.length);
      console.log(sortedDoorArray);
      
      const returnArray = [];
      sortedDoorArray.forEach(object => {
