      console.log("sortedDoorArray: ")
      console.log(returnArray)
      return returnArray;
    },
    checkForGapInDistances(listObject){
      // Checks for large distances in door lines and splits them into two arrays
      console.log("Doorarray")
      console.log(listObject.doorarray)
      let temparray = [];
      let temparray2 = [];
      let foundSplit = false;
      let previousX = undefined;
      let previousY = undefined;
      for(let i=0; i<listObject.doorarray.length; i++){
        if(i!=0){
          if(previousX) if((listObject.doorarray[i].x-previousX)>=8) foundSplit = true;
          if(previousY) if((listObject.doorarray[i].y-previousY)>=8) foundSplit = true;
          if(foundSplit){
            temparray2.push(listObject.doorarray[i]);
          } else {
            temparray.push(listObject.doorarray[i]);
          }
        } else {
          temparray.push(listObject.doorarray[i]);
        }
        previousX = listObject.doorarray[i].x;
        previousY = listObject.doorarray[i].y;
      }
      let returnArray = [];
      if(temparray[0]) returnArray.push({"x": temparray[0].x, "y": temparray[0].y, "doorarray": temparray});
      if(temparray2[0]) returnArray.push({"x": temparray2[0].x, "y": temparray2[0].y, "doorarray": temparray2});
      return returnArray;
    },
    countDuplicates(dupArray, doorArray, isX){
      const duplicateArray = [];
      dupArray.forEach(dup => {
        let dupExist = false;
        duplicateArray.forEach(checkExist => {
          if(approximatelyEqual(dup, checkExist, 0.6)) dupExist = true;
        });
        if(!dupExist) duplicateArray.push(dup);
      });
      let count = 0;
      const arrayOfCounts = [];
      for(var y = 0; y < duplicateArray.length; y++) {
        count = 0;
        for(var i = 0; i < doorArray.length; i++) {
          if(approximatelyEqual(doorArray[i].x, duplicateArray[y], 0.6)&&isX) count++;
          if(approximatelyEqual(doorArray[i].y, duplicateArray[y], 0.6)&&!isX) count++;
        }
        arrayOfCounts.push(count);
      }
      count = 0;
      //arrayOfCounts.sort((p1, p2) => (p1 < p2) ? 1 : (p1 > p2) ? -1 : 0);
      //count = arrayOfCounts[0]+arrayOfCounts[1]; // Takes only the two largest lines into consideration
      console.log("arrayofcounts")
      console.log(arrayOfCounts)
      arrayOfCounts.forEach(num => {
        if(num>4){
          count = count+2; // Bonus for having large lines of doors
        } else if(num>2) count++; // Counts only lines larger than 2 doors
      });
      return count;
    },
    removeTwoDoorDuplicates(yDuplicates, doorArray, debugF){
