      return [yDuplicates, doorArray, {"x": firstDoorPoint.x, "y": firstDoorPoint.y, "doorarray": tempDoorArray}];*/
    },
    moveYLineValues(yDuplicates, doorArray, maxX, minX, debugF){
      const minYLineVal = Math.min.apply( Math, yDuplicates );
      let tempDoorArray = [];
      for(var y = 0; y < yDuplicates.length; y++) {
        let offset = 2.5;
        if(yDuplicates[y]<=(minYLineVal+offset)){ // Check if two lines found near eachother
          for(var i = 0; i < doorArray.length; i++) {
            if(doorArray[i].y==yDuplicates[y]){
              // Put doors found on line into new array if not near to left of right side.
              if(Math.abs(doorArray[i].x-maxX)>0.3&&Math.abs(doorArray[i].x-minX)>0.3){
                tempDoorArray.push(doorArray[i]);
                if(debugF) console.log("Adding to Y line: "+doorArray[i].name);
                doorArray.splice(i, 1);
                i--;
              } 
            }
          }
          yDuplicates.splice(y, 1);
          y--;
        }
      }
      tempDoorArray = this.sortHallCross(tempDoorArray);
      let firstDoorPoint = {"x": 0, "y": 0};
      if(tempDoorArray[0]) firstDoorPoint = {"x": tempDoorArray[0].x, "y": tempDoorArray[0].y};
      if(debugF) console.log("First door point x: "+firstDoorPoint.x+" y: "+firstDoorPoint.y);
      if(debugF) console.log("Moving to new line...");
      return [yDuplicates, doorArray, {"x": firstDoorPoint.x, "y": firstDoorPoint.y, "doorarray": tempDoorArray}];
    },
    moveXLineValues(xDuplicates, doorArray, maxX, minX, debugF){
      const minXLineVal = Math.min.apply( Math, xDuplicates );
      let tempDoorArray = [];
      for(var x = 0; x < xDuplicates.length; x++) {
        let offset = 2.5;
        if(xDuplicates[x]<=(minXLineVal+offset)){ // Check if two lines found near eachother
          for(var i = 0; i < doorArray.length; i++) {
            if(doorArray[i].x==xDuplicates[x]){
              // Put doors found on line into new array
              tempDoorArray.push(doorArray[i]);
              if(debugF) console.log("Adding to X line: "+doorArray[i].name);
              doorArray.splice(i, 1);
              i--;
            }
          }
          xDuplicates.splice(x, 1);
          x--;
        }
      }
      tempDoorArray = this.sortHallAway(tempDoorArray);

      let firstDoorPoint = {"x": 0, "y": 0};
      if(tempDoorArray[0]) firstDoorPoint = {"x": tempDoorArray[0].x, "y": tempDoorArray[0].y};
      if(debugF) console.log("Moving to new line...");
      return [xDuplicates, doorArray, {"x": firstDoorPoint.x, "y": firstDoorPoint.y, "doorarray": tempDoorArray}];
    },
    sortHallCross(doorArray){
      return doorArray.sort(function (a, b) {
        // First compare x direction with deviation of 0.5 meters
        if (Math.abs(b.x-a.x)>0.3) {
          return a.x - b.x;
        } else {
          return a.y - b.y;
        }
      });
    },
    sortHallAway(doorArray){
      return doorArray.sort(function (a, b) {
        // First compare y direction with deviation of 0.5 meters
        if (Math.abs(b.y-a.y)>0.3) {
          return a.y - b.y;
        } else {
          return a.x - b.x;
        }
      });
    },
    getDoorsInSpace(spaceURI, debugF=false){
      const doorURIArray = [];
      const doorTagArray = [];
      const newDoorArray = [];
      store.state.rs.rdfStore.match($rdf.sym(spaceURI), ns.NEO("m_port"), null).forEach(door => {
        if(!doorURIArray.includes(door.object.value)){ 
          doorURIArray.push(door.object.value);
          doorTagArray.push(2);
        }
      });
      store.state.rs.rdfStore.match(null, ns.NEO("m_port"), $rdf.sym(spaceURI)).forEach(door => {
        if(!doorURIArray.includes(door.subject.value)){ 
          doorURIArray.push(door.subject.value);
          doorTagArray.push(2);
        }
      });
      store.state.rs.rdfStore.match($rdf.sym(spaceURI), ns.NEO("m_join"), null).forEach(door => {
        if(!doorURIArray.includes(door.object.value)){ 
          doorURIArray.push(door.object.value);
          doorTagArray.push(1);
        }
      });
      store.state.rs.rdfStore.match(null, ns.NEO("m_join"), $rdf.sym(spaceURI)).forEach(door => {
        if(!doorURIArray.includes(door.subject.value)){ 
          doorURIArray.push(door.subject.value);
          doorTagArray.push(1);
        }
      });
      doorURIArray.forEach((doorURI, cn) => {
        const doorName = store.state.rs.rdfStore.match($rdf.sym(doorURI), ns.RDFS("label"), null)[0].object.value;
        let nextRoomHAG = 0;
        if(store.state.rs.rdfStore.match($rdf.sym(doorURI), ns.IFOUR("roomhag"), null)[0]) nextRoomHAG = store.state.rs.rdfStore.match($rdf.sym(doorURI), ns.IFOUR("roomhag"), null)[0].object.value;
        if(debugF) console.log("DoorName: "+doorName);
        if(debugF) console.log("DoorURI: "+doorURI);
        let interfaceType = "Door";
        if(doorTagArray[cn]==1) interfaceType = "Join";
        let doorPos = new Vector3(0,0,0);
        if(interfaceType!= "Join"){
          doorPos = this.getDoorPosition(doorURI);
        } else {
          doorPos = this.getJoinPosition(doorURI);
        }
        newDoorArray.push({"uri": doorURI, "name": doorName, "position": doorPos, "x": 0, "y": 0, "pri": doorTagArray[cn], "nextroomhag": nextRoomHAG, "type": interfaceType, "roomobject": {}});
      });
      return newDoorArray;
    },
    getRoomBehindDoor(doorURI, currentSpaceURI){
      let roomURI = doorURI; // Default sets to enable shared space
      let roomType = "";
      let polyline = "";
      let trafficAreaType = "";
      let roomhag = 0;
      let trafficLoad = 0;
      let roomName = store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.RDFS("label"), null)[0].object.value;
      let isRoom = false;
      if(store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.RDF("type"), ns.BOT("Space"))[0]) isRoom = true;
      if(!isRoom){ // May find other way to compare
        store.state.rs.rdfStore.match($rdf.sym(doorURI), ns.NEO("m_port"), null).forEach(door => {
          if(door.object.value!=currentSpaceURI) roomURI = door.object.value;
        });
        store.state.rs.rdfStore.match(null, ns.NEO("m_port"), $rdf.sym(doorURI)).forEach(door => {
          if(door.subject.value!=currentSpaceURI) roomURI = door.subject.value;
        });
      }
      if(store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.RDFS("label"), null)[0]) roomName = store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.RDFS("label"), null)[0].object.value; // Overwrite because roomURI may have been changed
      let roomPosition = new Vector3(0,0,0);
      /*if(store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.SIO("SIO_000507"), null)[0]){
        roomPosition = store.state.rs.rdfStore.match($rdf.sym(roomURI), ns.SIO("SIO_000507"), null)[0].object.value;
      }*/
      this.roomList.forEach((room, id) => {
        if(room.uri==roomURI){
          //console.log("Polyline: "+room.polyline);
          trafficLoad = room.trafficload;
          roomPosition = new Vector3(room.loc.x, room.loc.y, room.loc.z);
          polyline = room.polyline;
          roomhag = room.heightaboveground;
          trafficAreaType = room.trafficAreaType;
        }
      });
      if(trafficLoad<2) roomType = "Single";
      if(trafficLoad<4&&trafficLoad>1) roomType = "Subrooms";
      if(trafficLoad>=4) roomType = "Trafficarea";
      return {"uri": roomURI, "name": roomName.replace("  ", " "), "position": roomPosition, "polyline": polyline, "trafficload": trafficLoad, "roomtype": roomType, "heightaboveground": roomhag};
    },
