    polyLineTo2dArray(polyline){
      const polyArray = bmu.convertPolylineToPolyarray(polyline);
      const polyArr = [];
      while (polyArray.length > 1) {
        const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
        polyArr.push([Number(polyXYZ[0]), Number(polyXYZ[1])]);
      }
      return polyArr;
    },
    polyLineXY(polyline){
      const polyArray = bmu.convertPolylineToPolyarray(polyline);
      const polyArr = [];
      while (polyArray.length > 1) {
        const polyXYZ = polyArray.splice(0, 3); // Puts 3 first object from array in polyXYZ, and removes from polyArray
        polyArr.push([Number(polyXYZ[0]), Number(polyXYZ[1])]);
      }
      return polyArr;
    },
    numberSubRooms(spaceURI, wingtag, roomNum, subRoomCounter=0, labelTag){
      const doorArray = this.getDoorsInSpace(spaceURI);
      const polyLines = [];
      let currentTrafficLoad = 0;
      let currentRoomListIndex = 0;
      this.roomList.forEach((room, index) => {
        if(room.uri==spaceURI){
          currentTrafficLoad = room.trafficload;
          currentRoomListIndex = index;
        }
      });
      doorArray.forEach(door => {
        const roomObject = this.getRoomBehindDoor(door.uri, spaceURI);
        door.roomobject = roomObject;
        if(door.type=="Join"){
          polyLines.push(door.roomobject.polyline);
          let intersections = bmu.findSpaceIntersections(polyLines, 0.5, false);
          intersections.forEach(intersection => {
            if(door.name.includes(intersection.room1)||door.name.includes(intersection.room2)){
              door.position = new Vector3(intersection.point.x, intersection.point.y, intersection.point.z);
            }
          });
        }
      });
      doorArray.forEach(door => {
        if(door.roomobject.trafficload!=0&&!this.roomURIsNumbered.includes(door.roomobject.uri)&&door.roomobject.trafficload<currentTrafficLoad){ // Sort out Outdoor etc.
          let position = new Vector3(0,0,0);
          if(door.roomobject.position) position = door.roomobject.position;
          let trafficLoadBehind = this.getTrafficLoadInSpaceBehind(spaceURI, door.roomobject.uri);
          if(trafficLoadBehind<=currentTrafficLoad||door.type=="Join"){
            subRoomCounter++;
            this.setSpaceLabel(position, wingtag+(roomNum.pad(2))+"."+subRoomCounter, labelTag);
            this.roomList[currentRoomListIndex].newname = wingtag+(roomNum.pad(2))+"."+subRoomCounter;
            this.roomURIsNumbered.push(door.roomobject.uri);
            const doorArray2 = this.getDoorsInSpace(door.roomobject.uri);
            doorArray2.forEach(door2 => {
              const roomObject = this.getRoomBehindDoor(door.uri, spaceURI);
              door2.roomobject = roomObject;
              this.numberSubRooms(door2.roomobject.uri, wingtag, roomNum, subRoomCounter);
            });
          }
        }
      });
    },
    getTrafficLoadInSpaceBehind(prevRoomURI, currRomURI){
      const doorArray = this.getDoorsInSpace(currRomURI);
      let trafficload = 0;
      doorArray.forEach(door2 => {
        const roomObject = this.getRoomBehindDoor(door2.uri, currRomURI);
        if(roomObject.uri!=prevRoomURI&&roomObject.uri!=currRomURI){
          if(roomObject.trafficload>trafficload) trafficload = roomObject.trafficload;
        }
      });
      return trafficload;
    },
    getDoorXDistance(point, observerPos, observerDir, debugF=false){
      const dist = observerPos.distanceTo(point);
      observerDir = observerPos.clone().add(observerDir.normalize().multiplyScalar(dist));
      const yLine = new Line3(observerPos, observerDir);
      if(debugF) bmu.createVisibleVector(observerPos, observerDir, 0x0000FF, dist);
      const closestPointOnLine = new Vector3(0,0,0);
      const posVector = new Vector3(point.x, point.y, point.z);
      yLine.closestPointToPoint(posVector, false, closestPointOnLine);
      let distance = closestPointOnLine.distanceTo(posVector);
      // Start finding positive or negative angle:
      var dirForward = new Vector3().subVectors(observerDir, observerPos).normalize();
      var dirToPoint = new Vector3().subVectors(point, observerPos).normalize();
      var cross = new Vector3().crossVectors(dirForward, dirToPoint);
      const dir = cross.dot(new Vector3(0,1,0));
      if(dir > 0.0) distance = distance*-1.0;
      if(isNaN(distance)) distance = 0;
      return distance;
    },
    getDoorYDistance(point, observerPos, observerDir, debugF=false){
      const dist = observerPos.clone().distanceTo(point);
      var q1 = new Quaternion();
      q1.setFromAxisAngle(new Vector3(0, 1, 0), Math.PI/2);
      observerDir.applyQuaternion(q1);
      if(debugF) bmu.createVisibleVector(observerPos, observerDir, 0x00FF00, dist);
      const p1 = observerPos.clone().add(observerDir.clone().multiplyScalar(dist));
      const p2 = observerPos.clone().add(observerDir.clone().negate().multiplyScalar(dist));
      if(debugF) bmu.createVisibleVector(observerPos, observerDir.clone().negate(), 0x00FF00, dist);
      const yLine = new Line3(p1, p2);
      const closestPointOnLine = new Vector3(0,0,0);
      const posVector = new Vector3(point.x, point.y, point.z);
      yLine.closestPointToPoint(posVector, false, closestPointOnLine);
      let distance = closestPointOnLine.distanceTo(posVector);
      // Start finding positive or negative angle:
      var dirForward = new Vector3().subVectors(p1, observerPos).normalize();
      var dirToPoint = new Vector3().subVectors(point, observerPos).normalize();
      var cross = new Vector3().crossVectors(dirForward, dirToPoint);
      const dir = cross.dot(new Vector3(0,1,0));
      if(dir > 0.0) distance = distance*-1.0;
      if(isNaN(distance)) distance = 0;
      return distance;
    },
