    getCurrentTrafficAreaType(doorArray){
      let trafficAreaType = "Wideareasmall";
      const doorXValues = [];
      const doorYValues = [];
      doorArray.forEach(door => {
        //console.log(door.position);
        doorXValues.push(door.x);
        doorYValues.push(door.y);
      });
      const maxX = Math.max.apply( Math, doorXValues );
      const minX = Math.min.apply( Math, doorXValues );
      const maxY = Math.max.apply( Math, doorYValues );
      const minY = Math.min.apply( Math, doorYValues );
      const lengthX = maxX-minX;
      const lengthY = maxY-minY;
      console.log("Length X: "+lengthX+" length Y: "+lengthY)
      const xCount = findDuplicates(doorXValues, 0.5);
      const yCount = findDuplicates(doorYValues, 0.5);
      if(xCount.length>2) trafficAreaType = "Hallaway";
      if(yCount.length>2&&yCount.length>xCount.length) trafficAreaType = "Hallcross";
      if((lengthX>=6&&lengthY>=10)||(lengthX>=10&&lengthY>=6)) trafficAreaType = "Widearealarge";
      return trafficAreaType;
    },
    increaseTrafficLoadForSpace(room1, room2, avvoid, increasevalue=1){
      const roomlit1 = $rdf.literal(room1, 'en');
      const roomlit2 = $rdf.literal(room2, 'en');
      let way = wf2.methods.wayBetweenRooms(roomlit1, roomlit2, 1, "Stair", this.uriBlackList, false);
      if(way.length>0){
        this.roomList.forEach((roomcheck, chid) => {
          if(way.includes(roomcheck.uri)&&this.roomList[chid].name!=avvoid){
            this.roomList[chid].trafficload = this.roomList[chid].trafficload+increasevalue;
            console.log("Increased traffic load for space: "+this.roomList[chid].name+" to value: "+this.roomList[chid].trafficload)
          } 
        });
      }
    },
