    async getPolylineForSpace(modelID, spaceID, spaceName, spaceHeight){
      let polyline = "";
      const props = await ifcLoader.ifcManager.getItemProperties(modelID, spaceID);
      const ObjectPlacement = await ifcLoader.ifcManager.getItemProperties(modelID, props.ObjectPlacement.value);
      const PlacementRelTo = await ifcLoader.ifcManager.getItemProperties(modelID, ObjectPlacement.PlacementRelTo.value);
      const RelativePlacement = await ifcLoader.ifcManager.getItemProperties(modelID, ObjectPlacement.RelativePlacement.value);
      const Location = await ifcLoader.ifcManager.getItemProperties(modelID, RelativePlacement.Location.value);
      const Axis = await ifcLoader.ifcManager.getItemProperties(modelID, RelativePlacement.Axis.value);
      const RefDirection = await ifcLoader.ifcManager.getItemProperties(modelID, RelativePlacement.RefDirection.value);
      const DirectionRatios = RefDirection.DirectionRatios;
      const AxisDirectionRatios = Axis.DirectionRatios; // Mostly { 0, 0, 1 }
      const coordinates = Location.Coordinates;

      const Representation = await ifcLoader.ifcManager.getItemProperties(modelID, props.Representation.value);
      const Representation1 = await ifcLoader.ifcManager.getItemProperties(modelID, Representation.Representations[1].value);
      const FootPrint = await ifcLoader.ifcManager.getItemProperties(modelID, Representation1.Items[0].value);
      const IfcPolyline = await ifcLoader.ifcManager.getItemProperties(modelID, FootPrint.Elements[0].value);

      if(IfcPolyline.Segments!=undefined){
        const Segments = IfcPolyline.Segments;
        for(let s=0; s<Segments.length; s++){
          const segment = await ifcLoader.ifcManager.getItemProperties(modelID, Segments[s].value);
          const parentcurve = await ifcLoader.ifcManager.getItemProperties(modelID, segment.ParentCurve.value);
          if(parentcurve.Points!=undefined){
            // Partentcurve include a set of points
            if(IfcPolyline.Points===undefined) IfcPolyline.Points = [];
            parentcurve.Points.forEach(point => {
              IfcPolyline.Points.push(point);
            });
          } else {
            const BasisCurve = await ifcLoader.ifcManager.getItemProperties(modelID, parentcurve.BasisCurve.value);
            //console.log(BasisCurve);
          }
        }
      }

      // Functionality to find rotation START ---------------------
      let inRotation = { x:DirectionRatios[0].value, y:DirectionRatios[1].value, z:DirectionRatios[2].value };
      if(!inRotation.x) inRotation = { x:DirectionRatios[0], y:DirectionRatios[1], z:DirectionRatios[2] };
      let inAxis = { x: AxisDirectionRatios[0].value, y:AxisDirectionRatios[1].value, z:AxisDirectionRatios[2].value };
      if(!inAxis.x) inAxis = { x: AxisDirectionRatios[0], y:AxisDirectionRatios[1], z:AxisDirectionRatios[2] };
      const crossProduct = v.v3cross(inRotation, inAxis);
      const myAxis = { x:0, y:-1, z:0 };

      var angle = v.getAngleRadians(crossProduct.x, crossProduct.y, inRotation.x, inRotation.y);
      var angle2 = v.getAngleRadians(inAxis.x, inAxis.y, myAxis.x, myAxis.y);
      angle = (angle2-angle)+(Math.PI/4);
      // Functionality to find rotation END ---------------------

      const polyCoordinates = [];
      if(IfcPolyline.Points){
        for (var i = 0; i < IfcPolyline.Points.length; i++) {
          const IfcCartesianPoint = await ifcLoader.ifcManager.getItemProperties(modelID, IfcPolyline.Points[i].value);
          polyCoordinates.push(IfcCartesianPoint.Coordinates);
        }

        // Create string of polyline
        var polyString = "polyline;"+encodeURI(spaceName)+";"+coordinates[0].value/1000+";"+coordinates[1].value/1000+";"+spaceHeight+";2.9;["; // Includes x, y, z and height of room. Must set z and height dynamic

        for (var i = 0; i < polyCoordinates.length; i++) {
          var point1 = (polyCoordinates[i][0].value/1000);
          var point2 = (polyCoordinates[i][1].value/1000);

          const rotatedPoints = v.rotateRadians(0, 0, point1, point2, angle);
          point1 = rotatedPoints[0];
          point2 = rotatedPoints[1];

          polyString = ""+polyString+point1+","+point2+",0"; // Switches points because of current version, may need to change
          if(i<polyCoordinates.length-1){
            polyString = polyString+",";
          }
        }
        polyString = polyString+"]";
        //console.log(polyString);
        return polyString;
      } else {
        return "";
      }
    },
    async saveDoorToTrinpod(modelID, room1, room2, room1uri, room2uri, heightAboveGround, doorname, vx, vy, vz, locationTAG, sstoreyTAG, isLocked, expressID, globalID){
      //console.log("Room URI: "+room1uri);
      //console.log("Room2 URI: "+room2uri);
      if(room1==undefined) room1 = "Outdoor";
      if(room2==undefined) room2 = "Outdoor";

      room1 = u.formatString(room1);
      room2 = u.formatString(room2);
      
      let doorBlank = u.getBlankNode("Door "+room1+"-"+room2)
      let doorTAG = u.prettyTAG(locationTAG+sstoreyTAG+".DR."+room1+room2);
      let doorTAG2 = u.prettyTAG(locationTAG+sstoreyTAG+".DR."+room2+room1);

      // Check door exist - If so abort
      if(this.roomsExistInTrinpod){
        let roomSearch = await r.searchAndGetURIs("#"+doorTAG+"*"); 
        let roomSearch2 = await r.searchAndGetURIs("#"+doorTAG2+"*"); 
        if(roomSearch!=""||roomSearch2!=""){
          console.log("Warning - Found duplicate door!!!!!!!!");
          return
        }
      }
   
      // Add triples related to door
      addToStores(doorBlank, ns.RDF("type"), ns.NEO("x_door"));
      addToStores(doorBlank, ns.TAG("tag"), doorTAG);
      addToStores(doorBlank, ns.RDFS("label"), $rdf.literal("Door "+room1+"_"+room2, 'en'));
      addToStores(doorBlank, ns.RDFS("label"), $rdf.literal("Dr "+room1+"_"+room2, 'no'));
      addToStores(doorBlank, ns.FOAF("name"), $rdf.literal(doorname));
      addToStores(doorBlank, ns.SIO("SIO_000507"), $rdf.literal("("+vx+") ("+vy+") ("+vz+")")); // SIO_000507 is same as r.point
      addToStores(doorBlank, ns.IFOUR("expressid"), expressID);
      addToStores(doorBlank, ns.IFOUR("globalid"), globalID);
      if(isLocked) addToStores(doorBlank, ns.IFOUR("islocked"), $rdf.literal("yes"));
      if(room1uri) addToStores($rdf.sym(room1uri), ns.NEO("m_port"), $rdf.sym(doorBlank));
      if(room2uri) addToStores($rdf.sym(room2uri), ns.NEO("m_port"), $rdf.sym(doorBlank));

      //ur.checkIfContainLoop(doorBlank.value, containsPortTypes);

      // Add triples related to room - if room is blank node:
      const room1exists = store.state.rs.rdfStore.match($rdf.sym(room1uri), ns.RDF("type"), ns.BOT("Space"))[0];
      if(!room1exists && room1uri.value.includes("_:")){
        addToStores(room1uri, ns.RDF("type"), ns.BOT("Space"));
        addToStores(room1uri, ns.RDFS("label"), $rdf.literal("Room "+room1, 'en'));
        addToStores(room1uri, ns.RDFS("label"), $rdf.literal("Rom "+room1, 'no'));
      }
      const room2exists = store.state.rs.rdfStore.match($rdf.sym(room2uri), ns.RDF("type"), ns.BOT("Space"))[0];
      if(!room2exists && room2uri.value.includes("_:")){
        addToStores(room2uri, ns.RDF("type"), ns.BOT("Space"));
        addToStores(room2uri, ns.RDFS("label"), $rdf.literal("Room "+room2, 'en'));
        addToStores(room2uri, ns.RDFS("label"), $rdf.literal("Rom "+room2, 'no'));
      }
      let doorloc = new Vector3(vx, vy+0.2, vz);
      if(isLocked){
        bmu.createPlaceMarker(doorloc, 0xFFFF00, false, 2, "placemarker", 0.16);
      } else {
        bmu.createPlaceMarker(doorloc, 0xFF0000, false, 2, "placemarker", 0.16);
      }
      
      //ur.checkIfContainLoop(room1uri, containsTypes);
    },
