    async generateOpeningCirculation(debugF=false){
      console.log("Started finding space intersections.");
      this.loading = true;
      const modelID = ifcModel.modelID;
      const polyLines = [];
      const storeys = await ifcLoader.ifcManager.getAllItemsOfType(modelID, IFCBUILDINGSTOREY);
      for(let s=0; s<this.floorlist.length; s++){
        if(this.floorsselected==this.floorlist[s]||this.floorsselected.length==0){ // Enabling only running for current storey if selected, or all storeys
          const props = await ifcLoader.ifcManager.getItemProperties(modelID, storeys[s]);
          const selectdStoreyHeightAboveGround = props.Elevation.value/1000;
          store.state.rs.rdfStore.match(null, ns.IFOUR("m_polyline"), null).forEach(poly => {
            const splitted = poly.object.value.split(";");
            const storeyHeight = parseFloat(splitted[4]);
            const spaceHeight = parseFloat(splitted[5]);
            if(bmu.checkIfMeshCrossStoreyLevel(selectdStoreyHeightAboveGround, storeyHeight, storeyHeight+spaceHeight)){
              polyLines.push(poly.object.value);
            }
          });
        }
      }
      if(debugF) console.log("Selected polylines:");
      if(debugF) console.log(polyLines);
      const foundIntersections = bmu.findSpaceIntersections(polyLines, 0.5, true);
      foundIntersections.forEach(intersect => {
        this.spaceIntersections.push(intersect);
      });
      this.storeSpaceIntersections(foundIntersections, false);
      await this.getIFCOpeningElements();
      this.loading = false;
      this.roomNumberProcess = 5;
      console.log("Finished finding space intersections.");
    },
    storeSpaceIntersections(foundIntersections, debugF=false){
      foundIntersections.forEach(intersect => {
        let room1uri = undefined;
        let room2uri = undefined;
        try{
          room1uri = store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal("Room "+intersect.room1, 'en'))[0].subject.value;
          room2uri = store.state.rs.rdfStore.match(null, ns.RDFS("label"), $rdf.literal("Room "+intersect.room2, 'en'))[0].subject.value;
        } catch (error){}
        if(room1uri && room2uri){
          // Get or create parent space - Check for parent first
          let parentSpaceURI = "";
          if(store.state.rs.rdfStore.match($rdf.sym(room1uri), ns.DUL("hasPart"), null)[0]){
            parentSpaceURI = store.state.rs.rdfStore.match($rdf.sym(room1uri), ns.DUL("hasPart"), null)[0].object.value;
          }
          if(store.state.rs.rdfStore.match($rdf.sym(room2uri), ns.DUL("hasPart"), null)[0]){
            parentSpaceURI = store.state.rs.rdfStore.match($rdf.sym(room2uri), ns.DUL("hasPart"), null)[0].object.value;
          }
          if(parentSpaceURI=="") parentSpaceURI = u.getBlankNode("Parentspace "+intersect.room1+"-"+intersect.room2)
          addToStores($rdf.sym(parentSpaceURI), ns.DUL("hasPart"), $rdf.sym(room1uri));
          addToStores($rdf.sym(parentSpaceURI), ns.DUL("hasPart"), $rdf.sym(room2uri));
          addToStores($rdf.sym(room1uri), ns.NEO("m_join"), $rdf.sym(room2uri));
          if(this.lockedDoors.includes(intersect.room1)){
            addToStores($rdf.sym(room1uri), ns.IFOUR("islocked"), $rdf.literal("yes"));
            bmu.createPlaceMarker(intersect.point, 0xFFFF00, false, 1);
          } else if(this.lockedDoors.includes(intersect.room2)){
            addToStores($rdf.sym(room2uri), ns.IFOUR("islocked"), $rdf.literal("yes"));
            bmu.createPlaceMarker(intersect.point, 0xFFFF00, false, 1);
          } else {
            bmu.createPlaceMarker(intersect.point, 0x000099, false, 1);
          }
          const interfaceBlank = u.getBlankNode("Interface_"+intersect.room1+"-"+intersect.room2)
          addToStores(interfaceBlank, ns.RDF("type"), ns.IFOUR("InterfacePoint"));
          addToStores(interfaceBlank, ns.SIO("SIO_000507"), $rdf.literal("("+intersect.point.x+") ("+intersect.point.y+") ("+intersect.point.z+")"));
          addToStores($rdf.sym(room1uri), ns.IFOUR("hasInterfacePoint"), interfaceBlank);
          addToStores($rdf.sym(room2uri), ns.IFOUR("hasInterfacePoint"), interfaceBlank);

          if(debugF) console.log("Saving room1: "+intersect.room1+" and room2: "+intersect.room2);
          /*let intersectBlank = u.getBlankNode("Opening "+intersect.room1+"-"+intersect.room2)
          addToStores(intersectBlank, ns.RDF("type"), ns.NEO("m_interface-point"));
          addToStores(intersectBlank, ns.RDFS("label"), $rdf.literal("Opening "+" "+intersect.room1+"-"+intersect.room2, 'en'));
          addToStores(intersectBlank, ns.RDFS("label"), $rdf.literal("pning "+" "+intersect.room1+"-"+intersect.room2, 'no'));
          addToStores(intersectBlank, ns.SIO("SIO_000507"), $rdf.literal("("+intersect.point.x+") ("+intersect.point.y+") ("+intersect.point.z+")")); // SIO_000507 is same as r.point
          if(room1uri) addToStores($rdf.sym(room1uri), ns.NEO("m_port"), intersectBlank);
          if(room2uri) addToStores($rdf.sym(room2uri), ns.NEO("m_port"), intersectBlank);*/
          if(debugF) this.setSpaceLabel(intersect.point, intersect.room1+"-"+intersect.room2);
        } else {
          if(debugF) console.log("Did not find room1: "+intersect.room1+" and room2: "+intersect.room2);
        }
      });
