    async getStairShaftOrElevator(spaceID1, spaceName1, spaceName2){
      //let spaceMesh = store.state.bv.scene.getObjectByName(spaceName1);
      let returnvalue = "Shaft";
      let stairW = 0, shaftW = 0, elevatorW = 0;
      let spaceHAG = 0;

      const modelID = ifcModel.modelID;
      const polyString = await this.getPolylineForSpace(modelID, spaceID1, "", 1);
      const calculatedArea = bmu.trinagulateAndCalculateArea(polyString);
      if(calculatedArea>8) stairW = stairW+2;
