    async showDoorMarkers2(){ 
      // Finding storey height above ground to make filter
      let selectdStoreyHeightAboveGround = 0;
      const polyLines = [];
      const modelID = ifcModel.modelID;
      const storeys = await ifcLoader.ifcManager.getAllItemsOfType(modelID, IFCBUILDINGSTOREY);
      for(let s=0; s<this.floorlist.length; s++){
        if(this.floorsselected==this.floorlist[s]||this.floorsselected.length==0){
          const props = await ifcLoader.ifcManager.getItemProperties(modelID, storeys[s]);
          selectdStoreyHeightAboveGround = props.Elevation.value/1000;
        }
      }
      // List all intersection points from 
      const doorURIlist = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.NEO("x_door"));
      for(let i=0; i<doorURIlist.length; i++){
        const doorPosition = this.getDoorPosition(doorURIlist[i].subject.value);
        if(approximatelyEqual(selectdStoreyHeightAboveGround, doorPosition.y, 0.5)){
          let doorExpressID = "";
          if(store.state.rs.rdfStore.match($rdf.sym(doorURIlist[i].subject.value), ns.IFOUR("expressid"), null)[0]) doorExpressID = store.state.rs.rdfStore.match($rdf.sym(doorURIlist[i].subject.value), ns.IFOUR("expressid"), null)[0].object.value;
          bmu.createPlaceMarker(doorPosition, 0xFF0000, false, 2, "placemarker"+doorExpressID, 0.16);
        }
      }
      const interfacePointList = store.state.rs.rdfStore.match(null, ns.RDF("type"), ns.IFOUR("InterfacePoint"));
      for(let i=0; i<interfacePointList.length; i++){
        const interfacePosition = this.getDoorPosition(interfacePointList[i].subject.value);
        if(approximatelyEqual(selectdStoreyHeightAboveGround, interfacePosition.y, 0.5)){
          store.state.rs.rdfStore.match(null, ns.IFOUR("hasInterfacePoint"), $rdf.sym(interfacePointList[i].subject.value)).forEach(space => {
            const roomName = this.getRoomNamebyURI(space.subject.value);
            if(this.lockedDoors.includes(roomName)){
              addToStores($rdf.sym(space.subject.value), ns.IFOUR("islocked"), $rdf.literal("yes"));
              bmu.createPlaceMarker(interfacePosition, 0xFFFF00, false, 3, "placemarker", 0.3);
            } else {
              bmu.createPlaceMarker(interfacePosition, 0x000099, false, 2);
            }
          });
        }
      }

      // Populate space intersections
      if(this.spaceIntersections.length<4){
        store.state.rs.rdfStore.match(null, ns.IFOUR("m_polyline"), null).forEach(poly => {
          const splitted = poly.object.value.split(";");
          const storeyHeight = parseFloat(splitted[4]);
          const spaceHeight = parseFloat(splitted[5]);
          if(bmu.checkIfMeshCrossStoreyLevel(selectdStoreyHeightAboveGround, storeyHeight, storeyHeight+spaceHeight)){
            polyLines.push(poly.object.value);
          }
        });

        const foundIntersections = bmu.findSpaceIntersections(polyLines, 0.5, true);
        foundIntersections.forEach(intersect => {
          this.spaceIntersections.push(intersect);
        });
      }

      store.state.bv.needsrender = true;
    },
