    async getCirclulationFromCSV(){
      let csvFile = await ifcu.getCirculationCSV(this.$route.query.location);
      var allTextLines = csvFile.split(/\r\n|\n/);
      var headers = allTextLines[0].split(';');
      var lines = [];

      for(var i=1; i<allTextLines.length; i++) {
        if(allTextLines[i]=="<html>") break;
          var data = allTextLines[i].split(';');
          if (data.length == headers.length) {
              var tarr = [];
              for (var j=0; j<headers.length; j++) {
                  tarr.push(data[j]);
              }
              lines.push(tarr);
          }
      }
      console.log(lines);
      for (var i=1; i<lines.length; i++) {
        const room1 = lines[i][0];
        const room2 = lines[i][1];
        const room1uri = this.getRoomURIbyName(room1);
        const room2uri = this.getRoomURIbyName(room2);
        const interfaceType = lines[i][2]+" ";
        let doorBlank = u.getBlankNode(interfaceType+room1+"-"+room2)
        // Add triples related to interface_point
        addToStores(doorBlank, ns.RDF("type"), ns.NEO("m_interface-point"));
        //addToStores(doorBlank, ns.TAG("tag"), doorTAG);
        addToStores(doorBlank, ns.RDFS("label"), $rdf.literal(interfaceType+room1+"-"+room2, 'en'));
        addToStores(doorBlank, ns.RDFS("label"), $rdf.literal(interfaceType+room1+"-"+room2, 'no'));
        if(room1uri) addToStores($rdf.sym(room1uri), ns.NEO("m_port"), $rdf.sym(doorBlank));
        if(room2uri) addToStores($rdf.sym(room2uri), ns.NEO("m_port"), $rdf.sym(doorBlank));
      }
    },
    async getRoomZonesOnEachSideOfDoor(modelID, doorID=0, location, debugF=false){
      const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      let unnamedRoom = false;
      let doorCenterDistance = 0.8;
      if(doorID){
        const props = await ifcLoader.ifcManager.getItemProperties(modelID, doorID);
        const doorWidth = props.OverallWidth.value;
        doorCenterDistance = doorWidth/1600;
      }
      //const doorCenterDistance = 2.7;
      let foundRooms = [];
      const loc = location.clone()
      loc.y = loc.y+1;
      let retryCount = 0;
      do{
        // Do adjustments on second try
        if(retryCount==1){
          console.log("Second attempt to find rooms..."+doorCenterDistance)
          if(foundRooms.length>2) foundRooms = [];
          if(foundRooms.length<2){
            doorCenterDistance=0.5;
          } else {
            doorCenterDistance=doorCenterDistance/2;
          }
          //debugF = true;
        }

        const firstOffsetPoint = new Vector3(doorCenterDistance, 0, 0); // Can change the number value to change radius of circle
        const firstPointOnCircle = loc.clone().add(firstOffsetPoint);
        const direction = v.v3subtract(loc, firstPointOnCircle); // Creating vector pointing to center of door
        if(debugF) bmu.createVisibleVector(firstPointOnCircle, direction, 0xFF0000);
        if(debugF) bmu.createPlaceMarker(firstPointOnCircle.clone(), 0x00FF00);

        // Finding the intersects for the first point to circle
        const intersects = bmu.raycastUsingVectorDir(firstPointOnCircle, direction);
        if(debugF) bmu.createVisibleVector(firstPointOnCircle, direction, 0xFF0000);
        const intersectsCombined = [];
        intersects.forEach(inters => {
          intersectsCombined.push(inters);
          if(inters.distance <= (doorCenterDistance)) {
            const roomname = inters.object.name;
            if(debugF) bmu.createPlaceMarker(inters.point, 0x0066FF); // For verifying where ray hits
            if(debugF) console.log("Room name: "+roomname+" Distance: "+inters.distance);
            if(!foundRooms.includes(roomname) && roomname!="" &&  !roomname.includes("placemarker")){
              //if(foundRooms.length==2) return;
              foundRooms.push(roomname);
            } 
            if(roomname=="") unnamedRoom = true;
          }
        });
      
        // Do room checks for each 45 degrees while rotating around door
        const axis = new Vector3(0, 1, 0);
        const angle = Math.PI/3;
        for(let i=0; i<=4; i++){
          const currentCheckPoint = loc.clone().add(firstOffsetPoint.applyAxisAngle(axis, angle));
          if(debugF) bmu.createPlaceMarker(currentCheckPoint, 0x0000FF);
          const direction = v.v3subtract(loc, currentCheckPoint).normalize(); // Creating vector pointing to center of door
          if(debugF) bmu.createVisibleVector(currentCheckPoint, direction, 0xFF9900);
          const intersects2 = bmu.raycastUsingVectorDir(currentCheckPoint, direction);
          if(debugF) console.log("Intersects")
          if(debugF) console.log(intersects2)
          intersects2.forEach(inters => {
            intersectsCombined.push(inters);
            if(inters.distance <= (doorCenterDistance)) {
              const roomname = inters.object.name;
              //if(debugF) bmu.createPlaceMarker(inters.point, 0x0066FF); // For verifying where ray hits
              //if(debugF) console.log("Room name: "+roomname+" Distance: "+inters.distance);
              if(!foundRooms.includes(roomname) && roomname!="" && !roomname.includes("placemarker")){
                //if(foundRooms.length==2) return;
                foundRooms.push(roomname);
              }
              if(roomname=="" || !roomname) unnamedRoom = true;
            }
          });
        }

        // Try a second time if too many neihgbour rooms was found
        debugF = false;
        retryCount = retryCount+2;
        if(foundRooms.length!=2){
          retryCount = retryCount-1;
        }
      } while (retryCount < 2) // Trying to run the function with smaller check radius
      // Debugging +++++
      //bmu.panToFocusObject(location.x, location.z);
      //alert("Moved to point.");
      // Debugging -----
      if(debugF) console.log("Number of rooms: "+foundRooms.length);
      if(foundRooms.length>2){
        foundRooms.forEach(roomn => {
          console.log(roomn);
        });
      }
      if(foundRooms.length!=2){
        bmu.createPlaceMarker(loc, 0xFF2200, false, 1, "placemarker");
        let issueText = "Issue:"+foundRooms.length;
        if(foundRooms.length>=3) issueText = foundRooms[0]+":"+foundRooms[1];
        if(unnamedRoom) issueText = issueText.replace("Issue", "Unnamed");
        bmu.createSimpleLabel(loc.applyMatrix4(buildingbox.matrixWorld), issueText);
        let issueAlreadyFound = false;
        this.doorIssueTableList.forEach(issue => {
          if(issue.doorid==doorID) issueAlreadyFound = true;
        });
        if(!issueAlreadyFound) this.doorIssueTableList.push(JSON.parse('{"doorid":"'+doorID+'","room1":"'+encodeURI(foundRooms[0])+'","room2":"'+encodeURI(foundRooms[1])+'"}'));
      }
      return foundRooms;
    },
