    async getRoomZones(modelID, buildingbox){
      const spaceIDs = await ifcLoader.ifcManager.getAllItemsOfType(modelID, IFCSPACE);
      const spaceMeshes = ifcu.getAllSubsetMeshes(spaceIDs, modelID, ifcLoader);
      let roomSpaceArray = [];
      for(let i=0; i<spaceIDs.length; i++){
        //console.log(spaceMeshes[i]);
        roomSpaceArray.push({expressID: spaceIDs[i], elementID: spaceMeshes[i].id });
      }

      const material = new MeshLambertMaterial({
        transparent: true,
        opacity: 0.5,
        color: 0xaaaaff,
        depthTest: true
      });

      for(let i=0; i<spaceMeshes.length; i++){
        const props = await ifcLoader.ifcManager.getItemProperties(modelID, spaceIDs[i]);
        const roomMesh = ifcu.getAllSubsetMeshes([spaceIDs[i]], modelID, ifcLoader)[0];
        roomMesh.material = material.clone();
        roomMesh.material.side = DoubleSide; // Must have doubleside for making the raycaster work - Espen.
        if(props.Name){
          roomMesh.name = u.formatString(props.Name.value);
          if(roomMesh.name=="") roomMesh.name = "room"+spaceIDs[i];
        } else {
          roomMesh.name = "room"+spaceIDs[i];
        }
        roomMesh.updateMatrixWorld();
        buildingbox.add(roomMesh);
        this.getSpacePolyline(spaceIDs[i]);
      }

      // Show only selected floors
      if(this.floorsselected.length>0){
        for(let i=0; i<this.floorlist.length; i++){
          if(this.floorsselected==this.floorlist[i]){
            console.log("Floor height: "+this.floorheights[i])
            this.showRoomsForStorey(this.floorheights[i]);
          }
        }
      }

      this.RENDER();
    },
    getMeshHeight(mesh){
      let minZ = 100000, maxZ = -100000;
      for(let y = 1; y<=mesh.geometry.attributes.position.array.length; y=y+3){
        maxZ = Math.max(mesh.geometry.attributes.position.array[y], maxZ);
        minZ = Math.min(mesh.geometry.attributes.position.array[y], minZ);
      }
      return maxZ-minZ;
    },
