    async getIFCOpeningElements(showOnlyMarkers=false, debugF=false){
      const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
      const container = document.createElement('div');
      IFCmetadata = await ifcu.getIFCmetaDataFromJson(this.$route.query.location+"home/IFC/building.json");
      
