      let IFCuri = this.$route.query.location+"home/IFC/"+this.ifcfile
      //console.log("IFC uri: "+IFCuri);
      const res = await window.solid.session.fetch(
        IFCuri, {
        method: "GET",
        credentials: "include",
      });
      console.log(res);
      let filedata = await res.arrayBuffer();
      let fileurl = URL.createObjectURL(new Blob([filedata], { type: "application/x-step"}));
      //const ifcModel = await ifcLoader.loadAsync(fileurl);
      if(!this.ifcmodelLoaded){
        ifcModel = await ifcLoader.loadAsync(fileurl);
        this.ifcmodelLoaded = true;
      }
      const modelID = ifcModel.modelID;
      /*const combinedLocation = new Vector3(-27.9825826007, 7.4, -62.14883771467999);
      const foundRooms = await this.getRoomZonesOnEachSideOfDoor(modelID, undefined, combinedLocation, true);
      console.log(foundRooms);*/

