      const refillsElementIDs = await ifcLoader.ifcManager.getAllItemsOfType(modelID, IFCRELFILLSELEMENT);
      const relvoidsElementIDs = await ifcLoader.ifcManager.getAllItemsOfType(modelID, IFCRELVOIDSELEMENT);
      // Refills are relating door to opening to insert door into
      const refillIDs = [];
      for(let r=0; r<refillsElementIDs.length; r++){
        const refillProperties = await ifcLoader.ifcManager.getItemProperties(modelID, refillsElementIDs[r]);
        refillIDs.push(refillProperties.RelatingOpeningElement.value)
      }
      // Relvoids are relating wall to opening in wall
      const relvoids = [];
      for(let r=0; r<relvoidsElementIDs.length; r++){
        const relvoidProperties = await ifcLoader.ifcManager.getItemProperties(modelID, relvoidsElementIDs[r]);
        if(relvoidProperties) relvoids.push({opening: relvoidProperties.RelatedOpeningElement.value, buildingelement: relvoidProperties.RelatingBuildingElement.value});
      }
      const foundIntersections = [];
      let counter = 0;
      for(let o=0; o<openingIDs.length; o++){
          // Need to get position of wall first, because position of opening is added to that position
          let wallid = 0;
          for(let v=0; v<relvoids.length; v++){
            if(relvoids[v].opening==openingIDs[o]){
              wallid = relvoids[v].buildingelement;
              //console.log("Found wall: "+wallid);
            } 
          }
          const dimensions = await this.getIFCElementDimentions(openingIDs[o], modelID, false);
          let foundRefill = false;
          if(refillIDs.includes(openingIDs[o])) foundRefill = true; // Check if opening is filled with window or door
          //console.log(dimensions);
          if(dimensions.x>=600&&dimensions.y>=600&&!foundRefill){
            const wallLocation = await this.getIFCElementPosition(wallid, modelID);
            if(debugF) console.log("wall location")
            if(debugF) console.log(wallLocation)
            let myAxis = wallLocation.rot;
            if(debugF) console.log("Rot: "+wallLocation.rot.x+"-"+wallLocation.rot.y+"-"+wallLocation.rot.z);
            const location = await this.getIFCElementLocationTranslated(openingIDs[o], modelID, myAxis);
            if(debugF) console.log("location")
            if(debugF) console.log(location)
            const combinedLocation = new Vector3(wallLocation.x+location.x, wallLocation.z+location.z, (-wallLocation.y+location.y));
            const relatedSpaces = await this.getSpacesRelatedToWall(wallid, modelID);
            let relatedStoreys = await this.getStoreysRelatedToSpace(relatedSpaces[0], modelID);
            let storeyProperties = await ifcLoader.ifcManager.getItemProperties(modelID, relatedStoreys[0]);
            if(!storeyProperties){
              console.log("Number of related spaces: "+relatedSpaces.length+" Trying next..");
              relatedStoreys = await this.getStoreysRelatedToSpace(relatedSpaces[1], modelID);
              storeyProperties = await ifcLoader.ifcManager.getItemProperties(modelID, relatedStoreys[0]);
            }
            if(storeyProperties){
              const storeyHeight = storeyProperties.Elevation.value/1000;
              combinedLocation.y = combinedLocation.y+storeyHeight;
              if(debugF) console.log("Storey height: "+storeyHeight);
            } else {
              console.log("Warning, storey height was not found.");
            }
            if(debugF) console.log(combinedLocation);
            bmu.createPlaceMarker(combinedLocation, 0x000000, false, 1, "placemarker", 0.2);
            const foundRooms = await this.getRoomZonesOnEachSideOfDoor(modelID, undefined, combinedLocation);
            console.log(foundRooms)
            if(!foundRooms[0]) continue;
            foundIntersections.push({"room1": u.formatString(foundRooms[0]), "room2": u.formatString(foundRooms[1]), "point": combinedLocation, "length": 1});
            if(debugF) console.log("Found room1: "+foundRooms[0]+" and: "+foundRooms[1])
            if(debugF) console.log("Open opening id: "+openingIDs[o]);
            counter++;
          }
      }
      this.storeSpaceIntersections(foundIntersections, false);
      console.log("Count of found elements: "+counter);
    },
