    showWay(wayArray){
      if(wayArray.length<=2) return;
      let directionVectors = [];
      let prevURI = undefined;
      let prevDoorLocation = new Vector3(0.0,0.0,0.0);
      wayArray.forEach(wayItem => {
        let isDoor = false;
        if(store.state.rs.rdfStore.match($rdf.sym(wayItem), ns.RDF("type"), ns.NEO("x_door"))[0]){
          prevDoorLocation = this.getDoorPosition(wayItem);
          isDoor = true;
        }
        if(prevURI!=undefined&&!isDoor){
          if(store.state.rs.rdfStore.match($rdf.sym(prevURI), ns.RDF("type"), ns.NEO("x_door"))[0]){
            // Previous URI was a door
            const polyline = this.getRoomPolylinebyURI(wayItem)
            const doorDirectionVector = bmu.getDoorDirectionVector(polyline, prevDoorLocation, false);
            directionVectors.push({"uri": wayItem, "pos": prevDoorLocation, "vec": doorDirectionVector});
          } else {
            // Previous uri was space -- Then join
            const polylines = [];
            let intersectPoint = new Vector3(0,0,0);
            polylines.push(this.getRoomPolylinebyURI(prevURI));
            polylines.push(this.getRoomPolylinebyURI(wayItem));
            let intersections = bmu.findSpaceIntersections(polylines, 0.5, false);
            if(intersections[0]) intersectPoint = intersections[0].point;
            const doorDirectionVector = bmu.getDoorDirectionVector(polylines[1], intersectPoint, false);
            directionVectors.push({"uri": "", "pos": intersectPoint, "vec": doorDirectionVector});
          }
        }
        prevURI = wayItem;
      });
      // Create function for creating vectors in between all directionvectors.
      let prevVector = undefined;
      directionVectors = directionVectors.reduce(function(newArray, item) { 
          // Create vectors in front of door:
          let beforePos = item.pos.clone().add(item.vec.clone().negate());
          newArray.push({"uri": "", "pos": beforePos.clone(), "vec": item.vec})
          newArray.push(item);
          // Create vectors behind door:
          if(prevVector!=undefined){
            let afterDir = new Vector3(0.0,0.0,0.0).subVectors(beforePos.clone(), prevVector.pos.clone().add(prevVector.vec.clone())).normalize(); // Must take in the previous offset for the after pos
            console.log("After dir:")
            console.log(afterDir);
            let lengthToNextDoor = item.pos.clone().distanceTo(prevVector.pos);
            let afterPos = prevVector.pos.clone().add(prevVector.vec.clone());
            newArray.push({"uri": "", "pos": afterPos, "vec": afterDir});
            for(let n=3.0; n<Math.abs(lengthToNextDoor-3.0); n=n+3.0){
              // Set new marker for each 3 meters in between
              let betwPos = afterPos.clone().addScaledVector(afterDir.clone(), n);
              newArray.push({"uri": "", "pos": betwPos, "vec": afterDir});
            }
          }
          prevVector = item;  
          return newArray;
      }, []);
      store.state.bv.arrowModelStart = false;
      directionVectors.forEach((vec) => {
        bmu.createVisibleVector(vec.pos, vec.vec, 0x00FFFF);
