      setBuildingPoint1(){
        const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
        this.buildingPosition.buildingPosX = store.state.bv.intersectXY.x-buildingbox.position.x+((this.buildingPosition.buildingdimensions.v2.x/2.0)*store.state.bv.scaleFactor);
        this.buildingPosition.buildingPosY = store.state.bv.intersectXY.y+50-buildingbox.position.z-((this.buildingPosition.buildingdimensions.v3.y/2.0)*store.state.bv.scaleFactor);
      },
      fineTuneBuildingRotation(){
        this.rotationStep = 720;
      },
      setBuildingLatLong1(){
        const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
        console.log(store.state.bv.buildingboxPosition)
        console.log(store.state.bv.intersectXY)
        const latlong = UnitsUtils.sphericalToDatums(store.state.bv.buildingboxPosition.x+store.state.bv.intersectXY.x, store.state.bv.buildingboxPosition.z-store.state.bv.intersectXY.y);
        this.buildingPosition.buildinglat = latlong.latitude;
        this.buildingPosition.buildinglong = latlong.longitude;

        // Move building box into place
        console.log("Removing building box..");
        console.log(buildingbox);
        let movedirX = store.state.bv.intersectXY.x-buildingbox.position.x+((this.buildingPosition.buildingdimensions.v2.x/2.0)*store.state.bv.scaleFactor);
        let movedirY = store.state.bv.intersectXY.y+50-buildingbox.position.z-((this.buildingPosition.buildingdimensions.v3.y/2.0)*store.state.bv.scaleFactor);

        //buildingbox.position.x = store.state.bv.intersectXY.x+((this.buildingPosition.buildingdimensions.v2.x/2)*store.state.bv.scaleFactor);
        //buildingbox.position.z = store.state.bv.intersectXY.y+50-((this.buildingPosition.buildingdimensions.v3.y/2)*store.state.bv.scaleFactor); // M legge til 50 pga strrelse p standard bounding box
        buildingbox.translateX(movedirX);
        buildingbox.translateZ(movedirY);
        buildingbox.translateX(-this.buildingPosition.buildingPosX);
        buildingbox.translateZ(-this.buildingPosition.buildingPosY);

        // Move according to BuildingPoint1
        //buildingbox.geometry.translate(new Matrix4().makeTranslation(buildingbox.position.x-this.buildingPosition.buildingPosX, 0 , buildingbox.position.z+this.buildingPosition.buildingPosY));
      

        buildingbox.updateMatrix();
        buildingbox.matrixAutoUpdate = false;
        buildingbox.castShadow = false;
        buildingbox.renderOrder=1;
        this.RENDER();
      },
      rotateBuildingbox(direction){
        const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
        let rotateAngle = Math.PI/this.rotationStep*direction;
        buildingbox.translateX(-((this.buildingPosition.buildingdimensions.v2.x/2.0)*store.state.bv.scaleFactor)+this.buildingPosition.buildingPosX);
        buildingbox.translateZ(((this.buildingPosition.buildingdimensions.v3.y/2.0)*store.state.bv.scaleFactor)+this.buildingPosition.buildingPosY);
        buildingbox.rotateOnAxis( new Vector3(0,1,0), -rotateAngle);
        buildingbox.translateX(((this.buildingPosition.buildingdimensions.v2.x/2.0)*store.state.bv.scaleFactor)-this.buildingPosition.buildingPosX);
        buildingbox.translateZ(-(((this.buildingPosition.buildingdimensions.v3.y/2.0)*store.state.bv.scaleFactor))-this.buildingPosition.buildingPosY);
        buildingbox.updateMatrix();
        buildingbox.matrixAutoUpdate = false;
        buildingbox.castShadow = false;
        buildingbox.renderOrder=1;
        this.RENDER();
        console.log(buildingbox);
        this.buildingPosition.rotation.x = buildingbox.rotation.x;
        this.buildingPosition.rotation.y = buildingbox.rotation.y;
        this.buildingPosition.rotation.z = buildingbox.rotation.z;
      },
      moveBuildingBox(mx, my, mz){
        const buildingbox = store.state.bv.scene.getObjectByName("buildingbox");
        buildingbox.translateX(mx);
        buildingbox.translateX(my);
        buildingbox.translateZ(mz);
        buildingbox.updateMatrix();
        buildingbox.matrixAutoUpdate = false;
        buildingbox.castShadow = false;
        buildingbox.renderOrder=1;
        this.RENDER();
        console.log(buildingbox);
        this.buildingPosition.buildingdimensions.v1.x = buildingbox.position.x;
        this.buildingPosition.buildingdimensions.v1.y = buildingbox.position.y;
        this.buildingPosition.buildingdimensions.v1.z = buildingbox.position.z;
      },
